import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { baseURL } from '../shared/baseurl';
import { Etudiant } from '../shared/etudiant';

@Injectable({
  providedIn: 'root'
})
export class EtudiantService {

  constructor(private httpClient: HttpClient) { }

  getAllEtudiant(): Observable<Etudiant[]> {
    return this.httpClient.get<Etudiant[]>( baseURL + 'etudiants' );
  }

  createEtudiant(etudiant: Etudiant): Observable<Etudiant> {
    return this.httpClient.post<Etudiant>(baseURL + 'etudiants', etudiant);
  }

  getEtudiantByCompteId(compteId?: string): Observable<Etudiant> {
    return this.httpClient.get<Etudiant>(baseURL + 'etudiants/compte/' + compteId);
  }

  getEtudiantByClasseId(classeId?: string): Observable<Etudiant[]> {
    return this.httpClient.get<Etudiant[]>(baseURL + 'etudiants/classes/' + classeId);
  }

  getEtudiantById(idEtudiant?: string): Observable<Etudiant> {
    return this.httpClient.get<Etudiant>(baseURL + 'etudiants/' + idEtudiant);
  }
  updateEtudiant(idEtudiant?: string, etudiant?: Etudiant): Observable<Etudiant> {
    return this.httpClient.put<Etudiant>(baseURL + 'etudiants/' + idEtudiant, etudiant);
  }
  updateEtudiant1(idEtudiant: string, file: File, etudiant: Etudiant): Observable<Etudiant> {
    const formData = new FormData();
    formData.append('avatar', file);
    return this.httpClient.put<Etudiant>(baseURL + 'etudiants/' + idEtudiant, etudiant);
  }

  //Les methodes de cration ici
  public createfiles(file: File, compteId?: string, etudiantId?: string): Observable<string> {
    let formData = new FormData();
    formData.append('avatar', file);
    console.log(formData);
    return this.httpClient.put<string>(baseURL + "etudiants/" + compteId + '/' + etudiantId, formData, {});
  }

  //Les methodes de cration ici
  public createfiles1(etudiant: Etudiant, compteId?: string, etudiantId?: string): Observable<Etudiant> {
    return this.httpClient.post<Etudiant>(baseURL + "etudiants/" + compteId + '/' + etudiantId, etudiant, {});
  }

  getBase64(file: File): Observable<string> {
    return new Observable<string>(sub => {
      const reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onload = () => {
        sub.next(reader.result?.toString());
        sub.complete();
      };
      reader.onerror = error => {
        sub.error(error);
      };
    })
  }
}
