import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { baseURL } from '../shared/baseurl';
import { Enseignant } from '../shared/enseignant';

@Injectable({
  providedIn: 'root'
})
export class EnseignantService {
  constructor(private httpClient: HttpClient) { }


  createEnseignant(enseignant: Enseignant): Observable<Enseignant> {
    return this.httpClient.post<Enseignant>(baseURL + 'enseignants', enseignant);
  }

  getEnseignantByCompteId(compteId?: string): Observable<Enseignant> {
    return this.httpClient.get<Enseignant>(baseURL + 'enseignants/compte/' + compteId);
  }

  getEnseignantById(idEnseignant?: string): Observable<Enseignant> {
    return this.httpClient.get<Enseignant>(baseURL + 'enseignants/' + idEnseignant);
  }

  //Les methodes de cration ici
  public updateEnseignant(enseignantId?: string, enseignant?: Enseignant): Observable<Enseignant> {
    return this.httpClient.put<Enseignant>(baseURL + "enseignants/" + enseignantId, enseignant, {});
  }

  //Les methodes de cration ici
  public createfiles1(enseignant: Enseignant, compteId?: string, enseignantId?: string): Observable<Enseignant> {
    return this.httpClient.post<Enseignant>(baseURL + "enseignants/" + compteId + '/' + enseignantId, enseignant, {});
  }


  //Les methodes de cration ici
  public createfiles(file: File, compteId?: string, enseignantId?: string): Observable<string> {
    let formData = new FormData();
    formData.append('avatar', file);
    console.log(formData);
    return this.httpClient.put<string>(baseURL + "enseignants/" + compteId + '/' + enseignantId, formData, {});
  }

  getBase64(file: File): Observable<string> {
    return new Observable<string>(sub => {
      const reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onload = () => {
        sub.next(reader.result?.toString());
        sub.complete();
      };
      reader.onerror = error => {
        sub.error(error);
      };
    })
  }
}
