import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { baseURL } from '../shared/baseurl';
import { CadreAdministratif } from '../shared/cadreAdministratif';

@Injectable({
  providedIn: 'root'
})
export class CadreAdministratifService {
  constructor(private httpClient: HttpClient) { }

  getAllCadreAdministratifs(): Observable<CadreAdministratif[]> {
    return this.httpClient.get<CadreAdministratif[]>(baseURL + 'cadres');
  }
  getCadreAdministratifById(idCadreAdministratif: string): Observable<CadreAdministratif> {
    return this.httpClient.get<CadreAdministratif>(baseURL + 'cadres/' + idCadreAdministratif);
  }

  createCadreAdministratif(cadreAdministratif: CadreAdministratif): Observable<CadreAdministratif> {
    return this.httpClient.post<CadreAdministratif>(baseURL + 'cadres', cadreAdministratif);
  }

  updateCadreAdministratif(idCadreAdministratif: string, cadreAdministratif: CadreAdministratif): Observable<CadreAdministratif> {
    return this.httpClient.put<CadreAdministratif>(baseURL + 'cadres/' + idCadreAdministratif, cadreAdministratif)
  }
  getCadreAdministratifByUniversiteId(idUniversite: string): Observable<CadreAdministratif[]> {
    return this.httpClient.get<CadreAdministratif[]>(baseURL + 'cadres/universites/' + idUniversite);
  }

  deleteCadreAdministratif(idCadreAdministratif: string): Observable<CadreAdministratif> {
    return this.httpClient.delete<CadreAdministratif>(baseURL + 'cadres/' + idCadreAdministratif);
  }
}

