import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { Route, Router } from '@angular/router';
import { LoginComponent } from 'src/app/login/login.component';
import { AuthService } from 'src/app/services/auth.service';
import { RegisterComponent } from '../../etudiant/register/register.component';
import { baseURL } from '../../shared/baseurl';
import { PLANSITE } from "./../../shared/plansite";

@Component({
  selector: 'app-header1',
  templateUrl: './header1.component.html',
  styleUrls: ['./header1.component.scss']
})
export class Header1Component implements OnInit {

  baseURL: string = baseURL;
  plansite: any;
  langage: string = 'Franais';
  flag_un: string = '';
  username?: String;
  avatar?: string;
  langages: any = [
    {
      lang: 'Franais',
      icon: "flag-icon flag-icon-fr mr-1"
    },
    {
      lang: 'Anglais',
      icon: "flag-icon flag-icon-us mr-1"
    }
  ];
  constructor(
    public dialog: MatDialog,
    private authService: AuthService,
    private route: Router
  ) { }

  toToUniversite(param: string) {
    this.route.navigateByUrl('list-universities/' + param);
  }

  onLogout() {
    this.authService.logout();
    location.reload();
  }
  openDialog() {
    const dialogRef = this.dialog.open(LoginComponent);

    dialogRef.afterClosed().subscribe(result => {});
  }

  openDialogRegister() {
    const dialogRef = this.dialog.open(RegisterComponent, {
      width: '100%',
      autoFocus: true,
      maxHeight: '100vh',
      position: {
        top: '0px'
      }
    });

    dialogRef.afterClosed().subscribe(result => {});
  }

  ngOnInit(): void {
    this.avatar = localStorage.getItem('avatar')?.toString();
    this.username = localStorage.getItem('username')?.toString();
    
    if (localStorage.getItem('langue') === 'Franais') {
      this.plansite = PLANSITE.fr;
      this.langage = this.langages[0].lang;
      this.flag_un = this.langages[0].icon;
      localStorage.setItem('flag_un', this.flag_un)
    } else if (localStorage.getItem('langue') === 'Anglais') {
      this.plansite = PLANSITE.en;
      this.langage = this.langages[1].lang;
      this.flag_un = this.langages[1].icon;
      localStorage.setItem('flag_un', this.flag_un)
    } else {
      localStorage.setItem('langue', 'Franais');
      this.langage = this.plansite.fr.fr;
      this.langage = this.langages[0].lang;
      this.flag_un = this.langages[0].icon;
      localStorage.setItem('flag_un', this.flag_un)
    }
  }

  changerLangue(langue: string): void {
    localStorage.setItem('langue', langue);
    this.ngOnInit();
    this.reloadPage()
  }

  reloadPage() {
    setTimeout(() => {
      window.location.reload();
    }, 100);
  }
}
