import { Injectable } from '@angular/core';
import {
  HttpRequest,
  HttpHandler,
  HttpEvent,
  HttpInterceptor
} from '@angular/common/http';
import { Observable } from 'rxjs';
import jwt_decode from 'jwt-decode';
import { VariablesGlobales } from 'src/variablesGlobales';

@Injectable()
export class JwtTokenInterceptorInterceptor implements HttpInterceptor {
  constructor() { }
  intercept(request: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
    const API_KEY = '123456';
    const headers = {
      Authorization: `Bearer ${localStorage.getItem('token')?.toString()}`,
      'API_KEY': API_KEY
    };
    request = request.clone({
      setHeaders: headers
    });
    console.log(this.getDecodedAccessToken(headers.Authorization.substr('Bearer '.length)));
    return next.handle(request);
  }

  getDecodedAccessToken(token: string): any {
    try {
      return jwt_decode(token);
    }
    catch (Error) {
      return null;
    }
  }

}
