import { Component, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { RechercheService } from '../../services/recherche.service';
import { UniversiteService } from '../../services/universite.service';
import { Recherche } from '../../shared/recherche';
import { Universite } from '../../shared/universite';

@Component({
  selector: 'app-create-recherche',
  templateUrl: './create-recherche.component.html',
  styleUrls: ['./create-recherche.component.scss']
})
export class CreateRechercheComponent implements OnInit {

  @ViewChild('fform', { static: false }) rechercheFormDirective: any;
  rechercheForm: FormGroup;

  selectedFile: File;

  recherche: Recherche;
  loading: boolean = false;
  formErrors: any = {
    titre: '',
    presentation: '',
    apropos: '',
    description: '',
    universite: ''
  };

  validationMessages: any = {
    titre: {
      required: 'le titre  est requis.',
      minlength: 'le titre tre plus que 4 caractres.'
    },
    description: {
      required: 'la description est requise.',
      minlength: 'la description doit tre plus que 6 caractres.'
    },
    presentation: {
      required: 'la presentation est requise.',
      minlength: 'la presentation doit tre plus que 6 caractres.'
    },
    apropos: {
      required: 'le champ apropos est requis.',
      minlength: 'le champ apropos doit tre plus que 6 caractres.'
    },
    universite: {
      required: 'le titre  est requis.'
    },
  };

  compteId?: string;
  msgError?: string;
  universites: Universite[] = [];

  constructor(
    private formBuilder: FormBuilder,
    private router: Router,
    private rechercheService: RechercheService,
    private universiteService: UniversiteService,
    private route: ActivatedRoute
  ) {
    this.createForm();
  }

  ngOnInit(): void {
    this.universiteService
      .getAllUniversities()
      .subscribe(universites => this.universites = universites)
  }

  createForm() {
    this.rechercheForm = this.formBuilder.group({
      titre: ['', [Validators.required, Validators.minLength(4)]],
      apropos: ['', [Validators.required, Validators.minLength(4)]],
      presentation: ['', [Validators.required, Validators.minLength(4)]],
      description: ['', [Validators.required, Validators.minLength(4)]],
      universite: ['', [Validators.required]],
      fichier: '',
      editeur: '',
      collection: '',
      isbn: '',
      datePublication: ''
    });
    this.rechercheForm
      .valueChanges
      .subscribe(data => this.onValueChanged(data));
    this.onValueChanged();
  }

  onValueChanged(data?: any) {
    if (!this.rechercheForm) { return; }
    const form = this.rechercheForm;
    for (const field in this.formErrors) {
      if (this.formErrors.hasOwnProperty(field)) {
        // clear previous error message (if any)
        this.formErrors[field] = '';
        const control = form.get(field);
        if (control && control.dirty && !control.valid) {
          const messages = this.validationMessages[field];
          for (const key in control.errors) {
            if (control.errors.hasOwnProperty(key)) {
              this.formErrors[field] += messages[key] + ' ';
            }
          }
        }
      }
    }
  }

  ngOnChanges() {
    this.onSubmit();
  }

  closeModal() {
    this.router.navigateByUrl('reset-password');
  }

  onSubmit() {
    this.recherche = this.rechercheForm?.value;
    this.recherche.fichier = 'images/recherches/' + this.selectedFile.name;
    this.rechercheService
        .telecharger(this.selectedFile)
        .subscribe( file => {
          this.rechercheService
              .createRecherche(this.recherche)
              .subscribe(
                recherche=> {},
                err => { this.msgError = err.error.message; },
                () => {
                  setTimeout(() => { this.loading = false; }, 2000);
                  location.reload()
                }
              )
        } )
    this.rechercheFormDirective.resetForm();
  }


  fileChanged(event: any) {
    this.selectedFile = event.target.files[0];
  }

}
