import { Component, OnInit } from '@angular/core';
import { Etudiant } from './../../shared/etudiant';
import { EtudiantService } from './../../services/etudiant.service';
import { ActivatedRoute } from '@angular/router';
import { Classe } from './../../shared/classe';
import { ClasseService } from './../../services/classe.service';

@Component({
  selector: 'app-etudiant-accepter-par-classe',
  templateUrl: './etudiant-accepter-par-classe.component.html',
  styleUrls: ['./etudiant-accepter-par-classe.component.scss']
})
export class EtudiantAccepterParClasseComponent implements OnInit {
  loading: boolean = false;
  etudiants: Etudiant[] = [];
  idClasse?: string;
  classe?: Classe;
  errorEtudiants?: any;
  constructor(
    private etudiantService: EtudiantService,
    private classeService: ClasseService,
    private route: ActivatedRoute
  ) { }

  ngOnInit(): void {
    this.loading = true;
    this.idClasse = this.route.snapshot.params['id'];
    this.etudiantService
        .getEtudiantByClasseId( this.idClasse )
        .subscribe( 
          etudiants => this.etudiants = etudiants,
          error => this.errorEtudiants = error,
          () => this.loading = false
         );
    this.classeService
        .getClasseById( this.idClasse )
        .subscribe( 
          classe => this.classe = classe,
          error => this.errorEtudiants = error,
          () => this.loading = false
         );
  }

}
