import { Component, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatDialog } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { CompteService } from '../../services/compte.service';
import { EtudiantService } from '../../services/etudiant.service';
import { Compte } from '../../shared/compte';
import { Etudiant } from '../../shared/etudiant';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.scss']
})
export class RegisterComponent implements OnInit {

  @ViewChild('fform', { static: false }) compteFormDirective: any;
  compteForm: FormGroup;

  compte?: any;

  formErrors: any = {
    username: '',
    password: '',
    confirmation: '',
    prenom: '',
    nom: '',
    email: '',
    telephone: '',
    adresse: '',
    ville: '',
    pays: '',
    specialite: ''
  };

  validationMessages: any = {
    username: {
      required: 'le username  est requis.',
      minlength: 'le username tre plus que 4 caractres.',
      maxlength: 'le nom l\'auteur ne doit pas tre plus que 150 caractres.'
    },
    password: {
      required: 'le mot de passe est requis.',
      minlength: 'le mot de passe tre plus que 4 caractres.',
      maxlength: 'le mot de passe ne doit pas tre plus que 150 caractres.'
    },
    confirmation: {
      required: 'la confirmation du mot de passe est requis.',
      minlength: 'la confirmation du mot de passe doit tre plus que 4 caractres.',
      maxlength: 'la confirmation du mot de passe ne doit pas tre plus que 150 caractres.'
    },
    prenom: {
      required: 'le prnom est requis.',
      minlength: 'le prnom doit tre plus que 4 caractres.',
      maxlength: 'le prnom doit  ne doit pas tre plus que 150 caractres.'
    },
    nom: {
      required: 'le nom est requis.',
      minlength: 'le nom  doit tre plus que 4 caractres.',
      maxlength: 'le nom  doit ne doit pas tre plus que 150 caractres.'
    },
    adresse: {
      required: 'l\'adresse est requise.',
      minlength: 'l\'adresse doit tre plus que 4 caractres.',
      maxlength: 'l\'adresse ne doit pas tre plus que 150 caractres.'
    },
    email: {
      required: 'l\'e-mail est requis.',
      minlength: 'l\'e-mail doit tre plus que 4 caractres.',
      maxlength: 'l\'e-mail ne doit pas tre plus que 150 caractres.'
    },
    ville: {
      required: 'le nom de la ville est requise',
      minlength: 'le nom de la ville doit tre plus que 4 caractres.',
      maxlength: 'le nom de la ville ne doit pas tre plus que 150 caractres.'
    },
    pays: {
      required: 'le nom du pays est requis.',
      minlength: 'le nom du pays doit tre plus que 4 caractres.',
      maxlength: 'le nom du pays ne doit pas tre plus que 150 caractres.'
    },
    telephone: {
      required: 'le numro du tlphone est requis.',
      minlength: 'le numro du tlphone doit tre plus que 4 caractres.',
      maxlength: 'le numro du tlphone ne doit pas tre plus que 150 caractres.'
    },
    specialite: {
      required: 'la spcialit est requise.',
      minlength: 'la spcialit doit tre plus que 4 caractres.',
      maxlength: 'la spcialit  ne doit pas tre plus que 150 caractres.'
    }
  };




  constructor(
    private formBuilder: FormBuilder,
    private router: Router,
    private compteService: CompteService,
    private etudiantService: EtudiantService,
    private matDialog: MatDialog
  ) {
    this.createForm();
  }

  ngOnInit(): void {
  }

  createForm() {
    this.compteForm = this.formBuilder.group({
      username: ['', [Validators.required, Validators.minLength(4), Validators.maxLength(150)]],
      password: ['', [Validators.required, Validators.minLength(4), Validators.maxLength(150)]],
      confirmation: ['', [Validators.required, Validators.minLength(4), Validators.maxLength(150)]],
      prenom: ['', [Validators.required, Validators.minLength(4), Validators.maxLength(150)]],
      nom: ['', [Validators.required, Validators.minLength(4), Validators.maxLength(150)]],
      email: ['', [Validators.required, Validators.minLength(4), Validators.email, Validators.maxLength(150)]],
      adresse: ['', [Validators.required, Validators.minLength(4), Validators.maxLength(150)]],
      telephone: ['', [Validators.required, Validators.minLength(4), Validators.maxLength(150)]],
      ville: ['', [Validators.required, Validators.minLength(4), Validators.maxLength(150)]],
      pays: ['', [Validators.required, Validators.minLength(4), Validators.maxLength(150)]],
      specialite: ['', [Validators.required, Validators.minLength(4), Validators.maxLength(150)]]
    });
    this.compteForm
      .valueChanges
      .subscribe(data => this.onValueChanged(data));
    this.onValueChanged();
  }

  onValueChanged(data?: any) {
    if (!this.compteForm) { return; }
    const form = this.compteForm;
    for (const field in this.formErrors) {
      if (this.formErrors.hasOwnProperty(field)) {
        // clear previous error message (if any)
        this.formErrors[field] = '';
        const control = form.get(field);
        if (control && control.dirty && !control.valid) {
          const messages = this.validationMessages[field];
          for (const key in control.errors) {
            if (control.errors.hasOwnProperty(key)) {
              this.formErrors[field] += messages[key] + ' ';
            }
          }
        }
      }
    }
  }

  onSubmit() {
    this.compte = this.compteForm?.value;
    this.compteService
      .createCompte({
        username: this.compte.username,
        password: this.compte.password
      } as Compte)
      .subscribe(data => {
        if (data != null) {
          this.etudiantService
            .createEtudiant({
              prenom: this.compte.prenom,
              nom: this.compte.nom,
              email: this.compte.email,
              ville: this.compte.ville,
              pays: this.compte.pays,
              adresse: this.compte.adresse,
              telephone: this.compte.telephone,
              specialite: this.compte.specialite,
              compte: data
            } as Etudiant)
            .subscribe(et => {
              alert(et.nom + ', vous venez de vous inscrire !');
              this.matDialog.closeAll();
              this.router.navigateByUrl('/login');
            })
        }
      })
    this.compteFormDirective.resetForm();
  }

}
