import { Component, Inject, OnInit } from '@angular/core';
import { MatDialog, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { DepartementService } from '../../services/departement.service';
import { Departement } from '../../shared/departement';

@Component({
  selector: 'app-supprimer-departement',
  templateUrl: './supprimer-departement.component.html',
  styleUrls: ['./supprimer-departement.component.scss']
})
export class SupprimerDepartementComponent implements OnInit {
  idDepartement: string = '';
  departement?: Departement;
  loading: boolean = false;
  constructor(
    private departementService: DepartementService,
    @Inject(MAT_DIALOG_DATA) private data: any,
    private matDialog: MatDialog,
    private router: Router
  ) { }

  ngOnInit(): void {
    this.idDepartement = this.data.idDepartement;
    this.departementService
        .getDepartementById(this.idDepartement)
        .subscribe(departement => this.departement = departement)
  }

  supprimerDepartement() {
    this.loading = true;
    this.departementService
      .deleteDepartement(this.idDepartement)
      .subscribe(
        filiere => {},
        error => {},
        () => { 
          this.loading = false, 
          this.matDialog.closeAll(),
          this.router.navigateByUrl('unversites-detail/' + this.data.idUnversite) 
          location.reload() 
        }
      )
  }

  closeModal() {
    this.matDialog.closeAll()
  }

}
