import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { ActivatedRoute, Router } from '@angular/router';
import { Observable } from 'rxjs';
import { AuthService } from 'src/app/services/auth.service';
import { UpdateAvatarComponent } from '../modules/etudiant/update-avatar/update-avatar.component';
import { EnseignantService } from '../modules/services/enseignant.service';
import { EtudiantService } from '../modules/services/etudiant.service';
import { UniversiteService } from '../modules/services/universite.service';
import { baseURL } from '../modules/shared/baseurl';
import { Enseignant } from '../modules/shared/enseignant';
import { Etudiant } from '../modules/shared/etudiant';
import { Universite } from '../modules/shared/universite';
import { flyInOut, expand } from 'animations/app.animation';

@Component({
  selector: 'app-etudiant-profil',
  templateUrl: './etudiant-profil.component.html',
  styleUrls: ['./etudiant-profil.component.scss'],
  host: {
    '[@flyInOut]': 'true',
    'style': 'display: block;'
  },
  animations: [
    flyInOut(),
    expand()
  ]
})
export class EtudiantProfilComponent implements OnInit {
  idUniversite: string = '';
  baseURL: string = baseURL;
  avatar?: string;
  universite: Universite;

  compteId?: string;
  etudiant$?: Observable<Etudiant>;
  enseignant$?: Observable<Enseignant>;
  idEtudiant?: string;
  constructor(
    private etudiantService: EtudiantService,
    private route: ActivatedRoute,
    private router: Router,
    public dialog: MatDialog,
    private enseignantService: EnseignantService,
    private universiteService: UniversiteService,
    private auth: AuthService
  ) {}
  openDialogAvatarUpload(id: string) {
    this.idEtudiant = id;
    const dialogRef = this.dialog.open(UpdateAvatarComponent, {
      width: '100%',
      height: '50%',
      autoFocus: true,
      maxHeight: '60vh',
      position: {
        top: '7%'
      },
      data: { id: id }
    });
    dialogRef.afterClosed().subscribe(result => {});
  }

  gotoUpdateEtudiant(_id: string) {
    this.router.navigate(['/update', { _id: _id }]);
  }
  ngOnChanges() {
    this.avatar = localStorage.getItem('avatar')?.toString();
  }

  ngOnInit(): void {
    this.idUniversite = localStorage.getItem('universite')?.toString() as string;
    this.universiteService
      .getUniversiteById(this.idUniversite)
      .subscribe(universite => this.universite = universite);
    this.avatar = localStorage.getItem('avatar')?.toString();
    this.compteId = localStorage.getItem('_id')?.toString();
    this.etudiant$ = this.etudiantService.getEtudiantByCompteId(this.compteId);
    this.enseignant$ = this.enseignantService.getEnseignantByCompteId(this.compteId);
    this.etudiantService
      .getEtudiantByCompteId(this.compteId)
      .subscribe(data => { if (data != null) { localStorage.setItem('avatar', data.avatar) } })

    this.enseignantService
      .getEnseignantByCompteId(this.compteId)
      .subscribe(data => { if (data != null) { localStorage.setItem('avatar', data.avatar) } })
  }

}
