import { Component, OnInit } from '@angular/core';
import { PLANSITE } from '../../shared/plansite';

@Component({
  selector: 'app-footer1',
  templateUrl: './footer1.component.html',
  styleUrls: ['./footer1.component.scss']
})
export class Footer1Component implements OnInit {
  anneeUniversitaire: string = '';
  langue: string = "";
  plansite: any;
  constructor() {
    this.anneeUniversitaire = new Date().getFullYear() + '-' + Number((new Date().getFullYear()) + 1)
  }

  ngOnInit(): void {
    if (localStorage.getItem("langue") == "Franais") {
      this.plansite = PLANSITE.fr;
    } else if (localStorage.getItem("langue") == "Anglais") {
      this.plansite = PLANSITE.en;
    }
  }
}
