import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { OffreService } from '../modules/services/offre.service';
import { UniversiteService } from '../modules/services/universite.service';
import { baseURL } from '../modules/shared/baseurl';
import { Offre } from '../modules/shared/offre';
import { Universite } from '../modules/shared/universite';
import { flyInOut, expand } from 'animations/app.animation';

@Component({
  selector: 'app-offre',
  templateUrl: './offre.component.html',
  styleUrls: ['./offre.component.scss'],
  host: {
    '[@flyInOut]': 'true',
    'style': 'display: block;'
  },
  animations: [
    flyInOut(),
    expand()
  ]
})
export class OffreComponent implements OnInit {
  idUniversite: string = '';
  universite: Universite;
  offres: Offre[] = [];
  baseURL: string = baseURL;
  constructor(
    private universiteService: UniversiteService,
    private offreService: OffreService,
    private route: ActivatedRoute
  ) { }

  ngOnInit(): void {
    this.idUniversite = this.route.snapshot.params['id'];
    this.universiteService
        .getUniversiteById( this.idUniversite )
        .subscribe( universite => this.universite = universite );

    this.offreService
        .getOffreByUniversiteId( this.idUniversite )
        .subscribe( offres => this.offres = offres )
  }

}
