import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AccueilComponent } from './accueil/accueil.component';
import { ConfirmationComponent } from './confirmation/confirmation.component';
import { CoursComponent } from './cours/cours.component';
import { EtudiantProfilComponent } from './etudiant-profil/etudiant-profil.component';
import { ListUniversiteComponent } from './list-universite/list-universite.component';
import { LoginComponent } from './login/login.component';
import { LogoutComponent } from './logout/logout.component';
import { MatiereDetailComponent } from './matiere-detail/matiere-detail.component';
import { ModifierMotDePasseComponent } from './modifier-mot-de-passe/modifier-mot-de-passe.component';
import { AdminRoutingModule } from './modules/admin/admin-routing.module';
import { EnseignantRoutingModule } from './modules/enseignant/enseignant-routing.module';
import { EtudiantRoutingModule } from './modules/etudiant/etudiant-routing.module';
import { UniversiteDetailComponent } from './universite-detail/universite-detail.component';
import { CadreAdministratifComponent } from './cadre-administratif/cadre-administratif.component';
import { PresentationUniversiteComponent } from './presentation-universite/presentation-universite.component';
import { InscriptionEnLigneComponent } from './inscription-en-ligne/inscription-en-ligne.component';
import { TestComponent } from './test/test.component';
import { InscriptionEtudiantComponent } from './inscription-etudiant/inscription-etudiant.component';
import { CalendrierDetailComponent } from './calendrier-detail/calendrier-detail.component';
import { CalendrierEnLigneComponent } from './calendrier-en-ligne/calendrier-en-ligne.component';
import { OffreComponent } from './offre/offre.component';
import { SujetEtCorrectionComponent } from './sujet-et-correction/sujet-et-correction.component';
import { BibliothqueComponent } from './bibliothque/bibliothque.component';
import { ListCoursComponent } from './list-cours/list-cours.component';
import { ListPublicationComponent } from './list-publication/list-publication.component';
import { ListDistinctionComponent } from './list-distinction/list-distinction.component';
import { ListColloqueComponent } from './list-colloque/list-colloque.component';
import { DetailPublicationComponent } from './detail-publication/detail-publication.component';
import { DetailDistinctionComponent } from './detail-distinction/detail-distinction.component';
import { DetailColloqueComponent } from './detail-colloque/detail-colloque.component';
import { ListVieDeCampusComponent } from './list-vie-de-campus/list-vie-de-campus.component';
import { DetailVieDeCampusComponent } from './detail-vie-de-campus/detail-vie-de-campus.component';
import { ListActualiteComponent } from './list-actualite/list-actualite.component';
import { DetailActualiteComponent } from './detail-actualite/detail-actualite.component';
import { EtudiantAccepterParClasseComponent } from './modules/admin/etudiant-accepter-par-classe/etudiant-accepter-par-classe.component';
import { ListTutorialComponent } from './list-tutorial/list-tutorial.component';
import { GuideTutoralComponent } from './guide-tutoral/guide-tutoral.component';

const routes: Routes = [
  { path: '', component: ListUniversiteComponent },
  { path: 'accueil', component: ListUniversiteComponent },
  {
    path: 'guide-tutorial',
    component: GuideTutoralComponent
  },
  {
    path: 'etudiant-accepter-par-classe/:id',
    component: EtudiantAccepterParClasseComponent
  },
  { path: 'login', component: LoginComponent },
  {
    path: 'list-tutoral/:type',
    component: ListTutorialComponent
  },
  { path: 'universite/:idUniversite/:profile/logout', component: LogoutComponent },
  { path: 'universite/:idUniversite/profile', component: EtudiantProfilComponent },
  { path: 'reset-password', component: ModifierMotDePasseComponent },
  { path: 'confirmation', component: ConfirmationComponent },
  { path: 'matiere-detail/:id', component: MatiereDetailComponent },
  { path: 'universites', component: ListUniversiteComponent },
  { path: 'universite/:id', component: UniversiteDetailComponent },
  { path: 'cours/:id', component: CoursComponent },
  { path: 'universite/:idUniversite/etudiant-profile', component: EtudiantProfilComponent },
  { path: 'cadre-administratif/:id', component: CadreAdministratifComponent },
  { path: 'presentation/:id', component: PresentationUniversiteComponent },
  { path: 'inscription-en-ligne/:id', component: InscriptionEnLigneComponent },
  { path: 'test', component: TestComponent },
  { path: 'inscription-etudiant/:id', component: InscriptionEtudiantComponent },
  { path: 'calendrier-detail/:id', component: CalendrierDetailComponent  },
  { path: 'calendrier-en-ligne/:id', component: CalendrierEnLigneComponent },
  { path: 'offre/:id', component: OffreComponent },
  { path: 'sujet-et-correction/:id', component: SujetEtCorrectionComponent },
  { path: 'livres', component: BibliothqueComponent },
  { path: 'list-cours/:id', component: ListCoursComponent },
  { path: 'list-publication/:id', component: ListPublicationComponent },
  { path: 'list-distinction/:id', component: ListDistinctionComponent },
  { path: 'list-colloque/:id', component: ListColloqueComponent },
  { path: 'detail-publication/:id', component: DetailPublicationComponent },
  { path: 'detail-distinction/:id', component: DetailDistinctionComponent },
  { path: 'detail-colloque/:id', component: DetailColloqueComponent },
  { path: 'vie-de-campus/:type', component: ListVieDeCampusComponent },
  { path: 'detail-vie-de-campus/:id', component: DetailVieDeCampusComponent },
  { path: 'list-actualite/:id', component: ListActualiteComponent },
  { path: 'detail-actualite/:id', component: DetailActualiteComponent }
];

@NgModule({
  imports: [
    RouterModule.forRoot(routes),
    AdminRoutingModule,
    EtudiantRoutingModule,
    EnseignantRoutingModule
  ],
  exports: [RouterModule]
})
export class AppRoutingModule { }
