import { Component, Inject, OnInit, Pipe } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ChapitreService } from '../modules/services/chapitre.service';
import { MatiereService } from '../modules/services/matiere.service';
import { SectionService } from '../modules/services/section.service';
import { Chapitre } from '../modules/shared/chapitre';
import { Matiere } from '../modules/shared/matiere';
import { Section } from '../modules/shared/section';
import { EnseignantService } from '../modules/services/enseignant.service';
import { Enseignant } from '../modules/shared/enseignant';
import { baseURL } from '../modules/shared/baseurl';
import { MatDialog, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { SupprimerSectionComponent } from '../modules/admin/supprimer-section/supprimer-section.component';
import { SupprimerChapitreComponent } from '../modules/admin/supprimer-chapitre/supprimer-chapitre.component';
import { AuthService } from '../services/auth.service';
import { flyInOut, expand } from 'animations/app.animation';

@Component({
  selector: 'app-matiere-detail',
  templateUrl: './matiere-detail.component.html',
  styleUrls: ['./matiere-detail.component.scss'],
  host: {
    '[@flyInOut]': 'true',
    'style': 'display: block;'
  },
  animations: [
    flyInOut(),
    expand()
  ]
})
export class MatiereDetailComponent implements OnInit {
  panelOpenState = false;
  matiere?: Matiere;
  loading: boolean = false;
  idMatiere: string = "";
  chapitres?: Chapitre[];
  chapitre: Chapitre;
  url: string = 'https://www.youtube.com/embed/';
  sections: Section[] = [];
  booleen: boolean = false;
  enseignant?: Enseignant;
  index = 0;
  loginId?: string;
  courseOwner: boolean = false;
  idSection?: string;
  sectio: any[] = [];

  constructor(
    private route: ActivatedRoute,
    private matiereService: MatiereService,
    private chapitreService: ChapitreService,
    private sectionService: SectionService,
    private enseignantService: EnseignantService,
    private dialog: MatDialog,
    private authService: AuthService,
    @Inject('baseURL') public baseURL: any
  ) { /*this.authService.logout()*/ }

  ngOnInit(): void {
    this.idMatiere = this.route.snapshot.params['id'];
    this.loginId = localStorage.getItem('_id')?.toString();
    this.matiereService
      .getMatiereById(this.idMatiere)
      .subscribe(
        matiere => {
          this.matiere = matiere;
          if (this.matiere != null)
            this.enseignantService
              .getEnseignantByCompteId(this.matiere.compte._id)
              .subscribe(enseignant => {
                this.enseignant = enseignant;
                this.courseOwner = (enseignant.compte._id == this.loginId) ? true : false;
              })
        },
        error => {},
        () => { this.loading = false }
      )

    this.chapitreService
      .getChapitreByMatiereId(this.idMatiere)
      .subscribe(
        chapitres => {
          this.chapitres = chapitres;
          this.chapitre = this.chapitres[2];
          //this.getSectionByChapitre(this.chapitre)
          this.chapitres.forEach(c => {
            this.sectionService
              .getSectionByChapitreId(c._id)
              .subscribe(se => {
                this.sectio.push(se);
              })
          })
        },
        error => {},
        () => { this.loading = false }
      )

  }

  getSectionByChapitre(chapitre: Chapitre) {
    this.sectionService
      .getSectionByChapitreId(chapitre._id)
      .subscribe(sections => {
        this.sections = sections;
      });
  }

  getReturn(): boolean {
    let valeur: boolean = false;
    return !valeur;
  }

  openSupprimerSectionModal(id: string) {
    this.dialog.open(SupprimerSectionComponent, {
      width: "50%",
      height: "50%",
      position: {
        top: '8%'
      },
      data: { id: id }
    })
  }

  openSupprimerChapitreModal(id: string) {
    this.dialog.open(SupprimerChapitreComponent, {
      width: "50%",
      height: "50%",
      position: {
        top: '8%'
      },
      data: { id: id }
    })
  }
}
