import { Component, Input, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { UniversiteService } from '../modules/services/universite.service';
import { baseURL } from '../modules/shared/baseurl';
import { Universite } from '../modules/shared/universite';
import { AuthService } from '../services/auth.service';
import { flyInOut, expand } from 'animations/app.animation';

@Component({
  selector: 'app-list-universite',
  templateUrl: './list-universite.component.html',
  styleUrls: ['./list-universite.component.scss'],
  host: {
    '[@flyInOut]': 'true',
    'style': 'display: block;'
  },
  animations: [
    flyInOut(),
    expand()
  ]
})
export class ListUniversiteComponent implements OnInit {
  baseURL: string = baseURL;
  universites: Universite[] = [];
  idUniversite?: string;
  loading: boolean = true;
  i?: string;

  constructor(
    private universiteService: UniversiteService,
    private router: Router,
    private authService: AuthService,
    private route: ActivatedRoute
  ) { }

  ngOnInit(): void {
    //this.idUniversite = this.route.snapshot.params['id']
    this.idUniversite = localStorage.getItem('universite')?.toString()
    this.universiteService
      .getAllUniversities()
      .subscribe(
          universites => this.universites = universites,
          error => {},
          () => { this.loading = false }
        );
  }

  goToUniversiteDetail(id: string) {
    this.authService.goToUniversiteDetail(id)
      .subscribe(id => {
        this.idUniversite = id;
        this.i = localStorage.getItem('universite')?.toString()
        this.router.navigateByUrl('universite/' + this.idUniversite);
      });
  }

}

