import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { RechercheService } from '../modules/services/recherche.service';
import { UniversiteService } from '../modules/services/universite.service';
import { baseURL } from '../modules/shared/baseurl';
import { Recherche } from '../modules/shared/recherche';
import { Universite } from '../modules/shared/universite';
import { flyInOut, expand } from 'animations/app.animation';

@Component({
  selector: 'app-list-publication',
  templateUrl: './list-publication.component.html',
  styleUrls: ['./list-publication.component.scss'],
  host: {
    '[@flyInOut]': 'true',
    'style': 'display: block;'
  },
  animations: [
    flyInOut(),
    expand()
  ]
})
export class ListPublicationComponent implements OnInit {
  idUniversite: string = '';
  universite: Universite;
  recherches: Recherche[] = [];
  baseURL: string = baseURL;
  loading: boolean = true;
  constructor(
    private universiteService: UniversiteService,
    private rechercheService: RechercheService,
    private route: ActivatedRoute
  ) { }

  ngOnInit(): void {
    this.idUniversite = this.route.snapshot.params['id'];
    this.universiteService
        .getUniversiteById( this.idUniversite )
        .subscribe( 
          universite => this.universite = universite,
          error => {},
          () => this.loading = false
        );

    this.rechercheService
        .getRechercheByUniversiteId( this.idUniversite )
        .subscribe( recherches => this.recherches = recherches )
  }

}
