import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { DepartementService } from '../modules/services/departement.service';
import { ParcoursService } from '../modules/services/parcours.service';
import { UniversiteService } from '../modules/services/universite.service';
import { Departement } from '../modules/shared/departement';
import { Parcours } from '../modules/shared/parcours';
import { Universite } from '../modules/shared/universite';
import { flyInOut, expand } from 'animations/app.animation';
@Component({
  selector: 'app-calendrier-en-ligne',
  templateUrl: './calendrier-en-ligne.component.html',
  styleUrls: ['./calendrier-en-ligne.component.scss'],
  host: {
    '[@flyInOut]': 'true',
    'style': 'display: block;'
  },
  animations: [
    flyInOut(),
    expand()
  ]
})
export class CalendrierEnLigneComponent implements OnInit {
  idUniversite: string = '';
  universite: Universite;
  departements: Departement[];
  parcours: any = {};
  constructor(
    private universiteService: UniversiteService,
    private departementService: DepartementService,
    private parcoursService: ParcoursService,
    private route: ActivatedRoute
  ) { }

  ngOnInit(): void {
    this.idUniversite = this.route.snapshot.params['id'];
    this.universiteService
        .getUniversiteById( this.idUniversite )
        .subscribe( universite => this.universite = universite );

    this.departementService
        .getDepartementByUniversiteById( this.idUniversite )
        .subscribe( departements => {
          this.departements = departements;
          this.departements.forEach( d => {
            this.parcoursService
                .getParcoursByDepartementId(d._id)
                .subscribe( p => {
                  this.parcours[d.nom] = p;
                } )
          } )
        });
  }

}
