import { Component, OnInit, ViewChild } from '@angular/core';
import { FormArray, FormBuilder, FormGroup } from '@angular/forms';
import { MatDialog } from '@angular/material/dialog';
import { ActivatedRoute, Router } from '@angular/router';
import { head } from 'lodash';
import { PresentationService } from '../../services/presentation.service';
import { UniversiteService } from '../../services/universite.service';
import { Presentation } from '../../shared/presentation';
import { Universite } from '../../shared/universite';
import { SupprimerPresentationComponent } from '../supprimer-presentation/supprimer-presentation.component';

@Component({
  selector: 'app-ajouter-presentation',
  templateUrl: './ajouter-presentation.component.html',
  styleUrls: ['./ajouter-presentation.component.scss']
})
export class AjouterPresentationComponent implements OnInit {
  @ViewChild('fform', { static: false }) presentationFormDirective: any;
  presentationForm: FormGroup;
  items: FormArray;
  idUniversite: string = "";
  presentation?: Presentation;
  universite?: Universite;
  presentations?: Presentation[];

  constructor(
    private formBuilder: FormBuilder,
    private universiteService: UniversiteService,
    private presentationService: PresentationService,
    private route: ActivatedRoute,
    private router: Router,
    private dialog: MatDialog
  ) { }

  ngOnInit() {
    this.idUniversite = this.route.snapshot.params['id'];

    this.presentationService
        .getPresentationByUniversiteId( this.idUniversite )
        .subscribe( presentations => this.presentations = presentations )

    this.universiteService
      .getUniversiteById(this.idUniversite)
      .subscribe(universite => {
        this.universite = universite;
      })

    this.presentationForm = this.formBuilder.group({
      items: this.formBuilder.array([this.createItem()])
    });
  }
  get presentationDetails() {
    return (this.presentationForm.controls.items as FormGroup).controls;
  }

  get presentationFormGroups() {
    return this.presentationForm.get('items') as FormArray
  }

  createItem(): FormGroup {
    return this.formBuilder.group({
      titre: '',
      description: '',
      universite: ''
    });
  }

  addItem(): void {
    this.items = this.presentationForm.get('items') as FormArray;
    this.items.push(this.createItem());
  }

  onSubmit() {
        for (let i = 0; i < this.presentationForm.value.items.length; i++) {
          this.presentationService
            .createPresentation(
              {
                titre: this.presentationForm.value.items[i].titre,
                description: this.presentationForm.value.items[i].description,
                universite: this.universite
              } as Presentation
            )
            .subscribe(presentation => {
              location.reload();
            });
        }
  }

  supprimerPresentationDialog(idPresentation: string){
    this.dialog.open( SupprimerPresentationComponent, {
      position: {
        top: '8%'
      },
      width: '35%',
      height: '35%',
      data: {
        idPresentation: idPresentation,
        idUniversite: this.idUniversite
      }
    } )
  }
}