import { Component, Inject, OnChanges, OnInit, ViewChild } from '@angular/core';
import { PLANSITE } from '../modules/shared/plansite';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Compte } from '../modules/shared/compte';
import { AuthService } from '../services/auth.service';
import jwt_decode from 'jwt-decode';
import { ActivatedRoute, Router } from '@angular/router';
import { EtudiantService } from '../modules/services/etudiant.service';
import { EnseignantService } from '../modules/services/enseignant.service';
import { CompteService } from '../modules/services/compte.service';
import { MatDialog } from '@angular/material/dialog';


@Component({
  selector: 'app-logout',
  templateUrl: './logout.component.html',
  styleUrls: ['./logout.component.scss']
})
export class LogoutComponent implements OnInit {
  idUniversite: string = '';
  constructor(
    private authService: AuthService,
    private router: Router,
    private route: ActivatedRoute
  ) { }

  ngOnInit(): void {
    this.idUniversite = this.route.snapshot.params['idUniversite']; //localStorage.getItem('universite')?.toString();
    this.authService.logout();
    this.router.navigateByUrl('/accueil');
    this.reloadPage();
  }
  reloadPage() {
    setTimeout(() => {
      window.location.reload();
    }, 100);
  }
}
