import { Component, Input, OnInit,Output, EventEmitter } from '@angular/core';
import { PLANSITE } from '../modules/shared/plansite';
import { MatDialog } from '@angular/material/dialog';
import { LoginComponent } from '../login/login.component';
import { AuthService } from '../services/auth.service';
import { baseURL } from '../modules/shared/baseurl';
import { EtudiantService } from '../modules/services/etudiant.service';
import { EnseignantService } from '../modules/services/enseignant.service';
import { EnregisterEnsignantComponent } from '../modules/enseignant/enregister-ensignant/enregister-ensignant.component';
import { EnregistrerEtudiantComponent } from '../modules/etudiant/enregistrer-etudiant/enregistrer-etudiant.component';
import { ActivatedRoute, Router } from '@angular/router';
import { VariablesGlobales } from 'src/variablesGlobales';
import { Universite } from '../modules/shared/universite';
import { Actualite } from '../modules/shared/actualite';
import { ActualiteService } from '../modules/services/actualite.service';

@Component({
  selector: 'app-header-segment',
  templateUrl: './header-segment.component.html',
  styleUrls: ['./header-segment.component.scss']
})
export class HeaderSegmentComponent implements OnInit {

  @Input() idUniversite: string = '';
  @Input() login: string = '';
  @Input() universite: Universite;
  compteId?: string;
  baseURL: string = baseURL;
  plansite: any;
  langage: string = 'Franais';
  flag_un: string = '';
  username?: String;
  avatar?: string;
  cinqDerinereActualite: Actualite[]=[];
  @Output() newItemEvent = new EventEmitter<Actualite[]>();
  url: string = "";
  loading: boolean = false;
  langages: any = [
    {
      lang: 'Franais',
      icon: "flag-icon flag-icon-fr mr-1"
    },
    {
      lang: 'Anglais',
      icon: "flag-icon flag-icon-us mr-1"
    }
  ];
  constructor(public dialog: MatDialog,
    private authService: AuthService,
    private etudiantService: EtudiantService,
    private enseignantService: EnseignantService,
    private router: Router,
    private route: ActivatedRoute,
    private actualiteService: ActualiteService
  ) {
    this.universite? this.loading = true : this.loading = false;
  }

  addNewItem(cinqDerinereAct: Actualite[]) {
    this.newItemEvent.emit(cinqDerinereAct);
  }
  onLogout() {
    this.authService.logout();
    location.reload();
  }
  openDialog() {
    const dialogRef = this.dialog.open(LoginComponent);
    dialogRef.afterClosed().subscribe(result => {
    });
  }
  openDialogRegisterEnseignant() {
    const dialogRef = this.dialog.open(EnregisterEnsignantComponent, {
      width: '100%',
      autoFocus: true,
      maxHeight: '100vh',
      position: {
        top: '0px'
      }
    });

    dialogRef.afterClosed().subscribe(result => {});
  }

  openDialogRegisterEtudiant() {
    const dialogRef = this.dialog.open(EnregistrerEtudiantComponent, {
      width: '100%',
      autoFocus: true,
      maxHeight: '100vh',
      position: {
        top: '0px'
      }
    });
    dialogRef.afterClosed().subscribe(result => {});
  }



  ngOnInit(): void {
    this.url = location.pathname;
    this.compteId=localStorage.getItem('_id')?.toString();
    this.avatar = localStorage.getItem('avatar')?.toString();
    this.username = localStorage.getItem('username')?.toString();

    if (localStorage.getItem('langue') === 'Franais') {
      this.plansite = PLANSITE.fr;
      this.langage = this.langages[0].lang;
      this.flag_un = this.langages[0].icon;
      localStorage.setItem('flag_un', this.flag_un)
    } else if (localStorage.getItem('langue') === 'Anglais') {
      this.plansite = PLANSITE.en;
      this.langage = this.langages[1].lang;
      this.flag_un = this.langages[1].icon;
      localStorage.setItem('flag_un', this.flag_un)
    } else {
      localStorage.setItem('langue', 'Franais');
      this.langage = this.plansite.fr.fr;
      this.langage = this.langages[0].lang;
      this.flag_un = this.langages[0].icon;
      localStorage.setItem('flag_un', this.flag_un)
    }

    this.actualiteService
        .getLastFiveActualite(this.universite?._id)
        .subscribe( actualite => {
          this.cinqDerinereActualite = actualite
        } );
  }

  changerLangue(langue: string): void {
    localStorage.setItem('langue', langue);
    this.reloadPage()
  }

  reloadPage() {
    setTimeout(() => {
      window.location.reload();
    }, 100);
  }

  goToHome(routes: string) {
    this.router.navigateByUrl(routes);
  }

  mesCours() {
   this.router.navigateByUrl('mes-cours/' + this.compteId )
  }
}
