import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { baseURL } from '../shared/baseurl';
import { SujetEtCorrection } from '../shared/sujetEtCorrection';

@Injectable({
  providedIn: 'root'
})
export class SujetEtCorrectionService {

  constructor(private httpClient: HttpClient) { }
  getAllSujetEtCorrections(): Observable<SujetEtCorrection[]> {
    return this.httpClient.get<SujetEtCorrection[]>(baseURL + 'sujet-correction');
  }

  getSujetEtCorrectionById(id: string): Observable<SujetEtCorrection> {
    return this.httpClient.get<SujetEtCorrection>(baseURL + 'sujet-correction' + id)
  }

  createSujetEtCorrection(SujetEtCorrection: SujetEtCorrection): Observable<SujetEtCorrection> {
    return this.httpClient.post<SujetEtCorrection>(baseURL + 'sujet-correction', SujetEtCorrection, {});
  }

  getLogoName(file: File): Observable<any> {
    const formData = new FormData();
    formData.append('logo', file)
    console.log("SERVICE", formData);
    return this.httpClient.put<any>(baseURL + 'sujet-correction', formData, {});
  }

  getSujetEtCorrectionByClasseId(classeId: string): Observable<SujetEtCorrection[]> {
    return this.httpClient.get<SujetEtCorrection[]>(baseURL + 'sujet-correction/classes/' + classeId);
  }

  updateSujetEtCorrection(id: string, SujetEtCorrection: SujetEtCorrection): Observable<SujetEtCorrection> {
    return this.httpClient.put<SujetEtCorrection>(baseURL + 'sujet-correction/' + id, SujetEtCorrection);
  }
  //Les methodes de cration ici
  telechargerSujet(file: File): Observable<File> {
    let formData = new FormData();
    formData.append('sujet', file);
    console.log(formData);
    return this.httpClient.post<File>(baseURL + "sujet-correction/sujets", formData);
  }

  telechargerCorrection(file: File): Observable<File> {
    let formData = new FormData();
    formData.append('correction', file);
    console.log(formData);
    return this.httpClient.post<File>(baseURL + "sujet-correction/corrections", formData);
  }
}
