import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { baseURL } from '../shared/baseurl';
import { InscriptionEnLigne } from '../shared/inscriptionEnLigne';

@Injectable({
  providedIn: 'root'
})
export class InscriptionEnLigneService {

  
  constructor(private httpClient: HttpClient) { }

  getAllInscriptionEnLignes(): Observable<InscriptionEnLigne[]> {
    return this.httpClient.get<InscriptionEnLigne[]>(baseURL + 'enligne');
  }
  getInscriptionEnLigneById(idInscriptionEnLigne: string): Observable<InscriptionEnLigne> {
    return this.httpClient.get<InscriptionEnLigne>(baseURL + 'enligne/' + idInscriptionEnLigne);
  }

  createInscriptionEnLigne(inscriptionEnLigne: InscriptionEnLigne): Observable<InscriptionEnLigne> {
    return this.httpClient.post<InscriptionEnLigne>(baseURL + 'enligne', inscriptionEnLigne);
  }

  updateInscriptionEnLigne(idInscriptionEnLigne: string, inscriptionEnLigne: InscriptionEnLigne): Observable<InscriptionEnLigne> {
    return this.httpClient.put<InscriptionEnLigne>(baseURL + 'enligne/' + idInscriptionEnLigne, inscriptionEnLigne)
  }
  getInscriptionEnLigneByUniversiteId(idUniversite: string): Observable<InscriptionEnLigne[]> {
    return this.httpClient.get<InscriptionEnLigne[]>(baseURL + 'enligne/universites/' + idUniversite);
  }

  deleteInscriptionEnLigne(idInscriptionEnLigne: string): Observable<InscriptionEnLigne> {
    return this.httpClient.delete<InscriptionEnLigne>(baseURL + 'enligne/' + idInscriptionEnLigne);
  }
}

