import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { baseURL } from '../shared/baseurl';
import { Tutoral } from './../shared/tutoral';



@Injectable({
  providedIn: 'root'
})
export class TutoralService {
  constructor(private httpClient: HttpClient) { }

  getAllTutorals(): Observable<Tutoral[]> {
    return this.httpClient.get<Tutoral[]>(baseURL + 'tutorals');
  }
  getTutoralById(idTutoral?: string): Observable<Tutoral> {
    return this.httpClient.get<Tutoral>(baseURL + 'tutorals/' + idTutoral);
  }

  createTutoral(Tutoral: Tutoral): Observable<Tutoral> {
    return this.httpClient.post<Tutoral>(baseURL + 'tutorals', Tutoral);
  }

  updateTutoral(idTutoral?: string, Tutoral?: Tutoral): Observable<Tutoral> {
    return this.httpClient.put<Tutoral>(baseURL + 'tutorals/' + idTutoral, Tutoral)
  }
  getTutoralByType(type: string): Observable<Tutoral[]> {
    return this.httpClient.get<Tutoral[]>(baseURL + 'tutorals/types/' + type);
  }

  deleteTutoral(idTutoral?: string): Observable<Tutoral> {
    return this.httpClient.delete<Tutoral>(baseURL + 'tutorals/' + idTutoral);
  }
}

