import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { baseURL } from '../shared/baseurl';
import { Colloque } from '../shared/colloque';

@Injectable({
  providedIn: 'root'
})
export class ColloqueService {
  
  constructor(private httpClient: HttpClient) { }
  getAllColloque(): Observable<Colloque[]> {
    return this.httpClient.get<Colloque[]>(baseURL + 'colloques');
  }

  getColloqueById(id: string): Observable<Colloque> {
    return this.httpClient.get<Colloque>(baseURL + 'colloques/' + id)
  }

  createColloque(colloque: Colloque): Observable<Colloque> {
    return this.httpClient.post<Colloque>(baseURL + 'Colloques', colloque, {});
  }

  updateColloque(id: string, colloque: Colloque): Observable<Colloque> {
    return this.httpClient.put<Colloque>(baseURL + 'colloques/' + id, colloque);
  }
  //Les methodes de cration ici
  telecharger(file: File): Observable<File> {
    let formData = new FormData();
    formData.append('fichier', file);
    console.log(formData);
    return this.httpClient.put<File>(baseURL + "colloques", formData);
  }
  getColloqueByUniversiteId( idUniversite: string ): Observable<Colloque[]>{
    return this.httpClient.get<Colloque[]>( baseURL + 'colloques/universites/' + idUniversite );
  }
}
