import { Component, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { PresentationService } from '../../services/presentation.service';
import { UniversiteService } from '../../services/universite.service';
import { Presentation } from '../../shared/presentation';
import { Universite } from '../../shared/universite';

@Component({
  selector: 'app-modifier-presentation',
  templateUrl: './modifier-presentation.component.html',
  styleUrls: ['./modifier-presentation.component.scss']
})
export class ModifierPresentationComponent implements OnInit {
  @ViewChild('fform', { static: false }) presentionFormDirective: any;
  presentationForm: FormGroup;

  universite: Universite;
  presentation: Presentation;
  presentationUpdate: Presentation;
  loading: boolean = false;

  formErrors: any = {
    titre: '',
    description: ''
  };

  validationMessages: any = {
    titre: {
      required: 'le titre  est requis.',
      minlength: 'le titre tre plus que 4 caractres.'
    },
    description: {
      required: 'la description est requise.',
      minlength: 'la description tre plus que 6 caractres.'
    }
  };

  idPresentation: string = '';

  constructor(
    private formBuilder: FormBuilder,
    private router: Router,
    private presentationService: PresentationService,
    private universiteService: UniversiteService,
    private route: ActivatedRoute
  ) {
    this.createForm();
  }

  ngOnInit(): void {
    this.idPresentation = this.route.snapshot.params['id'];

    

    this.presentationService
      .getPresentationById(this.idPresentation)
      .subscribe(presentation => {
        this.presentationUpdate = presentation;
        var u = this.presentationUpdate.universite as Universite;
        this.universiteService
        .getUniversiteById( u._id )
        .subscribe( u => this.universite = u )
      });
  }

  createForm() {
    this.presentationForm = this.formBuilder.group({
      titre: '',
      description: ''
    });
    this.presentationForm
      .valueChanges
      .subscribe(data => this.onValueChanged(data));
    this.onValueChanged();
  }

  onValueChanged(data?: any) {
    if (!this.presentationForm) { return; }
    const form = this.presentationForm;
    for (const field in this.formErrors) {
      if (this.formErrors.hasOwnProperty(field)) {
        // clear previous error message (if any)
        this.formErrors[field] = '';
        const control = form.get(field);
        if (control && control.dirty && !control.valid) {
          const messages = this.validationMessages[field];
          for (const key in control.errors) {
            if (control.errors.hasOwnProperty(key)) {
              this.formErrors[field] += messages[key] + ' ';
            }
          }
        }
      }
    }
  }
  onSubmit() {
    this.presentation = this.presentationForm?.value;
    this.loading = true;
    this.presentationService
            .updatePresentation(this.idPresentation, this.presentation)
            .subscribe(univ => {},
              err => { },
              () => {
                setTimeout(() => { this.loading = false }, 1800);
                this.router.navigateByUrl('ajouter-presentation/' + this.presentationUpdate.universite)
              }
            )
    }
}
