import { Component, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { Universite } from '../../shared/universite';
import { Observable } from 'rxjs';
import { Departement } from '../../shared/departement';
import { DepartementService } from '../../services/departement.service';
import { Filiere } from '../../shared/filiere';
import { FiliereService } from '../../services/filiere.service';
import { Classe } from '../../shared/classe';
import { Matiere } from '../../shared/matiere';
import { Compte } from '../../shared/compte';
import { ClasseService } from '../../services/classe.service';
import { CompteService } from '../../services/compte.service';
import { MatiereService } from '../../services/matiere.service';
import { Semestre } from './../../shared/semestre';
import { SemestreService } from './../../services/semestre.service';

@Component({
  selector: 'app-ajouter-matiere',
  templateUrl: './ajouter-matiere.component.html',
  styleUrls: ['./ajouter-matiere.component.scss']
})
export class AjouterMatiereComponent implements OnInit {

  @ViewChild('fform', { static: false }) matiereFormDirective: any;
  matiereForm: FormGroup;

  classe: Classe;
  matiere: Matiere;
  loading: boolean = false;
  idClasse: string = '';
  compte: Compte;
  semestres: Semestre[];
  semestre: Semestre;
  idSemestre: string;

  formErrors: any = {
    nom: '',
    description: '',
    duree: ''
  };

  validationMessages: any = {
    nom: {
      required: 'le nom  est requis.',
      minlength: 'le nom tre plus que 2 caractres.',
      maxlength: 'le nom  ne doit pas tre plus que 150 caractres.'
    },
    description: {
      required: 'la description est requise.',
      minlength: 'la description tre plus que 10 caractres.',
      maxlength: 'la description ne doit pas tre plus que 1000 caractres.'
    },
    duree: {
      required: 'la dure est requise.',
      minlength: 'la dure doit tre suprieure  0 mois',
      maxlength: 'la dure doit tre infrieure  12 mois'
    }
  };


  constructor(
    private formBuilder: FormBuilder,
    private router: Router,
    private route: ActivatedRoute,
    private semestreService: SemestreService,
    private compteService: CompteService,
    private matiereService: MatiereService
  ) { this.createForm(); }

  ngOnInit(): void {
    this.idSemestre = this.route.snapshot.params['id'];
    this.compteService.getCompteById(localStorage.getItem('_id')?.toString())
      .subscribe(data => this.compte = data);
    this.semestreService
      .getSemestreById(this.idSemestre)
      .subscribe(semestre => this.semestre = semestre);
  }


  createForm() {
    this.matiereForm = this.formBuilder.group({
      nom: ['', [Validators.required, Validators.minLength(2), Validators.maxLength(150)]],
      description: ['', [Validators.required, Validators.minLength(10), Validators.maxLength(1000)]],
      duree: ['', [Validators.required, Validators.minLength(0), Validators.maxLength(12)]]
    });
    this.matiereForm
      .valueChanges
      .subscribe(data => this.onValueChanged(data));
    this.onValueChanged();
  }

  onValueChanged(data?: any) {
    if (!this.matiereForm) { return; }
    const form = this.matiereForm;
    for (const field in this.formErrors) {
      if (this.formErrors.hasOwnProperty(field)) {
        // clear previous error message (if any)
        this.formErrors[field] = '';
        const control = form.get(field);
        if (control && control.dirty && !control.valid) {
          const messages = this.validationMessages[field];
          for (const key in control.errors) {
            if (control.errors.hasOwnProperty(key)) {
              this.formErrors[field] += messages[key] + ' ';
            }
          }
        }
      }
    }
  }

  ngOnChanges() {
    this.idClasse = this.route.snapshot.params['id'];
  }
  onSubmit() {
    this.matiere = this.matiereForm?.value;
    this.matiere.compte = this.compte;
    this.loading = true;
    this.matiereService
      .createMatiere(this.matiere)
      .subscribe(matiere => {
        this.semestreService
            .getSemestreById( this.idSemestre )
            .subscribe( semestre => {
              this.semestre = semestre;
              this.semestre.matieres.push( matiere );
              this.semestreService
                  .updateSemestre( semestre._id, semestre )
                  .subscribe( sem => this.router.navigate(['/']) );
            });
      },
        err => { },
        () => {
          setTimeout(() => { this.loading = false }, 1800);
          this.router.navigateByUrl('/classe-detail/' + this.idClasse)
        }
      )
    this.matiereFormDirective.resetForm();
  }


  reloadPage() {
    setTimeout(() => {
      window.location.reload();
    }, 100);
  }

}
