import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { DistinctionService } from '../modules/services/distinction.service';
import { UniversiteService } from '../modules/services/universite.service';
import { baseURL } from '../modules/shared/baseurl';
import { Distinction } from '../modules/shared/distinction';
import { Universite } from '../modules/shared/universite';
import { flyInOut, expand } from 'animations/app.animation';

@Component({
  selector: 'app-detail-distinction',
  templateUrl: './detail-distinction.component.html',
  styleUrls: ['./detail-distinction.component.scss'],
  host: {
    '[@flyInOut]': 'true',
    'style': 'display: block;'
  },
  animations: [
    flyInOut(),
    expand()
  ]
})
export class DetailDistinctionComponent implements OnInit {
  idUniversite: string = '';
  idDistinction: string;
  universite: Universite;
  distinction: Distinction;
  baseURL: string = baseURL;
  constructor(
    private universiteService: UniversiteService,
    private distinctionService: DistinctionService,
    private route: ActivatedRoute
  ) { }

  ngOnInit(): void {
    this.idDistinction = this.route.snapshot.params['id'];
    this.idUniversite = localStorage.getItem('universite')?.toString() as string;
    this.universiteService
        .getUniversiteById( this.idUniversite )
        .subscribe( universite => this.universite = universite );

    this.distinctionService
        .getDistinctionById( this.idDistinction )
        .subscribe( distinction => this.distinction =distinction )
  }

}
