import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { baseURL } from '../shared/baseurl';
import { Chapitre } from '../shared/chapitre';

@Injectable({
  providedIn: 'root'
})
export class ChapitreService {
  constructor(private httpClient: HttpClient) { }

  getAllChapitres(): Observable<Chapitre[]> {
    return this.httpClient.get<Chapitre[]>(baseURL + 'chapitres');
  }
  getChapitreById(idChapitre: string): Observable<Chapitre> {
    return this.httpClient.get<Chapitre>(baseURL + 'chapitres/' + idChapitre);
  }

  createChapitre(chapitre: Chapitre): Observable<Chapitre> {
    return this.httpClient.post<Chapitre>(baseURL + 'chapitres', chapitre);
  }

  updateChapitre(idChapitre: string, chapitre: Chapitre): Observable<Chapitre> {
    return this.httpClient.put<Chapitre>(baseURL + 'chapitres/' + idChapitre, chapitre)
  }
  getChapitreByMatiereId(idMaitiere: string): Observable<Chapitre[]> {
    return this.httpClient.get<Chapitre[]>(baseURL + 'chapitres/matieres/' + idMaitiere);
  }

  deleteChapitre(idChapitre: string): Observable<Chapitre> {
    return this.httpClient.delete<Chapitre>(baseURL + 'chapitres/' + idChapitre);
  }
}
