import { Component, OnInit, ViewChild } from '@angular/core';
import { FormArray, FormBuilder, FormGroup } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { ChapitreService } from '../../services/chapitre.service';
import { MatiereService } from '../../services/matiere.service';
import { SectionService } from '../../services/section.service';
import { Chapitre } from '../../shared/chapitre';
import { Matiere } from '../../shared/matiere';
import { Section } from '../../shared/section';

@Component({
  selector: 'app-ajouter-une-section',
  templateUrl: './ajouter-une-section.component.html',
  styleUrls: ['./ajouter-une-section.component.scss']
})
export class AjouterUneSectionComponent implements OnInit {
  @ViewChild('fform', { static: false }) compteFormDirective: any;
  chapitreForm: FormGroup;
  items: FormArray;
  selectedFile: File[] = [];
  idChapitre: string = "";
  matiere?: Matiere;
  chapitre: Chapitre;
  msgError: string = '';

  constructor(
    private formBuilder: FormBuilder,
    private sectionService: SectionService,
    private matiereService: MatiereService,
    private route: ActivatedRoute,
    private router: Router,
    private chapitreService: ChapitreService
  ) { }

  ngOnInit() {
    this.idChapitre = this.route.snapshot.params['id'];

    this.chapitreService
      .getChapitreById(this.idChapitre)
      .subscribe(chapitre => {
        this.chapitre = chapitre;
      })

    this.chapitreForm = this.formBuilder.group({
      items: this.formBuilder.array([this.createItem()])
    });
  }
  get userDetails() {
    return (this.chapitreForm.controls.items as FormGroup).controls;
  }

  get userFormGroups() {
    return this.chapitreForm.get('items') as FormArray
  }

  createItem(): FormGroup {
    return this.formBuilder.group({
      titre: '',
      contenu: '',
      fichier: '',
      video: ''
    });
  }

  addItem(): void {
    this.items = this.chapitreForm.get('items') as FormArray;
    this.items.push(this.createItem());
  }

  onSubmit() {
    for (let i = 0; i < this.chapitreForm.value.items.length; i++) {
      var valeur = this.chapitreForm.value.items[i].video.substr(this.chapitreForm.value.items[i].video.indexOf("=") + 1)
      var autre: string = "";
      if(valeur.indexOf("&")>=0) {
        autre = valeur.substr(0, valeur.indexOf("&"));
        this.chapitreForm.value.items[i].video = autre;
      } else
        this.chapitreForm.value.items[i].video = valeur;
    }
    for (let i = 0; i < this.chapitreForm.value.items.length; i++) {
      this.sectionService
        .createSection(
          {
            titre: this.chapitreForm.value.items[i].titre,
            contenu: this.chapitreForm.value.items[i].contenu,
            fichier: this.chapitreForm.value.items[i].fichier,
            video: this.chapitreForm.value.items[i].video,
            chapitre: this.chapitre
          } as Section,
          this.selectedFile[i]
        )
        .subscribe(data => {});
      this.sectionService
        .telecharger(this.selectedFile[i])
        .subscribe(
          data => {  },
          error => {  },
          () => {
            this.router.navigateByUrl('/facultes')
          }
        )
    }

  }

  fileChanged(event: any) {
    this.selectedFile.push(event.target.files[0]);
  }

}
