import { Component, Inject, OnInit } from '@angular/core';
import { MatDialog, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { PresentationService } from '../../services/presentation.service';
import { Presentation } from '../../shared/presentation';

@Component({
  selector: 'app-supprimer-presentation',
  templateUrl: './supprimer-presentation.component.html',
  styleUrls: ['./supprimer-presentation.component.scss']
})
export class SupprimerPresentationComponent implements OnInit {
  idPresentation: string = '';
  presentation?: Presentation;
  loading: boolean = false;
  constructor(
    private presentationService: PresentationService,
    @Inject(MAT_DIALOG_DATA) private data: any,
    private matDialog: MatDialog,
    private router: Router
  ) { }

  ngOnInit(): void {
    this.idPresentation = this.data.idPresentation;
    this.presentationService
      .getPresentationById(this.idPresentation)
      .subscribe(presentation => this.presentation = presentation)
  }

  supprimerPresentation() {
    this.loading = true;
    this.presentationService
      .deletePresentation(this.idPresentation)
      .subscribe(
        filiere => {},
        error => {},
        () => { 
          this.loading = false, 
          this.matDialog.closeAll(),
          location.reload() 
        }
      )
  }

  closeModal() {
    this.matDialog.closeAll()
  }

}
