import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ClasseService } from '../../services/classe.service';
import { ParcoursService } from '../../services/parcours.service';
import { Classe } from '../../shared/classe';
import { Parcours } from '../../shared/parcours';

@Component({
  selector: 'app-detail-parcours',
  templateUrl: './detail-parcours.component.html',
  styleUrls: ['./detail-parcours.component.scss']
})
export class DetailParcoursComponent implements OnInit {
  loading: boolean = false;
  parcours: Parcours;
  idParcours: string = '';
  classes?: Classe[];
  constructor(
    private parcoursService: ParcoursService,
    private route: ActivatedRoute,
    private classeService: ClasseService
  ) { }

  ngOnInit(): void {
    this.idParcours = this.route.snapshot.params['id'];
    this.parcoursService
      .getParcoursById(this.idParcours)
      .subscribe(parcours => this.parcours = parcours);
    this.getAllClasses();
  }

  getAllClasses() {
    this.loading = true;
    this.classeService
      .getClasseByParcoursId(this.idParcours)
      .subscribe(
        classes => { this.classes = classes },
        error => {  },
        () => { this.loading = false; }
      )
  }

}

