import { Component, Inject, OnInit } from '@angular/core';
import { MAT_DIALOG_DATA } from '@angular/material/dialog';
import { Classe } from '../modules/shared/classe';
import { Departement } from '../modules/shared/departement';
import { flyInOut, expand } from 'animations/app.animation';
@Component({
  selector: 'app-modal-access-restreint',
  templateUrl: './modal-access-restreint.component.html',
  styleUrls: ['./modal-access-restreint.component.scss'],
  host: {
    '[@flyInOut]': 'true',
    'style': 'display: block;'
  },
  animations: [
    flyInOut(),
    expand()
  ]
})
export class ModalAccessRestreintComponent implements OnInit {

  classe: Classe;
  departement: Departement;

  constructor(
    @Inject(MAT_DIALOG_DATA) public data: any
  ) { }

  ngOnInit(): void {
    this.classe = this.data.classe;
    this.departement = this.data.departement;
  }

}
