import { Component, OnInit, ViewChild } from '@angular/core';
import { FormArray, FormBuilder, FormGroup } from '@angular/forms';
import { MatDialog } from '@angular/material/dialog';
import { ActivatedRoute, Router } from '@angular/router';
import { EnregistrerEtudiantComponent } from '../modules/etudiant/enregistrer-etudiant/enregistrer-etudiant.component';
import { ClasseService } from '../modules/services/classe.service';
import { CompteService } from '../modules/services/compte.service';
import { DossierService } from '../modules/services/dossier.service';
import { ParcoursService } from '../modules/services/parcours.service';
import { UniversiteService } from '../modules/services/universite.service';
import { Classe } from '../modules/shared/classe';
import { Compte } from '../modules/shared/compte';
import { Dossier } from '../modules/shared/dossier';
import { Etudiant } from '../modules/shared/etudiant';
import { Parcours } from '../modules/shared/parcours';
import { Universite } from '../modules/shared/universite';
import { ValidationDossierEtudiantComponent } from '../validation-dossier-etudiant/validation-dossier-etudiant.component';
import { VerifierDossierModalComponent } from './../verifier-dossier-modal/verifier-dossier-modal.component';
import { baseURL } from './../modules/shared/baseurl';
import { UpdateDossierModalComponent } from './../update-dossier-modal/update-dossier-modal.component';
import { UpdateReleveModalComponent } from './../update-releve-modal/update-releve-modal.component';
import { UpdateReleveSecondModalComponent } from './../update-releve-second-modal/update-releve-second-modal.component';
import { flyInOut, expand } from 'animations/app.animation';
@Component({
  selector: 'app-inscription-etudiant',
  templateUrl: './inscription-etudiant.component.html',
  styleUrls: ['./inscription-etudiant.component.scss'],
  host: {
    '[@flyInOut]': 'true',
    'style': 'display: block;'
  },
  animations: [
    flyInOut(),
    expand()
  ]
})
export class InscriptionEtudiantComponent implements OnInit {
  @ViewChild('fform', { static: false }) etudiantFormDirective: any;
  etudiantForm: FormGroup;
  items: FormArray;
  idUniversite: string = "";
  etudiant?: Etudiant;
  universite: Universite;
  classes: Classe[];
  releveFiles: File[] = [];
  diplomeFile: File;
  releveBACFile: File;
  compte: Compte;
  dossier: Dossier;
  dossierExiste: Dossier;
  compteId?: string;
  parcours: Parcours[] = [];
  loading: boolean = true;

  constructor(
    private formBuilder: FormBuilder,
    private universiteService: UniversiteService,
    private classeService: ClasseService,
    private dossierService: DossierService,
    private route: ActivatedRoute,
    private router: Router,
    private compteService: CompteService,
    private dialog: MatDialog
  ) {
    this.compteId = localStorage.getItem('_id')?.toString();
    if(this.compteId != null || this.compteId != undefined) {
      this.compteService
          .getCompteById( this.compteId )
          .subscribe( compte => {
            this.compte = compte;
          }
        );
      }
   }
  
  ngOnInit() {
    this.idUniversite = this.route.snapshot.params['id'];
  
    this.universiteService
        .getUniversiteById(this.idUniversite)
        .subscribe(
            universite => {this.universite = universite},
            error => {},
            () => this.loading = false
          )

    this.classeService
        .getAllClasses()
        .subscribe( classes => this.classes = classes );

    this.etudiantForm = this.formBuilder.group({
      releves: this.formBuilder.array([this.createReleve()]),
      diplome:'',
      releve: '',
      compte: '',
      classe: ''
    });
   
      this.dossierService
        .getDossierByUniversiteAndCompte(this.idUniversite, this.compteId)
        .subscribe( dossier => {
             this.dossierExiste = dossier;
          })
        if( this.compteId == null )
          this.openModal();
  }
  get etudiantDetails() {
    return (this.etudiantForm.controls.items as FormGroup).controls;
  }

  get etudiantFormGroups() {
    return this.etudiantForm.get('releves') as FormArray
  }

  createReleve(): FormGroup {
    return this.formBuilder.group({
      fichier: ''
    });
  }


  addItem(): void {
    this.items = this.etudiantForm.get('releves') as FormArray;
    this.items.push(this.createReleve());
  }
  

  onSubmit() {
    this.dossier = this.etudiantForm.value;
    this.dossier.releveNotes = [];
    this.dossier.diplome = 'images/' + this.compte?.username + '/dossiers/' + this.diplomeFile.name;
    this.dossier.releveBAC = 'images/' + this.compte?.username + '/dossiers/' + this.releveBACFile.name;
    this.dossier.universite = this.universite;

    for(let i=0; i < this.etudiantForm.value.releves.length; i++) {
      this.dossier?.releveNotes.push('images/' + this.compte?.username + '/dossiers/' + this.releveFiles[i].name );
    }
    this.dossier.compte = this.compte as Compte;
    //this.compte._id;
    this.onValidationEtudiant(this.etudiantForm.value.classe);
  }

  onValidationEtudiant(idClasse?: string) {
    const dialogRef = this.dialog.open( ValidationDossierEtudiantComponent, {
      width: '100%',
      autoFocus: true,
      maxHeight: '100vh',
      position: {
        top: '5%'
      },
      data: {
        etudiantForm: this.etudiantForm,
        idUniversite: this.idUniversite,
        releveFiles: this.releveFiles,
        diplomeFile: this.diplomeFile,
        releveBACFile: this.releveBACFile,
        universite: this.universite,
        classe: idClasse,
        compte: this.compte,
        dossier: this.dossier
      }
    });

    dialogRef.afterClosed().subscribe(result => {});
  }

  fileChangedReveles(event: any) {
    this.releveFiles.push(event.target.files[0]);
  }

  fileChangedDiplome(event: any) {
    this.diplomeFile= event.target.files[0];
  }
  fileChangedBAC(event: any) {
    this.releveBACFile= event.target.files[0];
  }

  openModal() {
    this.dialog.open( EnregistrerEtudiantComponent, {
      position: {
        top: '0%'
      }
    } )
  }

  openModalDossier(fichier: string) {
    this.dialog.open( VerifierDossierModalComponent, {
      position: {
        top: '0%'
      },
      data: {
        fichier: baseURL + fichier
      }
    } )
  }

  openModalDossierModifier(nomFichier?: string) {
    this.dialog.open( UpdateDossierModalComponent, {
      position: {
        top: '3%'
      },
      data: {
        dossier: this.dossierExiste,
        username: this.compte.username
      }
    } )
  }

  openModalUpdateReleve() {
    this.dialog.open( UpdateReleveModalComponent, {
      position: {
        top: '3%'
      },
      data: {
        dossier: this.dossierExiste,
        username: this.compte.username
      }
    } )
  }

  openModalUpdateReleveSecondaire(index: number) {
    this.dialog.open( UpdateReleveSecondModalComponent, {
      position: {
        top: '3%'
      },
      data: {
        dossier: this.dossierExiste,
        username: this.compte.username,
        index: index
      }
    } )
  }
}