import { Component, Inject, OnChanges, OnInit, ViewChild } from '@angular/core';
import { PLANSITE } from '../modules/shared/plansite';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Compte } from '../modules/shared/compte';
import { AuthService } from '../services/auth.service';
import jwt_decode from 'jwt-decode';
import { Router } from '@angular/router';
import { EtudiantService } from '../modules/services/etudiant.service';
import { EnseignantService } from '../modules/services/enseignant.service';
import { CompteService } from '../modules/services/compte.service';
import { MatDialog } from '@angular/material/dialog';
import { flyInOut, expand } from 'animations/app.animation';

@Component({
  selector: 'app-modifier-mot-de-passe',
  templateUrl: './modifier-mot-de-passe.component.html',
  styleUrls: ['./modifier-mot-de-passe.component.scss'],
  host: {
    '[@flyInOut]': 'true',
    'style': 'display: block;'
  },
  animations: [
    flyInOut(),
    expand()
  ]
})
export class ModifierMotDePasseComponent implements OnInit {
  @ViewChild('ffform', { static: true }) compteFormDirective: any;
  @ViewChild('fform', { static: false }) emailFormDirective: any;
  compteForm: FormGroup;
  emailForm: FormGroup;
  email?: string;
  emailIsValid: boolean = false;

  compte?: any;
  loading: boolean = false;
  formErrorsEmail: any = {
    'email': ''
  };
  formErrorsCompte: any = {
    'password': '',
    'confirmation': ''
  };

  validationMessagesEmail: any = {
    'email': {
      'required': 'l\'e-mail est requis.',
      'email': 'l\'e-mail valide ',
      'minlength': ' & l\'e-mail tre plus que 4 caractres.',
      'maxlength': 'l\'e-mail ne doit pas tre plus que 150 caractres.'
    }
  }
  validationMessagesCompte: any = {
    'password': {
      'required': 'le mot de passe est requis.',
      'minlength': 'le mot de passe tre plus que 6 caractres.',
      'maxlength': 'le mot de passe ne doit pas tre plus que 150 caractres.'
    },
    'confirmation': {
      'required': 'la confirmation du mot de passe est requise.',
      'minlength': 'la confirmation du mot de passe tre plus que 6 caractres.',
      'maxlength': 'la confirmation du mot de passe ne doit pas tre plus que 150 caractres.'
    }
  };

  compteId?: string;
  msgError?: string;

  plansite = PLANSITE;
  constructor(
    private formBuilder: FormBuilder,
    private authService: AuthService,
    private router: Router,
    private etudiantSerice: EtudiantService,
    private enseignantService: EnseignantService,
    private compteService: CompteService,
    private matDiag: MatDialog
  ) {
    this.createFormEmail();
    this.createFormCompte();
  }

  ngOnInit(): void {
  }

  createFormEmail() {
    this.emailForm = this.formBuilder.group({
      email: ['', [Validators.required, Validators.email, Validators.minLength(4), Validators.maxLength(150)]]
    });
    this.emailForm
      .valueChanges
      .subscribe(data => this.onValueChangedEmail(data));
    this.onValueChangedEmail();
  }

  createFormCompte() {
    this.compteForm = this.formBuilder.group({
      password: ['', [Validators.required, Validators.minLength(6), Validators.maxLength(150)]],
      confirmation: ['', [Validators.required, Validators.minLength(6), Validators.maxLength(150)]]
    });
    this.compteForm
      .valueChanges
      .subscribe(data => this.onValueChangedCompte(data));
    this.onValueChangedCompte();
  }

  onValueChangedEmail(data?: any) {
    if (!this.emailForm) { return; }
    const form = this.emailForm;
    for (const field in this.formErrorsEmail) {
      if (this.formErrorsEmail.hasOwnProperty(field)) {
        // clear previous error message (if any)
        this.formErrorsEmail[field] = '';
        const control = form.get(field);
        if (control && control.dirty && !control.valid) {
          const messages = this.validationMessagesEmail[field];
          for (const key in control.errors) {
            if (control.errors.hasOwnProperty(key)) {
              this.formErrorsEmail[field] += messages[key] + ' ';
            }
          }
        }
      }
    }
  }

  onValueChangedCompte(data?: any) {
    if (!this.compteForm) { return; }
    const form = this.compteForm;
    for (const field in this.formErrorsCompte) {
      if (this.formErrorsCompte.hasOwnProperty(field)) {
        // clear previous error message (if any)
        this.formErrorsCompte[field] = '';
        const control = form.get(field);
        if (control && control.dirty && !control.valid) {
          const messages = this.validationMessagesCompte[field];
          for (const key in control.errors) {
            if (control.errors.hasOwnProperty(key)) {
              this.formErrorsCompte[field] += messages[key] + ' ';
            }
          }
        }
      }
    }
  }


  closeModal() {
    this.router.navigateByUrl('reset-password');
    this.matDiag.closeAll()
  }

  onSubmitEmail() {
    this.email = this.emailForm?.value.email;
    this.loading = true;
    this.compteService
      .verifyEmail(this.email)
      .subscribe(
        user => { this.emailIsValid = true, localStorage.setItem('email', user.email) },
        error => { this.msgError = error.error.message, this.loading = false; },
        () => {
          this.loading = false
        }

      )
    this.emailFormDirective.resetForm();
  }

  onSubmitCompte() {
    this.compte = this.compteForm?.value;
    if (this.compte.password !== this.compte.confirmation) {
      this.msgError = 'Le mot de passe ne correspond pas  sa confirmation !';
    } else {
      let email = localStorage.getItem('email')?.toString();
      this.compteService
        .resetPassword(email, this.compte)
        .subscribe(
          user => { this.emailIsValid = true, localStorage.removeItem('email'); this.goToConfirmation(); },
          error => { this.msgError = error.error.message; this.loading = false; },
          () => {
            this.loading = false
          }

        )
    }
    if (this.compteFormDirective != null)
      this.compteFormDirective.resetForm();
  }


  reloadPage() {
    setTimeout(() => {
      window.location.reload();
    }, 100);
  }

  goToConfirmation() {
    this.router.navigateByUrl('confirmation');
  }

}
