import { Component, Input, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { CadreAdministratifService } from '../modules/services/cadre-administratif.service';
import { UniversiteService } from '../modules/services/universite.service';
import { CadreAdministratif } from '../modules/shared/cadreAdministratif';
import { Universite } from '../modules/shared/universite';
import { flyInOut, expand } from 'animations/app.animation';

@Component({
  selector: 'app-cadre-administratif',
  templateUrl: './cadre-administratif.component.html',
  styleUrls: ['./cadre-administratif.component.scss'],
  host: {
    '[@flyInOut]': 'true',
    'style': 'display: block;'
  },
  animations: [
    flyInOut(),
    expand()
  ]
})
export class CadreAdministratifComponent implements OnInit {

  @Input() universite: Universite;
  idUniversite: string = '';
  loading: boolean = true;
  cadres?: CadreAdministratif[];

  constructor(
    private route: ActivatedRoute,
    private universiteService: UniversiteService,
    private cadreAdministratifService: CadreAdministratifService
  ) { }

  ngOnInit(): void {
    this.idUniversite = this.route.snapshot.params['id'];
    this.universiteService
        .getUniversiteById( this.idUniversite )
        .subscribe( 
          universite => { this.universite = universite },
          error => {  },
          () => this.loading = false
          );
    
    this.cadreAdministratifService
        .getCadreAdministratifByUniversiteId( this.idUniversite )
        .subscribe( cadres => this.cadres = cadres );
  }

}
