import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { TutoralService } from './../modules/services/tutoral.service';
import { Tutoral } from './../modules/shared/tutoral';
import { baseURL } from './../modules/shared/baseurl';
import { MatDialog } from '@angular/material/dialog';
import { DeleteModalTutoralComponent } from './../modules/admin/delete-modal-tutoral/delete-modal-tutoral.component';

@Component({
  selector: 'app-guide-tutoral',
  templateUrl: './guide-tutoral.component.html',
  styleUrls: ['./guide-tutoral.component.scss']
})
export class GuideTutoralComponent implements OnInit {
  type: string = '';
  baseURL: string = baseURL;
  loading: boolean = false;
  tutorals: Tutoral[] = [];
  erroMsg?: any;
  constructor(
    private tutoralService: TutoralService,
    private route: ActivatedRoute,
    private matDiag: MatDialog
  ) { }

  ngOnInit(): void {
    this.loading = true;
    this.tutoralService
        .getAllTutorals()
        .subscribe(
          tutorals => this.tutorals = tutorals,
          error => this.erroMsg = error,
          () => this.loading = false
        );
  }

  deleteTutoral(tutoral: Tutoral) {
    this.matDiag.open( DeleteModalTutoralComponent, {
      data: {
        tutoral: tutoral
      }
    } )
  }
}
