import { Component, Inject, OnChanges, OnInit, ViewChild } from '@angular/core';
import { FormArray, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { FormationService } from '../../services/formation.service';
import { UniversiteService } from '../../services/universite.service';
import { Formation } from '../../shared/formation';
import { Universite } from '../../shared/universite';



@Component({
  selector: 'app-formation',
  templateUrl: './formation.component.html',
  styleUrls: ['./formation.component.scss']
})
export class FormationComponent implements OnInit {
  @ViewChild('fform', { static: false }) formationFormDirective: any;
  formationForm: FormGroup;
  images: FormArray;

  selectedFile: File[] = [];

  formation: Formation;
  loading: boolean = false;
  formErrors: any = {
    nom: '',
    description: ''
  };

  validationMessages: any = {
    titre: {
      required: 'le nom  est requis.',
      minlength: 'le nom tre plus que 4 caractres.',
      maxlength: 'le nom l\'auteur ne doit pas tre plus que 150 caractres.'
    },
    description: {
      required: 'la description est requise.',
      minlength: 'la description doit tre plus que 6 caractres.',
      maxlength: 'la description ne doit pas tre plus que 2000 caractres.'
    }
  };

  compteId?: string;
  msgError?: string;
  universites: Universite[] = [];

  constructor(
    private formBuilder: FormBuilder,
    private router: Router,
    private formationService: FormationService,
    private universiteService: UniversiteService
  ) {
    this.createForm();
  }

  ngOnInit(): void {
    this.universiteService
      .getAllUniversities()
      .subscribe(universites => this.universites = universites)
  }

  createForm() {
    this.formationForm = this.formBuilder.group({
      titre: ['', [Validators.required, Validators.minLength(4), Validators.maxLength(150)]],
      description: ['', [Validators.required, Validators.minLength(4), Validators.maxLength(1000)]],
      universite: 0,
      images: this.formBuilder.array([this.createImage()])
    });
    this.formationForm
      .valueChanges
      .subscribe(data => this.onValueChanged(data));
    this.onValueChanged();
  }

  onValueChanged(data?: any) {
    if (!this.formationForm) { return; }
    const form = this.formationForm;
    for (const field in this.formErrors) {
      if (this.formErrors.hasOwnProperty(field)) {
        // clear previous error message (if any)
        this.formErrors[field] = '';
        const control = form.get(field);
        if (control && control.dirty && !control.valid) {
          const messages = this.validationMessages[field];
          for (const key in control.errors) {
            if (control.errors.hasOwnProperty(key)) {
              this.formErrors[field] += messages[key] + ' ';
            }
          }
        }
      }
    }
  }

  ngOnChanges() {
    this.onSubmit();
  }

  closeModal() {
    this.router.navigateByUrl('reset-password');
  }

  onSubmit() {
    this.formation = this.formationForm?.value;
    this.formation.images = [];

    if (this.selectedFile.length > 0) {
      this.selectedFile.forEach(f => {
        this.formation?.images.push('images/formations/' + f.name)
      })
    }
    if (this.formation != null && this.selectedFile) {
      this.formationService
        .createFormation(this.formation)
        .subscribe(
          offreEtude => {
            this.selectedFile.forEach(fichier => {
              this.formationService
                .telecharger(fichier)
                .subscribe(
                  data => {  },
                  error => { },
                  () => {
                    this.router.navigateByUrl('facultes')
                  }
                )
            });
          },
          err => { this.msgError = err.error.message; },
          () => {
            setTimeout(() => { this.loading = false; }, 2000)
          }
        )

    }

    this.formationFormDirective.resetForm();
  }

  createImage(): FormGroup {
    return this.formBuilder.group({
      fichier: '',
    });
  }

  addImage(): void {
    this.images = this.formationForm.get('images') as FormArray;
    this.images.push(this.createImage());
  }


  fileChanged(event: any) {
    this.selectedFile.push(event.target.files[0]);
  }

  get fichierFormGroups() {
    return this.formationForm.get('images') as FormArray
  }

}
