import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { Actualite } from '../shared/actualite';
import { baseURL } from '../shared/baseurl';

@Injectable({
  providedIn: 'root'
})
export class ActualiteService {

  constructor(private httpClient: HttpClient) { }
  getAllActualite(): Observable<Actualite[]> {
    return this.httpClient.get<Actualite[]>(baseURL + 'actualites');
  }

  getActualiteById(id: string): Observable<Actualite> {
    return this.httpClient.get<Actualite>(baseURL + 'actualites/' + id)
  }

  createActualite(actualite: Actualite): Observable<Actualite> {
    return this.httpClient.post<Actualite>(baseURL + 'actualites', actualite, {});
  }

  updateActualite(id: string, actualite: Actualite): Observable<Actualite> {
    return this.httpClient.put<Actualite>(baseURL + 'actualites/' + id, actualite);
  }
  //Les methodes de cration ici
  telecharger(file: File): Observable<File> {
    let formData = new FormData();
    formData.append('fichier', file);
    console.log(formData);
    return this.httpClient.put<File>(baseURL + "actualites", formData);
  }
  getActualiteByUniversiteId( idUniversite: string ): Observable<Actualite[]>{
    return this.httpClient.get<Actualite[]>( baseURL + 'actualites/universites/' + idUniversite );
  }
  getLastFiveActualite(idUniversite: string): Observable<Actualite[]> {
    return this.httpClient.get<Actualite[]>( baseURL + 'actualites/' + idUniversite + '/fives' );
  }
}
