import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { DistinctionService } from '../modules/services/distinction.service';
import { UniversiteService } from '../modules/services/universite.service';
import { baseURL } from '../modules/shared/baseurl';
import { Distinction } from '../modules/shared/distinction';
import { Universite } from '../modules/shared/universite';
import { flyInOut, expand } from 'animations/app.animation';

@Component({
  selector: 'app-list-distinction',
  templateUrl: './list-distinction.component.html',
  styleUrls: ['./list-distinction.component.scss'],
  host: {
    '[@flyInOut]': 'true',
    'style': 'display: block;'
  },
  animations: [
    flyInOut(),
    expand()
  ]
})
export class ListDistinctionComponent implements OnInit {
  idUniversite: string = '';
  universite: Universite;
  distinctions: Distinction[] = [];
  baseURL: string = baseURL;
  loading: boolean = true;
  constructor(
    private universiteService: UniversiteService,
    private distinctioncheService: DistinctionService,
    private route: ActivatedRoute
  ) { }

  ngOnInit(): void {
    this.idUniversite = this.route.snapshot.params['id'];
    this.universiteService
        .getUniversiteById( this.idUniversite )
        .subscribe( 
          universite => this.universite = universite,
          error => {},
          () => this.loading = false 
        );

    this.distinctioncheService
        .getDistinctionByUniversiteId( this.idUniversite )
        .subscribe( distinctions => this.distinctions = distinctions )
  }

}
