import { Component, OnInit, ViewChild } from '@angular/core';
import { FormGroup, FormArray, FormBuilder } from '@angular/forms';
import { Router } from '@angular/router';
import { TutoralService } from './../../services/tutoral.service';
import { Tutoral } from './../../shared/tutoral';

@Component({
  selector: 'app-ajouter-tutoral',
  templateUrl: './ajouter-tutoral.component.html',
  styleUrls: ['./ajouter-tutoral.component.scss']
})
export class AjouterTutoralComponent implements OnInit {
  @ViewChild('fform', { static: false }) tutoralFormDirective: any;
  tutoralForm: FormGroup;
  tutorals: FormArray;
  msgError: string = '';

  constructor(
    private formBuilder: FormBuilder,
    private tutoralService: TutoralService,
    private router: Router) { }

  ngOnInit() {
    this.tutoralForm = this.formBuilder.group({
      tutorals: this.formBuilder.array([this.createTutoral()])
    });
  }
  get tutoralDetails() {
    return (this.tutoralForm.controls.tutorals as FormGroup).controls;
  }

  get tutoralFormGroups() {
    return this.tutoralForm.get('tutorals') as FormArray
  }

  createTutoral(): FormGroup {
    return this.formBuilder.group({
      titre: '',
      type: '',
      lien: ''
    });
  }

  addItem(): void {
    this.tutorals = this.tutoralForm.get('tutorals') as FormArray;
    this.tutorals.push(this.createTutoral());
  }

  onSubmit() {
    for (let i = 0; i < this.tutoralForm.value.tutorals.length; i++) {
      var valeur = this.tutoralForm.value.tutorals[i].lien.substr(this.tutoralForm.value.tutorals[i].lien.indexOf("=") + 1);
      var autre: string = "";
      if(valeur.indexOf("&")>=0) {
        autre = valeur.substr(0, valeur.indexOf("&"));
        this.tutoralForm.value.tutorals[i].lien = autre;
      } else
        this.tutoralForm.value.tutorals[i].lien = valeur;
    }
    for (let i = 0; i < this.tutoralForm.value.tutorals.length; i++) {
      this.tutoralService
        .createTutoral(
          {
            titre: this.tutoralForm.value.tutorals[i].titre,
            lien: this.tutoralForm.value.tutorals[i].lien,
            type: this.tutoralForm.value.tutorals[i].type
          } as Tutoral
        )
        .subscribe(data =>{
          this.router.navigateByUrl('/guide-tutorial');                         
        });
    }

  }
}
