import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { baseURL } from '../shared/baseurl';
import { Matiere } from '../shared/matiere';

@Injectable({
  providedIn: 'root'
})
export class MatiereService {
  constructor(private httpClient: HttpClient) { }

  getAllMatieres(): Observable<Matiere[]> {
    return this.httpClient.get<Matiere[]>(baseURL + 'matieres');
  }
  getMatiereById(idMatiere: string): Observable<Matiere> {
    return this.httpClient.get<Matiere>(baseURL + 'matieres/' + idMatiere);
  }

  createMatiere(Matiere: Matiere): Observable<Matiere> {
    return this.httpClient.post<Matiere>(baseURL + 'matieres', Matiere);
  }

  updateMatiere(idMatiere: string, Matiere: Matiere): Observable<Matiere> {
    return this.httpClient.put<Matiere>(baseURL + 'matieres/' + idMatiere, Matiere)
  }
  getMatiereByClasseId(idClasse: string): Observable<Matiere[]> {
    return this.httpClient.get<Matiere[]>(baseURL + 'matieres/classes/' + idClasse);
  }

  deleteMatiere(idMatiere: string): Observable<Matiere> {
    return this.httpClient.delete<Matiere>(baseURL + 'matieres/' + idMatiere);
  }
}
