import { Component, OnInit } from '@angular/core';
import { UniversiteService } from '../../services/universite.service';
import { Universite } from '../../shared/universite';

@Component({
  selector: 'app-faculte',
  templateUrl: './faculte.component.html',
  styleUrls: ['./faculte.component.scss']
})
export class FaculteComponent implements OnInit {

  facultes?: Universite[];
  loading: boolean = false;

  constructor(private universiteService: UniversiteService) { }

  ngOnInit(): void {
    this.getFacultes('faculte');
  }

  getFacultes(type: string) {
    this.loading = true;
    this.universiteService
      .getUniversityByType(type)
      .subscribe(
        universites => { this.facultes = universites },
        error => { },
        () => {
          this.loading = false;
        }
      );
  }


}
