import { Component, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { ClasseService } from '../../services/classe.service';
import { SujetEtCorrectionService } from '../../services/sujet-et-correction.service';
import { Classe } from '../../shared/classe';
import { SujetEtCorrection } from '../../shared/sujetEtCorrection';

@Component({
  selector: 'app-create-sujet-et-correction',
  templateUrl: './create-sujet-et-correction.component.html',
  styleUrls: ['./create-sujet-et-correction.component.scss']
})
export class CreateSujetEtCorrectionComponent implements OnInit {
  @ViewChild('fform', { static: false }) sujetEtCorrectionFormDirective: any;
  sujetEtCorrectionForm: FormGroup;

  selectedFileSujet: File;
  selectedFileCorrection: File;

  sujetEtCorrection: SujetEtCorrection;
  loading: boolean = false;
  formErrors: any = {
    titre: '',
    sujet: '',
    correction: '',
    annee: '',
    semestre: '',
    classe: ''
  };

  validationMessages: any = {
    titre: {
      required: 'le titre  est requis.',
      minlength: 'le titre tre plus que 4 caractres.',
      maxlength: 'le titre l\'auteur ne doit pas tre plus que 150 caractres.'
    },
    annee: {
      required: 'l\'anne universitaire est requise.',
      minlength: 'l\'anne universitaire doit tre plus que 4 caractres.',
      maxlength: 'l\'anne universitaire ne doit pas tre plus que 150 caractres.'
    },
    semestre: {
      required: 'le semestre est requise.',
      minlength: 'le semestre doit tre plus que 4 caractres.',
      maxlength: 'le semestre ne doit pas tre plus que 150 caractres.'
    },
    sujet: {
      required: 'le sujet de la societ est requis.',
      minlength: 'le sujet de la societ que 4 caractres.',
      maxlength: 'le shjet de la societ ne doit pas tre plus que 150 caractres.'
    },
    correction: {
      required: 'le correction de la societ est requis.',
      minlength: 'le correction de la societ que 4 caractres.',
      maxlength: 'le correction de la societ ne doit pas tre plus que 150 caractres.'
    },
    classe: {
      required: 'le titre  est requis.'
    },
  };

  compteId?: string;
  msgError?: string;
  classes: Classe[] = [];

  constructor(
    private formBuilder: FormBuilder,
    private router: Router,
    private sujetEtCorrectionService: SujetEtCorrectionService,
    private classeService: ClasseService
  ) {
    this.createForm();
  }

  ngOnInit(): void {
    this.classeService
      .getAllClasses()
      .subscribe( classes => this.classes = classes)
  }

  createForm() {
    this.sujetEtCorrectionForm = this.formBuilder.group({
      titre: ['', [Validators.required, Validators.minLength(4), Validators.maxLength(150)]],
      sujet: ['', [Validators.required, Validators.minLength(4), Validators.maxLength(150)]],
      annee: ['', [Validators.required, Validators.minLength(4), Validators.maxLength(150)]],
      semestre: ['', [Validators.required, Validators.minLength(4), Validators.maxLength(150)]],
      correction: ['', [Validators.required, Validators.minLength(4), Validators.maxLength(150)]],
      classe: ['', [Validators.required]]
    });
    this.sujetEtCorrectionForm
      .valueChanges
      .subscribe(data => this.onValueChanged(data));
    this.onValueChanged();
  }

  onValueChanged(data?: any) {
    if (!this.sujetEtCorrectionForm) { return; }
    const form = this.sujetEtCorrectionForm;
    for (const field in this.formErrors) {
      if (this.formErrors.hasOwnProperty(field)) {
        // clear previous error message (if any)
        this.formErrors[field] = '';
        const control = form.get(field);
        if (control && control.dirty && !control.valid) {
          const messages = this.validationMessages[field];
          for (const key in control.errors) {
            if (control.errors.hasOwnProperty(key)) {
              this.formErrors[field] += messages[key] + ' ';
            }
          }
        }
      }
    }
  }

  ngOnChanges() {
    this.onSubmit();
  }

  closeModal() {
    this.router.navigateByUrl('reset-password');
  }
  onSubmit() {
    this.sujetEtCorrection = this.sujetEtCorrectionForm?.value;
    this.sujetEtCorrection.sujet = 'images/corrections/' + this.selectedFileSujet.name
    this.sujetEtCorrection.correction = 'images/corrections/' + this.selectedFileCorrection.name;
    this.sujetEtCorrectionService
        .telechargerCorrection(this.selectedFileCorrection)
        .subscribe( file => {
          this.sujetEtCorrectionService
              .telechargerSujet( this.selectedFileSujet )
              .subscribe( f => {});

          this.sujetEtCorrectionService
              .createSujetEtCorrection(this.sujetEtCorrection)
              .subscribe(
                offre=> {},
                err => { this.msgError = err.error.message; },
                () => {
                  setTimeout(() => { this.loading = false; }, 2000);
                  location.reload()
                }
              )
        } )
    this.sujetEtCorrectionFormDirective.resetForm();
  }
  fileChangedSujet(event: any) {
    this.selectedFileSujet = event.target.files[0];
  }
  fileChangedCorrection(event: any) {
    this.selectedFileCorrection = event.target.files[0];
  }
}
