import { Component, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { Universite } from '../../shared/universite';
import { Observable } from 'rxjs';
import { UniversiteService } from '../../services/universite.service';
import { Departement } from '../../shared/departement';
import { DepartementService } from '../../services/departement.service';

@Component({
  selector: 'app-ajouter-departement',
  templateUrl: './ajouter-departement.component.html',
  styleUrls: ['./ajouter-departement.component.scss']
})
export class AjouterDepartementComponent implements OnInit {


  @ViewChild('fform', { static: false }) compteFormDirective: any;
  departementForm: FormGroup;

  universite: Universite;
  departement: Departement;
  loading: boolean = false;
  idUniversite: string = '';

  formErrors: any = {
    nom: '',
    description: ''
  };

  validationMessages: any = {
    nom: {
      required: 'le nom  est requis.',
      minlength: 'le nom tre plus que 4 caractres.',
      maxlength: 'le nom  ne doit pas tre plus que 150 caractres.'
    },
    description: {
      required: 'la description est requise.',
      minlength: 'la description tre plus que 6 caractres.',
      maxlength: 'la description ne doit pas tre plus que 1000 caractres.'
    }
  };


  constructor(
    private formBuilder: FormBuilder,
    private router: Router,
    private universiteService: UniversiteService,
    private route: ActivatedRoute,
    private departementService: DepartementService
  ) {
    this.createForm();
  }

  ngOnInit(): void {
    this.idUniversite = this.route.snapshot.params['id'];
    this.universiteService
      .getUniversiteById(this.idUniversite)
      .subscribe(universite => this.universite = universite);
  }

  createForm() {
    this.departementForm = this.formBuilder.group({
      nom: ['', [Validators.required, Validators.minLength(4), Validators.maxLength(150)]],
      description: ['', [Validators.required, Validators.minLength(4), Validators.maxLength(1000)]]
    });
    this.departementForm
      .valueChanges
      .subscribe(data => this.onValueChanged(data));
    this.onValueChanged();
  }

  onValueChanged(data?: any) {
    if (!this.departementForm) { return; }
    const form = this.departementForm;
    for (const field in this.formErrors) {
      if (this.formErrors.hasOwnProperty(field)) {
        // clear previous error message (if any)
        this.formErrors[field] = '';
        const control = form.get(field);
        if (control && control.dirty && !control.valid) {
          const messages = this.validationMessages[field];
          for (const key in control.errors) {
            if (control.errors.hasOwnProperty(key)) {
              this.formErrors[field] += messages[key] + ' ';
            }
          }
        }
      }
    }
  }

  ngOnChanges() {
    this.idUniversite = this.route.snapshot.params['id'];
  }

  onSubmit() {
    this.departement = this.departementForm?.value;
    this.departement.universite = this.universite;
    this.loading = true;

    this.departementService
      .createDepartement(this.departement)
      .subscribe(departement => {
        this.universite.departements.push(departement);
        this.universiteService
            .updateUniversite( this.idUniversite, this.universite )
            .subscribe( univ => {} );
      },
        err => { },
        () => {
            this.loading = false; 
            this.router.navigateByUrl('unversites-detail/' + this.idUniversite);
        }
      )
  }


  reloadPage() {
    setTimeout(() => {
      window.location.reload();
    }, 100);
  }

}
