import { Component, Inject, OnInit } from '@angular/core';
import { Dossier } from './../../shared/dossier';
import { Etudiant } from './../../shared/etudiant';
import { DossierService } from './../../services/dossier.service';
import { EtudiantService } from './../../services/etudiant.service';
import { ActivatedRoute, Router } from '@angular/router';
import { MatDialog } from '@angular/material/dialog';
import { baseURL } from './../../shared/baseurl';
import { VerifierDossierModalComponent } from './../../../verifier-dossier-modal/verifier-dossier-modal.component';
import { ModalAcceptationDossierComponent } from './../modal-acceptation-dossier/modal-acceptation-dossier.component';
import { Classe } from './../../shared/classe';

@Component({
  selector: 'app-valider-dossier-etudiant',
  templateUrl: './valider-dossier-etudiant.component.html',
  styleUrls: ['./valider-dossier-etudiant.component.scss']
})
export class ValiderDossierEtudiantComponent implements OnInit {
  loading: boolean = false;
  dossier: Dossier;
  etudiant: Etudiant;
  idDossier?: string;
  errorDossier?: any;
  baseURL: string = baseURL;

  constructor(
    private dossierService: DossierService,
    private etudiantService: EtudiantService,
    private route: ActivatedRoute,
    private router: Router,
    private dialog: MatDialog
  ) { }

  ngOnInit(): void {
    this.loading = true;
    this.idDossier = this.route.snapshot.params['id'];
    this.dossierService
        .getDossierById( this.idDossier )
        .subscribe(
          dossier => {
            this.dossier = dossier;
            this.etudiantService
                .getEtudiantByCompteId( this.dossier.compte._id )
                .subscribe(
                  etudiant => this.etudiant = etudiant,
                  error => this.errorDossier = error,
                  () => this.loading = false
                )
          },
          error => this.errorDossier = error,
          () => this.loading = false
        );
    
  }

  openModalDossier(fichier?: string) {
    this.dialog.open( VerifierDossierModalComponent, {
      position: {
        top: '5%'
      },
      data: {
        fichier: baseURL + fichier
      }
    } )
  }

  openModalAcceptation() {
    this.dialog.open( ModalAcceptationDossierComponent, {
      position: {
        top: '5%'
      },
      data: {
        etudiant: this.etudiant,
        dossier: this.dossier
      }
    } )
  }

  refuser(){
    this.loading = true;
    this.dossier.valide = false;
    this.etudiant.classe = {_id: ''} as Classe;

    this.dossierService
        .updateDossier( this.dossier._id, this.dossier )
        .subscribe(
          dossier => {},
          error => this.errorDossier = error,
          () => this.loading = false
        );
    this.etudiantService
        .updateEtudiant( this.etudiant._id, this.etudiant )
        .subscribe( 
          etudiant => {},
          error => this.errorDossier = error,
          () => this.loading = false
         );

    if(this.errorDossier == null) {
      this.router.navigateByUrl( 'etudiant-accepter-par-classe/' +  this.dossier.classe._id);
    }
  }
}
