import { Component, Inject, OnInit } from '@angular/core';
import { MatDialog, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { SectionService } from '../../services/section.service';
import { Section } from '../../shared/section';

@Component({
  selector: 'app-supprimer-section',
  templateUrl: './supprimer-section.component.html',
  styleUrls: ['./supprimer-section.component.scss']
})
export class SupprimerSectionComponent implements OnInit {

  idSection: string = '';
  section?: Section;
  loading: boolean = false;
  constructor(
    @Inject(MAT_DIALOG_DATA) private data: any,
    private sectionService: SectionService,
    private dialog: MatDialog,
    private router: Router
  ) { }

  ngOnInit(): void {
    this.loading = true;
    this.idSection = this.data.id;
    this.sectionService
      .getSectionById(this.idSection)
      .subscribe(
        section => this.section = section,
        error => { },
        () => {
          this.loading = false;
        }
      )
  }

  closeModal() {
    this.dialog.closeAll();
  }

  supprimerSection() {
    this.sectionService
      .deleteSection(this.idSection)
      .subscribe(section => {
        this.router.navigateByUrl('/facultes');
        this.closeModal();
      })
  }

}
