import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { baseURL } from '../modules/shared/baseurl';
import { flyInOut, expand } from 'animations/app.animation';
import { Tutoral } from './../modules/shared/tutoral';
import { TutoralService } from './../modules/services/tutoral.service';

@Component({
  selector: 'app-list-tutorial',
  templateUrl: './list-tutorial.component.html',
  styleUrls: ['./list-tutorial.component.scss'],
  host: {
    '[@flyInOut]': 'true',
    'style': 'display: block;'
  },
  animations: [
    flyInOut(),
    expand()
  ]
})
export class ListTutorialComponent implements OnInit {
  type: string = '';
  baseURL: string = baseURL;
  loading: boolean = false;
  tutorals: Tutoral[] = [];
  erroMsg?: any;
  constructor(
    private tutoralService: TutoralService,
    private route: ActivatedRoute
  ) { }

  ngOnInit(): void {
    this.loading = true;
    this.type = this.route.snapshot.params['type'];
    this.tutoralService
        .getTutoralByType( this.type )
        .subscribe(
          tutorals => this.tutorals = tutorals,
          error => this.erroMsg = error,
          () => this.loading = false
        );
  }
}
