import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { baseURL } from '../shared/baseurl';
import { Parcours } from '../shared/parcours';

@Injectable({
  providedIn: 'root'
})
export class ParcoursService {

  constructor(private httpClient: HttpClient) { }

  getAllParcours(): Observable<Parcours[]> {
    return this.httpClient.get<Parcours[]>(baseURL + 'parcours');
  }
  getParcoursById(idParcours: string): Observable<Parcours> {
    return this.httpClient.get<Parcours>(baseURL + 'parcours/' + idParcours);
  }

  createParcours(parcours: Parcours): Observable<Parcours> {
    return this.httpClient.post<Parcours>(baseURL + 'parcours', parcours);
  }

  updateParcours(idParcours: string, parcours: Parcours): Observable<Parcours> {
    return this.httpClient.put<Parcours>(baseURL + 'parcours/' + idParcours, parcours)
  }
  getParcoursByDepartementId(idDepartement: string): Observable<Parcours[]> {
    return this.httpClient.get<Parcours[]>(baseURL + 'parcours/departements/' + idDepartement);
  }

  deleteParcours(idParcours?: string): Observable<Parcours> {
    return this.httpClient.delete<Parcours>(baseURL + 'parcours/' + idParcours);
  }
}
