import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { RegisterComponent } from './register/register.component';
import { UpdateAvatarComponent } from './update-avatar/update-avatar.component';
import { UpdateComponent } from './update/update.component';


const routes: Routes = [
    { path: 'register-etudiant', component: RegisterComponent },
    { path: 'update-etudiant/:id', component: UpdateComponent },
    { path: 'update-avatar', component: UpdateAvatarComponent }
];

@NgModule({
    imports: [
        RouterModule.forChild(routes)
    ],
    exports: [RouterModule]
})
export class EtudiantRoutingModule { }
