import { Component, OnInit, ViewChild } from '@angular/core';
import { FormArray, FormBuilder, FormGroup } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { SemestreService } from '../../services/semestre.service';
import { ClasseService } from '../../services/classe.service';
import { Semestre } from '../../shared/semestre';
import { Classe } from '../../shared/classe';

@Component({
  selector: 'app-ajouter-semestre',
  templateUrl: './ajouter-semestre.component.html',
  styleUrls: ['./ajouter-semestre.component.scss']
})
export class AjouterSemestreComponent implements OnInit {
@ViewChild('fform', { static: false }) semestreFormDirective: any;
  semestreForm: FormGroup;
  items: FormArray;
  idClasse: string = "";
  semestre?: Semestre;
  classe: Classe;
  semestres: Semestre[] = [];

  constructor(
    private formBuilder: FormBuilder,
    private semestreService: SemestreService,
    private classeService: ClasseService,
    private route: ActivatedRoute,
    private router: Router
  ) { }

  ngOnInit() {
    this.idClasse = this.route.snapshot.params['id'];
    this.semestreService
        .getSemestreByClasseId( this.idClasse )
        .subscribe( semestres => this.semestres = semestres )

    this.classeService
      .getClasseById(this.idClasse)
      .subscribe(classe => {
        this.classe = classe;
      })

    this.semestreForm = this.formBuilder.group({
      items: this.formBuilder.array([this.createItem()])
    });
  }
  get semestreDetails() {
    return (this.semestreForm.controls.items as FormGroup).controls;
  }

  get semestreFormGroups() {
    return this.semestreForm.get('items') as FormArray
  }

  createItem(): FormGroup {
    return this.formBuilder.group({
      nom: '',
      annee: ''
    });
  }

  addItem(): void {
    this.items = this.semestreForm.get('items') as FormArray;
    this.items.push(this.createItem());
  }

  onSubmit() {
        for (let i = 0; i < this.semestreForm.value.items.length; i++) {
          this.semestreService
            .createSemestre(
              {
                nom: this.semestreForm.value.items[i].nom,
                annee: this.semestreForm.value.items[i].annee
              } as Semestre
            )
            .subscribe(semestre => {
              this.classe.semestres.push( semestre );
              this.classeService
                  .updateClasse( this.classe._id,  this.classe)
                  .subscribe( classe => {
                    this.router.navigateByUrl( '/classe-detail/' + this.classe._id );
              });
            });
        }

  }

  verife( semestre: Semestre, classe: Classe ): boolean {
    let estContenu: boolean = false;
    classe.semestres.forEach( s => {
      if( s._id === semestre._id ) {
        estContenu = true;
      }
    } )
    return estContenu;
  }
}