import { Component, Inject, OnInit } from '@angular/core';
import { Dossier } from './../../shared/dossier';
import { Etudiant } from './../../shared/etudiant';
import { EtudiantService } from './../../services/etudiant.service';
import { DossierService } from './../../services/dossier.service';
import { MAT_DIALOG_DATA, MatDialog } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { Classe } from './../../shared/classe';

@Component({
  selector: 'app-modal-acceptation-dossier',
  templateUrl: './modal-acceptation-dossier.component.html',
  styleUrls: ['./modal-acceptation-dossier.component.scss']
})
export class ModalAcceptationDossierComponent implements OnInit {

  loading: boolean = false;
  dossier: Dossier;
  etudiant: Etudiant;
  errorDossier?: any;

  constructor(
    private etudiantService: EtudiantService,
    private dossierService: DossierService,
    private router: Router,
    private matDiag: MatDialog,
    @Inject( MAT_DIALOG_DATA ) public data: any
  ) { }

  ngOnInit(): void {
    this.etudiant = this.data.etudiant;
    this.dossier = this.data.dossier;
  }
  
  validerDossier() {
    this.loading = true;
    this.dossier.valide = true;
    this.etudiant.classe = {_id: this.dossier.classe._id} as Classe;

    this.dossierService
        .updateDossier( this.dossier._id, this.dossier )
        .subscribe(
          dossier => {},
          error => this.errorDossier = error,
          () => this.loading = false
        );
    this.etudiantService
        .updateEtudiant( this.etudiant._id, this.etudiant )
        .subscribe( 
          etudiant => {},
          error => this.errorDossier = error,
          () => this.loading = false
         );

    if(this.errorDossier == null) {
      this.matDiag.closeAll();
      this.router.navigateByUrl( 'etudiant-accepter-par-classe/' +  this.dossier.classe._id);
    }
  }

  closeModal() {
    this.matDiag.closeAll();
  }
}
