import { Component, Inject, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Observable } from 'rxjs';
import { ClasseService } from '../modules/services/classe.service';
import { DepartementService } from '../modules/services/departement.service';
import { FiliereService } from '../modules/services/filiere.service';
import { MatiereService } from '../modules/services/matiere.service';
import { ParcoursService } from '../modules/services/parcours.service';
import { UniversiteService } from '../modules/services/universite.service';
import { Classe } from '../modules/shared/classe';
import { Departement } from '../modules/shared/departement';
import { Matiere } from '../modules/shared/matiere';
import { Universite } from '../modules/shared/universite';
import { Etudiant } from './../modules/shared/etudiant';
import { EtudiantService } from './../modules/services/etudiant.service';
import { MatDialog } from '@angular/material/dialog';
import { ModalInfoComponent } from './../modal-info/modal-info.component';
import { baseURL } from './../modules/shared/baseurl';
import { flyInOut, expand } from 'animations/app.animation';

@Component({
  selector: 'app-universite-detail',
  templateUrl: './universite-detail.component.html',
  styleUrls: ['./universite-detail.component.scss'],
  host: {
    '[@flyInOut]': 'true',
    'style': 'display: block;'
  },
  animations: [
    flyInOut(),
    expand()
  ]
})
export class UniversiteDetailComponent implements OnInit {
  niveau: any = {
    licence: [],
    mastere: [],
    doctorat: []
  };
  classes: any[] = [];
  matieres: any = {};
  semestres: any = {};
  classeSelectded?: any;
  semaine?: string;
  departementSeelected?: any;
  matiereSelected?: any;

  listParcours: any = {};
  listMatieres: any = {};
  listClasses: any = {};
  classeParDepartement: any = {};

  panelOpenState = false;
  idUniversite: string = "";
  universite: Universite;
  sms:any = [];
  departements: Departement[] = [];
  loading: boolean = false;
  etudiant: Etudiant;
  compteId?: string;
  baseURL:string = baseURL;
  constructor(
    private route: ActivatedRoute,
    private universiteService: UniversiteService,
    private departementService: DepartementService,
    private classeService: ClasseService,
    private parcoursService: ParcoursService,
    private matiereService: MatiereService,
    private etudiantService: EtudiantService,
    private router: Router,
    private matDiag: MatDialog
  ) {

  }


  ngOnInit(): void {
    this.loading = true;
    this.idUniversite = this.route.snapshot.params['id'];
    this.compteId = localStorage.getItem('_id')?.toString();
    this.universiteService
      .getUniversiteById(this.idUniversite)
      .subscribe(
          universite => {this.universite = universite},
          error => {},
          () => {this.loading = false;}
        )
    localStorage.setItem('universite', this.idUniversite);
    console.log('78954: ' + this.idUniversite);

    this.departementService
        .getDepartementByUniversiteById( this.idUniversite )
        .subscribe( departements => {
            this.departements = departements;
                for(let i=0; i < this.departements.length; i++) {
                  this.parcoursService
                      .getParcoursByDepartementId( departements[i]._id )
                      .subscribe( parcours => {
                        this.listParcours[departements[i].nom] =  parcours;
                        this.listClasses[this.universite.nom] = [];
                        for( let j=0; j < this.listParcours[departements[i].nom].length; j++ ) {
                          this.classeService
                              .getClasseByParcoursId( this.listParcours[departements[i].nom][j]._id  )
                              .subscribe( classe => {
                                console.log('CLASSE:::::::::', classe)
                                this.listClasses[this.universite.nom][departements[i].nom] = parcours;
                                this.listClasses[this.universite.nom][departements[i].nom][this.listParcours[departements[i].nom][j].nom] = classe
                                 this.classeParDepartement = this.listClasses[this.universite.nom][departements[i].nom][this.listParcours[departements[i].nom][j].nom];
                                 //Initialisation des matires
                                 this.matieres[this.universite.nom] = [];
                                 this.matieres[this.universite.nom][departements[i].nom] = this.departements[i];
                                 this.matieres[this.universite.nom][departements[i].nom][this.listParcours[departements[i].nom][j].nom] = parcours[j];
                                 // Initialisation des semestres
                                 this.semestres[this.universite.nom] = [];
                                 this.semestres[this.universite.nom][departements[i].nom] = departements[i];
                                 this.semestres[this.universite.nom][departements[i].nom][this.listParcours[departements[i].nom][j].nom] = parcours[j];
                                 for(let k = 0; k < this.classeParDepartement.length; k++){
                                  this.matieres[this.universite.nom][departements[i].nom][this.listParcours[departements[i].nom][j].nom][this.classeParDepartement[k].nom] = departements[i];
                                  this.matiereService
                                      .getMatiereByClasseId(this.classeParDepartement[k]._id)
                                      .subscribe(matieres => {
                                        this.matieres[this.universite.nom][departements[i].nom][this.listParcours[departements[i].nom][j].nom][this.classeParDepartement[k].nom] = matieres;
                                        this.semestres[this.universite.nom][departements[i].nom][this.listParcours[departements[i].nom][j].nom][this.classeParDepartement[k].nom] = new Set();
                                        matieres.forEach((mm: any) => {
                                          this.semestres[this.universite.nom][departements[i].nom][this.listParcours[departements[i].nom][j].nom][this.classeParDepartement[k].nom].add(mm);
                                        });
                                       
                                      })                                   
                                }
                               
                              } )
                        }
                      })
            }
        });
        if( this.compteId != undefined ) {
          this.etudiantService
          .getEtudiantByCompteId( this.compteId )
          .subscribe( etudiant => {this.etudiant = etudiant;console.log(etudiant);} );
       }
 
  }

  getClasseByFiliereIdAndNiveau(idFiliere: string, niveau: string): Observable<Classe[]> {
    return this.classeService
      .getClasseByFiliereIdAndNiveau(idFiliere, niveau);
  }

  selectionSemaine(departement: any, classe: any, semaine: string, matiere: any) {
    this.departementSeelected = departement;
    this.classeSelectded = classe;
    this.semaine = semaine;
    this.matiereSelected = matiere;
    this.panelOpenState = false;
    console.log('LABY: ', this.classeSelectded, this.semaine);
  }

  selectionParcours(classe: any, semaine: string) {
    //this.parcoursSelected = this.parcours[i];
  }

  goToCourse(matiere: Matiere){
    this.router.navigateByUrl('cours/'  + matiere._id);
  }

  lancerModalInfo(titre: string) {
    this.matDiag.open( ModalInfoComponent, {
      data: {
        info: 'Vous devez tre inscris et admis  ' + titre + ', pour pouvoir suivre les cours'
      }
    } )
  }
}
