import { Component, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatDialog } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { CompteService } from '../../services/compte.service';
import { EnseignantService } from '../../services/enseignant.service';
import { Compte } from '../../shared/compte';
import { Enseignant } from '../../shared/enseignant';
import { UniversiteService } from './../../services/universite.service';
import { Universite } from './../../shared/universite';
import { Departement } from './../../shared/departement';

@Component({
  selector: 'app-enregister-ensignant',
  templateUrl: './enregister-ensignant.component.html',
  styleUrls: ['./enregister-ensignant.component.scss']
})
export class EnregisterEnsignantComponent implements OnInit {

  @ViewChild('fform', { static: false }) compteFormDirective: any;
  compteForm: FormGroup;
  universites: Universite[] = [];

  compte?: any;

  formErrors: any = {
    username: '',
    password: '',
    confirmation: '',
    prenom: '',
    nom: '',
    email: '',
    telephone: '',
    adresse: '',
    ville: '',
    pays: '',
    specialite: '',
    status: '',
    matricule: '',
    departement: ''
  };

  validationMessages: any = {
    username: {
      required: 'le username  est requis.',
      minlength: 'le username tre plus que 4 caractres.'
    },
    password: {
      required: 'le mot de passe est requis.',
      minlength: 'le mot de passe tre plus que 4 caractres.',
    },
    confirmation: {
      required: 'la confirmation du mot de passe est requis.',
      minlength: 'la confirmation du mot de passe doit tre plus que 4 caractres.'
    },
    prenom: {
      required: 'le prnom est requis.',
      minlength: 'le prnom doit tre plus que 4 caractres.'
    },
    nom: {
      required: 'le nom est requis.',
      minlength: 'le nom  doit tre plus que 4 caractres.'
    },
    adresse: {
      required: 'l\'adresse est requise.',
      minlength: 'l\'adresse doit tre plus que 4 caractres.'
    },
    email: {
      required: 'l\'e-mail est requis.',
      minlength: 'l\'e-mail doit tre plus que 4 caractres.'
    },
    ville: {
      required: 'le nom de la ville est requise',
      minlength: 'le nom de la ville doit tre plus que 4 caractres.'
    },
    pays: {
      required: 'le nom du pays est requis.',
      minlength: 'le nom du pays doit tre plus que 4 caractres.'
    },
    telephone: {
      required: 'le numro du tlphone est requis.',
      minlength: 'le numro du tlphone doit tre plus que 4 caractres.'
    },
    specialite: {
      required: 'la spcialit est requise.',
      minlength: 'la spcialit doit tre plus que 4 caractres.'
    },
    status: {
      required: 'le status est requis.',
      minlength: 'le status doit tre plus que 4 caractres.'
    },
    matricule: {
      required: 'le matricule est requis.',
      minlength: 'le matricule doit tre plus que 4 caractres.'
    },
    departement: {
      required: 'le departement est requis.'
    }
  };

  constructor(
    private formBuilder: FormBuilder,
    private router: Router,
    private compteService: CompteService,
    private enseignantService: EnseignantService,
    private universiteService: UniversiteService,
    private matDialog: MatDialog
  ) {
    this.createForm();
  }

  ngOnInit(): void {
    this.universiteService
        .getAllUniversities()
        .subscribe( universites => this.universites = universites );
  }

  createForm() {
    this.compteForm = this.formBuilder.group({
      username: ['', [Validators.required, Validators.minLength(4)]],
      password: ['', [Validators.required, Validators.minLength(4)]],
      confirmation: ['', [Validators.required, Validators.minLength(4)]],
      prenom: ['', [Validators.required, Validators.minLength(4)]],
      nom: ['', [Validators.required, Validators.minLength(4)]],
      email: ['', [Validators.required, Validators.minLength(4), Validators.email]],
      adresse: ['', [Validators.required, Validators.minLength(4)]],
      telephone: ['', [Validators.required, Validators.minLength(4)]],
      ville: ['', [Validators.required, Validators.minLength(4)]],
      pays: ['', [Validators.required, Validators.minLength(4)]],
      specialite: ['', [Validators.required, Validators.minLength(4)]],
      status: ['', [Validators.required, Validators.minLength(4)]],
      matricule: ['', [Validators.required, Validators.minLength(4)]],
      departement: ['', Validators.required ]
    });
    this.compteForm
      .valueChanges
      .subscribe(data => this.onValueChanged(data));
    this.onValueChanged();
  }

  onValueChanged(data?: any) {
    if (!this.compteForm) { return; }
    const form = this.compteForm;
    for (const field in this.formErrors) {
      if (this.formErrors.hasOwnProperty(field)) {
        // clear previous error message (if any)
        this.formErrors[field] = '';
        const control = form.get(field);
        if (control && control.dirty && !control.valid) {
          const messages = this.validationMessages[field];
          for (const key in control.errors) {
            if (control.errors.hasOwnProperty(key)) {
              this.formErrors[field] += messages[key] + ' ';
            }
          }
        }
      }
    }
  }

  onSubmit() {
    this.compte = this.compteForm?.value;
    this.compte.roles=[];
    this.compte.roles.push('ENSEIGNANT');
    this.compteService
        .createCompte({
          username: this.compte.username,
          password: this.compte.password,
          roles: this.compte.roles
        } as Compte)
        .subscribe(compte => {
          if (compte != null) {
            this.enseignantService
              .createEnseignant({
                prenom: this.compte.prenom,
                nom: this.compte.nom,
                email: this.compte.email,
                ville: this.compte.ville,
                pays: this.compte.pays,
                adresse: this.compte.adresse,
                telephone: this.compte.telephone,
                matricule: this.compte.matricule,
                status: this.compte.status,
                specialite: this.compte.specialite,
                compte: compte,
                departement: {_id: this.compte.departement}
              } as Enseignant)
              .subscribe(et => {
                alert(et.nom + ', vous venez de vous inscrire !');
                this.matDialog.closeAll();
                this.router.navigateByUrl('/login');
            })
        }
      })
    this.compteFormDirective.resetForm();

  }

}
