import { Component, Inject, OnInit } from '@angular/core';
import { MatDialog, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { ChapitreService } from '../../services/chapitre.service';
import { SectionService } from '../../services/section.service';
import { Chapitre } from '../../shared/chapitre';
import { Section } from '../../shared/section';

@Component({
  selector: 'app-supprimer-chapitre',
  templateUrl: './supprimer-chapitre.component.html',
  styleUrls: ['./supprimer-chapitre.component.scss']
})
export class SupprimerChapitreComponent implements OnInit {

  idChapitre: string = '';
  chapitre?: Chapitre;
  sections?: Section[];
  loading: boolean = false;

  constructor(
    private sectionService: SectionService,
    private chapitreService: ChapitreService,
    private dialog: MatDialog,
    @Inject(MAT_DIALOG_DATA) private data: any
  ) { }

  ngOnInit(): void {
    this.idChapitre = this.data.id;
    this.chapitreService
      .getChapitreById(this.idChapitre)
      .subscribe(data => this.chapitre = data)
  }

  supprimerChapitre() {
    this.loading = true;
    this.sectionService
      .getSectionByChapitreId(this.idChapitre)
      .subscribe(sections => {
        this.sections = sections;
        if (this.sections != null) {
          this.sections
            .forEach(section => {
              this.sectionService
                .deleteSection(section._id)
                .subscribe(data => {})
            })
        }

      });

    this.chapitreService
      .deleteChapitre(this.idChapitre)
      .subscribe(
        chapitre => { },
        error => { },
        () => { this.loading = false; location.reload() }
      )
  }

  closeModal() {
    this.dialog.closeAll();
  }

}
