import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { baseURL } from '../shared/baseurl';
import { Filiere } from '../shared/filiere';

@Injectable({
  providedIn: 'root'
})
export class FiliereService {

  constructor(private httpClient: HttpClient) { }

  getAllFilieres(): Observable<Filiere[]> {
    return this.httpClient.get<Filiere[]>(baseURL + 'filieres');
  }
  getFiliereById(idFiliere: string): Observable<Filiere> {
    return this.httpClient.get<Filiere>(baseURL + 'filieres/' + idFiliere);
  }

  createFiliere(filiere: Filiere): Observable<Filiere> {
    return this.httpClient.post<Filiere>(baseURL + 'filieres', filiere);
  }

  updateFiliere(idFiliere: string, filiere: Filiere): Observable<Filiere> {
    return this.httpClient.put<Filiere>(baseURL + 'filieres/' + idFiliere, filiere)
  }
  getFiliereByDepartementId(idDepartement: string): Observable<Filiere[]> {
    return this.httpClient.get<Filiere[]>(baseURL + 'filieres/departements/' + idDepartement);
  }

  deleteFiliere(idFiliere: string): Observable<Filiere> {
    return this.httpClient.delete<Filiere>(baseURL + 'filieres/' + idFiliere);
  }
}
