import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ActualiteService } from '../modules/services/actualite.service';
import { UniversiteService } from '../modules/services/universite.service';
import { Actualite } from '../modules/shared/actualite';
import { baseURL } from '../modules/shared/baseurl';
import { Universite } from '../modules/shared/universite';
import { flyInOut, expand } from 'animations/app.animation';

@Component({
  selector: 'app-list-actualite',
  templateUrl: './list-actualite.component.html',
  styleUrls: ['./list-actualite.component.scss'],
  host: {
    '[@flyInOut]': 'true',
    'style': 'display: block;'
  },
  animations: [
    flyInOut(),
    expand()
  ]
})
export class ListActualiteComponent implements OnInit {

  idUniversite: string = '';
  universite?: Universite;
  actualites: Actualite[] = [];
  baseURL: string = baseURL;
  loading: boolean = true;
  constructor(
    private route: ActivatedRoute,
    private universiteService: UniversiteService,
    private actualiteService: ActualiteService
  ) { }

  ngOnInit(): void {
    this.idUniversite = this.route.snapshot.params['id'];
    this.actualiteService
        .getActualiteByUniversiteId( this.idUniversite )
        .subscribe( actualites => {
            this.actualites = actualites
        })
    this.idUniversite = localStorage.getItem('universite')?.toString() as string;
    this.universiteService
    .getUniversiteById( this.idUniversite )
    .subscribe( 
      universite => this.universite = universite,
      error => {},
      () => this.loading = false 
    );
  }

  addItem(newItem?: string) {
    this.idUniversite = this.route.snapshot.params['id'];
    this.actualiteService
        .getActualiteByUniversiteId( this.idUniversite )
        .subscribe( actualites => {
            this.actualites = actualites
        })
  }

}
