import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RegisterComponent } from './register/register.component';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { baseURL } from '../shared/baseurl';
import { UpdateEnseignantComponent } from './update-enseignant/update-enseignant.component';
import { EnregisterEnsignantComponent } from './enregister-ensignant/enregister-ensignant.component';



@NgModule({
  declarations: [
    RegisterComponent,
    UpdateEnseignantComponent,
    EnregisterEnsignantComponent
  ],
  providers: [
    { provide: 'baseURL', useValue: baseURL },
  ],
  imports: [
    CommonModule,
    FormsModule,
    ReactiveFormsModule
  ],
  exports: [RegisterComponent, UpdateEnseignantComponent],
  entryComponents: [RegisterComponent, UpdateEnseignantComponent]
})
export class EnseignantModule { }
