import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { baseURL } from '../shared/baseurl';
import { Departement } from '../shared/departement';

@Injectable({
  providedIn: 'root'
})
export class DepartementService {

  constructor(private httpClient: HttpClient) { }

  createDepartement(departement: Departement): Observable<Departement> {
    return this.httpClient.post<Departement>(baseURL + 'departements', departement);
  }

  getAllDepartements(): Observable<Departement[]> {
    return this.httpClient.get<Departement[]>(baseURL + 'departements');
  }

  getDepartementById(id: string): Observable<Departement> {
    return this.httpClient.get<Departement>(baseURL + 'departements/' + id);
  }

  getDepartementByUniversiteById(idUniversite: string): Observable<Departement[]> {
    return this.httpClient.get<Departement[]>(baseURL + 'departements/universites/' + idUniversite);
  }
  getLogoName(file: File): Observable<any> {
    const formData = new FormData();
    formData.append('logo', file)
    return this.httpClient.put<any>(baseURL + 'universites', formData, {});
  }
  deleteDepartement(idDepartement: string): Observable<Departement>{
    return this.httpClient.delete<Departement>( baseURL + 'departements/' + idDepartement );
  }

  updateDepartement( id: string, departement: Departement ): Observable<Departement>{
    return this.httpClient.put<Departement>( baseURL + 'departements/' + id, departement );
  }
}
