import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { RechercheService } from '../modules/services/recherche.service';
import { UniversiteService } from '../modules/services/universite.service';
import { baseURL } from '../modules/shared/baseurl';
import { Recherche } from '../modules/shared/recherche';
import { Universite } from '../modules/shared/universite';
import { flyInOut, expand } from 'animations/app.animation';
@Component({
  selector: 'app-detail-publication',
  templateUrl: './detail-publication.component.html',
  styleUrls: ['./detail-publication.component.scss'],
  host: {
    '[@flyInOut]': 'true',
    'style': 'display: block;'
  },
  animations: [
    flyInOut(),
    expand()
  ]
})
export class DetailPublicationComponent implements OnInit {
  idUniversite: string = '';
  idRecherche: string;
  universite: Universite;
  recherche: Recherche;
  baseURL: string = baseURL;
  constructor(
    private universiteService: UniversiteService,
    private rechercheService: RechercheService,
    private route: ActivatedRoute
  ) { }

  ngOnInit(): void {
    this.idRecherche = this.route.snapshot.params['id'];
    this.idUniversite = localStorage.getItem('universite')?.toString() as string;
    this.universiteService
        .getUniversiteById( this.idUniversite )
        .subscribe( universite => this.universite = universite );

    this.rechercheService
        .getRechercheById( this.idRecherche )
        .subscribe( recherche => this.recherche = recherche )
  }

}
