import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { baseURL } from '../shared/baseurl';
import { Classe } from '../shared/classe';

@Injectable({
  providedIn: 'root'
})
export class ClasseService {


  constructor(private httpClient: HttpClient) { }

  getAllClasses(): Observable<Classe[]> {
    return this.httpClient.get<Classe[]>(baseURL + 'classes');
  }
  getClasseById(idClasse?: string): Observable<Classe> {
    return this.httpClient.get<Classe>(baseURL + 'classes/' + idClasse);
  }

  createClasse(classe: Classe): Observable<Classe> {
    return this.httpClient.post<Classe>(baseURL + 'classes', classe);
  }

  updateClasse(idClasse: string, classe: Classe): Observable<Classe> {
    return this.httpClient.put<Classe>(baseURL + 'classes/' + idClasse, classe)
  }
  getClasseByFiliereId(idFiliere: string): Observable<Classe[]> {
    return this.httpClient.get<Classe[]>(baseURL + 'classes/filieres/' + idFiliere);
  }

  getClasseByParcoursId(idParcours: string): Observable<Classe[]> {
    return this.httpClient.get<Classe[]>(baseURL + 'classes/parcours/' + idParcours);
  }
  getClasseByFiliereIdAndNiveau(idFiliere: string, niveau: string): Observable<Classe[]> {
    return this.httpClient.get<Classe[]>(baseURL + 'classes/filieres/' + idFiliere + '/' + niveau);
  }

  deleteClasse(idFiliere: string): Observable<Classe> {
    return this.httpClient.delete<Classe>(baseURL + 'classes/' + idFiliere);
  }
}
