import { Component, OnInit, ViewChild } from '@angular/core';
import { FormArray, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { ActualiteService } from '../../services/actualite.service';
import { UniversiteService } from '../../services/universite.service';
import { Actualite } from '../../shared/actualite';
import { Universite } from '../../shared/universite';

@Component({
  selector: 'app-create-actualite',
  templateUrl: './create-actualite.component.html',
  styleUrls: ['./create-actualite.component.scss']
})
export class CreateActualiteComponent implements OnInit {


  @ViewChild('fform', { static: false }) actualiteFormDirective: any;
  actualiteForm: FormGroup;
  fichiers: FormArray;

  selectedFile: File[] = [];

  actualite: Actualite;
  loading: boolean = false;
  formErrors: any = {
    titre: '',
    description: ''
  };

  validationMessages: any = {
    titre: {
      required: 'le titre  est requis.',
      minlength: 'le titre tre plus que 4 caractres.'
    },
    description: {
      required: 'la description est requise.',
      minlength: 'la description doit tre plus que 6 caractres.'
    }
  };

  compteId?: string;
  msgError?: string;
  universites: Universite[] = [];
  type: string = '';


  constructor(
    private formBuilder: FormBuilder,
    private router: Router,
    private actualiteService: ActualiteService,
    private universiteService: UniversiteService,
    private route: ActivatedRoute
  ) {
    this.createForm();
  }

  ngOnInit(): void {
    this.type = this.route.snapshot.params['name'];

    this.universiteService
      .getAllUniversities()
      .subscribe(universites => this.universites = universites)
  }

  createForm() {
    this.actualiteForm = this.formBuilder.group({
      titre: ['', [Validators.required, Validators.minLength(4)]],
      description: ['', [Validators.required, Validators.minLength(4)]],
      universite: 0,
      fichiers: this.formBuilder.array([this.createImage()])
    });
    this.actualiteForm
      .valueChanges
      .subscribe(data => this.onValueChanged(data));
    this.onValueChanged();
  }

  onValueChanged(data?: any) {
    if (!this.actualiteForm) { return; }
    const form = this.actualiteForm;
    for (const field in this.formErrors) {
      if (this.formErrors.hasOwnProperty(field)) {
        // clear previous error message (if any)
        this.formErrors[field] = '';
        const control = form.get(field);
        if (control && control.dirty && !control.valid) {
          const messages = this.validationMessages[field];
          for (const key in control.errors) {
            if (control.errors.hasOwnProperty(key)) {
              this.formErrors[field] += messages[key] + ' ';
            }
          }
        }
      }
    }
  }

  ngOnChanges() {
    this.onSubmit();
  }

  closeModal() {
    this.router.navigateByUrl('reset-password');
  }

  onSubmit() {
    this.actualite = this.actualiteForm?.value;
    this.actualite.fichiers = [];
    ///this.actualite = this.type;

    if (this.selectedFile.length > 0) {
      this.selectedFile.forEach(f => {
        this.actualite?.fichiers.push('images/actualites/' + f.name)
      })
    }
    if (this.actualite != null && this.selectedFile) {
      this.actualiteService
        .createActualite(this.actualite)
        .subscribe(
          actualite => {
            this.selectedFile.forEach(fichier => {
              this.actualiteService
                .telecharger(fichier)
                .subscribe(
                  data => {  },
                  error => {  },
                  () => {
                    
                  }
                )
            });
          },
          err => { this.msgError = err.error.message; },
          () => {
            setTimeout(() => { this.loading = false; }, 2000)
          }
        )

    }

    this.actualiteFormDirective.resetForm();
  }

  createImage(): FormGroup {
    return this.formBuilder.group({
      fichier: '',
    });
  }

  addImage(): void {
    this.fichiers = this.actualiteForm.get('fichiers') as FormArray;
    this.fichiers.push(this.createImage());
  }


  fileChanged(event: any) {
    this.selectedFile.push(event.target.files[0]);
  }

  get fichierFormGroups() {
    return this.actualiteForm.get('fichiers') as FormArray
  }

}
