import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { baseURL } from '../shared/baseurl';
import { VieEstidiantine } from '../shared/vieEstidiantine';

@Injectable({
  providedIn: 'root'
})
export class VieEstidiantineService {

  constructor(private httpClient: HttpClient) { }
  getAllUniversities(): Observable<VieEstidiantine[]> {
    return this.httpClient.get<VieEstidiantine[]>(baseURL + 'vies');
  }

  getVieEstidiantineById(id: string): Observable<VieEstidiantine> {
    return this.httpClient.get<VieEstidiantine>(baseURL + 'vies/' + id)
  }

  createVieEstidiantine(vieEstidiantine: VieEstidiantine): Observable<VieEstidiantine> {
    return this.httpClient.post<VieEstidiantine>(baseURL + 'vies', vieEstidiantine, {});
  }

  getLogoName(file: File): Observable<any> {
    const formData = new FormData();
    formData.append('logo', file)
    console.log("SERVICE", formData);
    return this.httpClient.put<any>(baseURL + 'vies', formData, {});
  }

  getUniversityByType(type?: string): Observable<VieEstidiantine[]> {
    return this.httpClient.get<VieEstidiantine[]>(baseURL + 'vies/types/' + type);
  }

  updateVieEstidiantine(id: string, VieEstidiantine: VieEstidiantine): Observable<VieEstidiantine> {
    return this.httpClient.put<VieEstidiantine>(baseURL + 'vies/' + id, VieEstidiantine);
  }
  //Les methodes de cration ici
  public telecharger(file: File): Observable<string> {
    let formData = new FormData();
    formData.append('fichier', file);
    console.log(formData);
    return this.httpClient.put<string>(baseURL + "vies", formData);
  }
}
