import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { InscriptionEnLigneService } from '../modules/services/inscription-en-ligne.service';
import { UniversiteService } from '../modules/services/universite.service';
import { InscriptionEnLigne } from '../modules/shared/inscriptionEnLigne';
import { Universite } from '../modules/shared/universite';
import { flyInOut, expand } from 'animations/app.animation';
@Component({
  selector: 'app-inscription-en-ligne',
  templateUrl: './inscription-en-ligne.component.html',
  styleUrls: ['./inscription-en-ligne.component.scss'],
  host: {
    '[@flyInOut]': 'true',
    'style': 'display: block;'
  },
  animations: [
    flyInOut(),
    expand()
  ]
})
export class InscriptionEnLigneComponent implements OnInit {

  universite: Universite;
  idUniversite: string = '';
  inscriptionEnLigne?: InscriptionEnLigne[];
  loading: boolean = true;

  constructor(
    private route: ActivatedRoute,
    private universiteService: UniversiteService,
    private inscriptionEnLigneService: InscriptionEnLigneService
  ) { }

  ngOnInit(): void {
    this.idUniversite = this.route.snapshot.params['id'];
    this.universiteService
        .getUniversiteById( this.idUniversite )
        .subscribe( 
          universite => this.universite = universite,
          error => {},
          () => this.loading = false 
        );
    
    this.inscriptionEnLigneService
        .getInscriptionEnLigneByUniversiteId( this.idUniversite )
        .subscribe( inscription => this.inscriptionEnLigne = inscription );
  }

}
