import { Component, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatDialog } from '@angular/material/dialog';
import { ActivatedRoute, Router } from '@angular/router';
import { Observable } from 'rxjs';
import { CompteService } from '../../services/compte.service';
import { EnseignantService } from '../../services/enseignant.service';
import { Enseignant } from '../../shared/enseignant';


@Component({
  selector: 'app-update-enseignant',
  templateUrl: './update-enseignant.component.html',
  styleUrls: ['./update-enseignant.component.scss']
})
export class UpdateEnseignantComponent implements OnInit {
  selectedFile: File;

  @ViewChild('fform', { static: false }) compteFormDirective: any;
  compteForm: FormGroup;
  enseignant$?: Observable<Enseignant>

  compte?: any;

  formErrors: any = {
    prenom: '',
    nom: '',
    email: '',
    telephone: '',
    adresse: '',
    ville: '',
    pays: '',
    specialite: '',
    status: '',
    matricule: '',
    cin: ''
  };

  validationMessages: any = {
    confirmation: {
      required: 'la confirmation du mot de passe est requis.',
      minlength: 'la confirmation du mot de passe doit tre plus que 6 caractres.',
      maxlength: 'la confirmation du mot de passe ne doit pas tre plus que 150 caractres.'
    },
    prenom: {
      required: 'le prnom est requis.',
      minlength: 'le prnom doit tre plus que 6 caractres.',
      maxlength: 'le prnom doit  ne doit pas tre plus que 150 caractres.'
    },
    nom: {
      required: 'le nom est requis.',
      minlength: 'le nom  doit tre plus que 6 caractres.',
      maxlength: 'le nom  doit ne doit pas tre plus que 150 caractres.'
    },
    adresse: {
      required: 'l\'adresse est requise.',
      minlength: 'l\'adresse doit tre plus que 6 caractres.',
      maxlength: 'l\'adresse ne doit pas tre plus que 150 caractres.'
    },
    email: {
      required: 'l\'e-mail est requis.',
      minlength: 'l\'e-mail doit tre plus que 6 caractres.',
      maxlength: 'l\'e-mail ne doit pas tre plus que 150 caractres.'
    },
    ville: {
      required: 'le nom de la ville est requise',
      minlength: 'le nom de la ville doit tre plus que 6 caractres.',
      maxlength: 'le nom de la ville ne doit pas tre plus que 150 caractres.'
    },
    pays: {
      required: 'le nom du pays est requis.',
      minlength: 'le nom du pays doit tre plus que 6 caractres.',
      maxlength: 'le nom du pays ne doit pas tre plus que 150 caractres.'
    },
    telephone: {
      required: 'le numro du tlphone est requis.',
      minlength: 'le numro du tlphone doit tre plus que 6 caractres.',
      maxlength: 'le numro du tlphone ne doit pas tre plus que 150 caractres.'
    },
    specialite: {
      required: 'la spcialit est requis.',
      minlength: 'la spcialit doit tre plus que 6 caractres.',
      maxlength: 'la spcialit ne doit pas tre plus que 150 caractres.'
    },
    matricule: {
      required: 'le matricule est requis.',
      minlength: 'le matricule  doit tre plus que 6 caractres.',
      maxlength: 'le matricule  ne doit pas tre plus que 150 caractres.'
    },
    status: {
      required: 'le status est requis.',
      minlength: 'le status doit tre plus que 6 caractres.',
      maxlength: 'le status ne doit pas tre plus que 150 caractres.'
    },
    cin: {
      required: 'le cin est requis.',
      minlength: 'le cin doit tre plus que 6 caractres.',
      maxlength: 'le cin ne doit pas tre plus que 150 caractres.'
    }
  };
  new_?: Enseignant;

  idEnseignant?: string;
  newEnseignant$?: Observable<Enseignant>;
  constructor(
    private formBuilder: FormBuilder,
    private router: Router,
    private enseignantService: EnseignantService,
    private route: ActivatedRoute,
    private dialog: MatDialog
  ) {
    this.idEnseignant = this.route.snapshot.params['id'];
    this.createForm();
  }

  ngOnInit(): void {
    this.newEnseignant$ = this.enseignantService.getEnseignantById(this.idEnseignant)
  }

  createForm() {
    this.compteForm = this.formBuilder.group({
      prenom: ['', [Validators.required, Validators.minLength(6), Validators.maxLength(150)]],
      nom: ['', [Validators.required, Validators.minLength(6), Validators.maxLength(150)]],
      email: ['', [Validators.required, Validators.minLength(6), Validators.email, Validators.maxLength(150)]],
      adresse: ['', [Validators.required, Validators.minLength(6), Validators.maxLength(150)]],
      telephone: ['', [Validators.required, Validators.minLength(6), Validators.maxLength(150)]],
      ville: ['', [Validators.required, Validators.minLength(6), Validators.maxLength(150)]],
      specialite: ['', [Validators.required, Validators.minLength(6), Validators.maxLength(150)]],
      pays: ['', [Validators.required, Validators.minLength(6), Validators.maxLength(150)]],
      status: ['', [Validators.required, Validators.minLength(6), Validators.maxLength(150)]],
      matricule: ['', [Validators.required, Validators.minLength(6), Validators.maxLength(150)]],
      cin: ['', [Validators.required, Validators.minLength(6), Validators.maxLength(150)]]
    });
    this.compteForm
      .valueChanges
      .subscribe(data => this.onValueChanged(data));
    this.onValueChanged();
  }

  onValueChanged(data?: any) {
    if (!this.compteForm) { return; }
    const form = this.compteForm;
    for (const field in this.formErrors) {
      if (this.formErrors.hasOwnProperty(field)) {
        // clear previous error message (if any)
        this.formErrors[field] = '';
        const control = form.get(field);
        if (control && control.dirty && !control.valid) {
          const messages = this.validationMessages[field];
          for (const key in control.errors) {
            if (control.errors.hasOwnProperty(key)) {
              this.formErrors[field] += messages[key] + ' ';
            }
          }
        }
      }
    }
  }


  onSubmit() {
    this.compte = this.compteForm?.value;
    const compteId = localStorage.getItem('_id')?.toString();
    this.enseignantService
      //.getBase64(this.selectedFile)
      .updateEnseignant(
        this.idEnseignant,
        {
          prenom: this.compte.prenom,
          nom: this.compte.nom,
          email: this.compte.email,
          ville: this.compte.ville,
          pays: this.compte.pays,
          adresse: this.compte.adresse,
          telephone: this.compte.telephone,
          specialite: this.compte.specialite,
          matricule: this.compte.matricule,
          status: this.compte.status
        } as Enseignant)
      .subscribe(enseignant => {
        // this.dialog.closeAll()
        this.router.navigateByUrl('/etudiant-profile');
      })

    this.compteFormDirective.resetForm();

  }

  fileChanged(event: any) {
    this.selectedFile = event.target.files[0];
  }

}

