import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { baseURL } from '../shared/baseurl';
import { Compte } from '../shared/compte';
import { Dossier } from '../shared/dossier';

@Injectable({
  providedIn: 'root'
})
export class DossierService {

  constructor(private httpClient: HttpClient) { }

  getAllDossiers(): Observable<Dossier[]> {
    return this.httpClient.get<Dossier[]>(baseURL + 'dossiers');
  }
  getDossierById(idDossier?: string): Observable<Dossier> {
    return this.httpClient.get<Dossier>(baseURL + 'dossiers/' + idDossier);
  }

  createDossier(dossier: Dossier): Observable<Dossier> {
    return this.httpClient.post<Dossier>(baseURL + 'dossiers', dossier);
  }

  updateDossier(idDossier: string, dossier: Dossier): Observable<Dossier> {
    return this.httpClient.put<Dossier>(baseURL + 'dossiers/' + idDossier, dossier)
  }
  getDossierByCompteId(idCompte: string): Observable<Dossier[]> {
    return this.httpClient.get<Dossier[]>(baseURL + 'dossiers/comptes/' + idCompte);
  }
  getDossierByClasseId(idClasse?: string): Observable<Dossier[]> {
    return this.httpClient.get<Dossier[]>(baseURL + 'dossiers/classes/' + idClasse);
  }

  getDossierByUniversiteAndCompte(idUniversite?: string, idCompte?: string): Observable<Dossier> {
    return this.httpClient.get<Dossier>(baseURL + 'dossiers/' + idUniversite + '/' + idCompte);
  }

  deleteDossier(idDossier: string): Observable<Dossier> {
    return this.httpClient.delete<Dossier>(baseURL + 'dossiers/' + idDossier);
  }

  createDiplome(file: File): Observable<File> {
      let formData = new FormData();
      formData.append('diplome', file);
      console.log('MBALOU',formData);
      return this.httpClient.post<File>(baseURL + "dossiers/diplomes", formData);
  }

  createReleveBAC(file: File): Observable<File> {
    let formData = new FormData();
    formData.append('releve', file);
    console.log(formData);
    return this.httpClient.post<File>(baseURL + "dossiers/releves", formData);
  }

  createReleveReleves(file: File): Observable<File> {
    let formData = new FormData();
    formData.append('fichier', file);
    console.log(formData);
    return this.httpClient.post<File>(baseURL + "dossiers/fichiers", formData);
  }
}
