import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ColloqueService } from '../modules/services/colloque.service';
import { DistinctionService } from '../modules/services/distinction.service';
import { UniversiteService } from '../modules/services/universite.service';
import { baseURL } from '../modules/shared/baseurl';
import { Colloque } from '../modules/shared/colloque';
import { Universite } from '../modules/shared/universite';
import { flyInOut, expand } from 'animations/app.animation';

@Component({
  selector: 'app-list-colloque',
  templateUrl: './list-colloque.component.html',
  styleUrls: ['./list-colloque.component.scss'],
  host: {
    '[@flyInOut]': 'true',
    'style': 'display: block;'
  },
  animations: [
    flyInOut(),
    expand()
  ]
})
export class ListColloqueComponent implements OnInit {
  idUniversite: string = '';
  universite: Universite;
  colloques: Colloque[] = [];
  baseURL: string = baseURL;
  loading: boolean = true;
  constructor(
    private universiteService: UniversiteService,
    private colloqueService: ColloqueService,
    private route: ActivatedRoute
  ) { }

  ngOnInit(): void {
    this.idUniversite = this.route.snapshot.params['id'];
    this.universiteService
        .getUniversiteById( this.idUniversite )
        .subscribe( 
          universite => this.universite = universite,
          error => {},
          () => this.loading = false 
        );

    this.colloqueService
        .getColloqueByUniversiteId( this.idUniversite )
        .subscribe( colloques => this.colloques = colloques )
  }

}

