import { Component, OnInit } from '@angular/core';
import { BiblithequeService } from '../modules/services/biblitheque.service';

@Component({
  selector: 'app-bibliothque',
  templateUrl: './bibliothque.component.html',
  styleUrls: ['./bibliothque.component.scss']
})
export class BibliothqueComponent implements OnInit {

  constructor(
    private bibliothequeService: BiblithequeService
  ) { }

  ngOnInit(): void {
    this.bibliothequeService
        .getAllLivres()
        .subscribe( livres => console.log('LIVRES: ', livres) )
  }

}
