import { Component, Inject, OnInit } from '@angular/core';
import { Tutoral } from './../../shared/tutoral';
import { MAT_DIALOG_DATA, MatDialog } from '@angular/material/dialog';
import { TutoralService } from './../../services/tutoral.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-delete-modal-tutoral',
  templateUrl: './delete-modal-tutoral.component.html',
  styleUrls: ['./delete-modal-tutoral.component.scss']
})
export class DeleteModalTutoralComponent implements OnInit {
  tutoral?: Tutoral;
  errorTutoral: any;
  constructor(
    private tutoralService: TutoralService,
    private matDiag: MatDialog,
    private router: Router,
    @Inject( MAT_DIALOG_DATA ) private data: any
  ) { }

  ngOnInit(): void {
    this.tutoral = this.data.tutoral;
  }

  supprimerTutoral(){
    this.tutoralService
        .deleteTutoral( this.tutoral?._id )
        .subscribe( 
          tutoral => {},
          error => this.errorTutoral = error,
          () => {this.router.navigateByUrl('/guide-tutoral'); location.reload();}
         )

  }

  closeModal() {
    this.matDiag.closeAll();
  }

}
