import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { baseURL } from '../shared/baseurl';
import { Semestre } from '../shared/semestre';

@Injectable({
  providedIn: 'root'
})
export class SemestreService {
  constructor(private httpClient: HttpClient) { }

  getAllSemestres(): Observable<Semestre[]> {
    return this.httpClient.get<Semestre[]>(baseURL + 'semestres');
  }
  getSemestreById(idSemestre: string): Observable<Semestre> {
    return this.httpClient.get<Semestre>(baseURL + 'semestres/' + idSemestre);
  }

  createSemestre(semestre: Semestre): Observable<Semestre> {
    return this.httpClient.post<Semestre>(baseURL + 'semestres', semestre);
  }

  updateSemestre(idSemestre: string, semestre: Semestre): Observable<Semestre> {
    return this.httpClient.put<Semestre>(baseURL + 'semestres/' + idSemestre, semestre)
  }
  getSemestreByClasseId(idClasse: string): Observable<Semestre[]> {
    return this.httpClient.get<Semestre[]>(baseURL + 'semestres/classes/' + idClasse);
  }

  deleteSemestre(idSemestre: string): Observable<Semestre> {
    return this.httpClient.delete<Semestre>(baseURL + 'semestres/' + idSemestre);
  }
}

