import { Component, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { OffreService } from '../../services/offre.service';
import { UniversiteService } from '../../services/universite.service';
import { Offre } from '../../shared/offre';
import { Universite } from '../../shared/universite';

@Component({
  selector: 'app-offre',
  templateUrl: './offre.component.html',
  styleUrls: ['./offre.component.scss']
})
export class OffreComponent implements OnInit {

  @ViewChild('fform', { static: false }) offreFormDirective: any;
  offreForm: FormGroup;

  selectedFile: File;

  offre: Offre;
  loading: boolean = false;
  formErrors: any = {
    titre: '',
    societe: '',
    lien: '',
    description: '',
    universite: ''
  };

  validationMessages: any = {
    titre: {
      required: 'le titre  est requis.',
      minlength: 'le titre tre plus que 4 caractres.',
      maxlength: 'le titre l\'auteur ne doit pas tre plus que 150 caractres.'
    },
    societe: {
      required: 'le nom de la societ est requis.',
      minlength: 'le nom de la societ que 4 caractres.',
      maxlength: 'le nom de la societ ne doit pas tre plus que 150 caractres.'
    },
    lien: {
      required: 'le lien de la societ est requis.',
      minlength: 'le lien de la societ que 4 caractres.',
      maxlength: 'le lien de la societ ne doit pas tre plus que 150 caractres.'
    },
    description: {
      required: 'la description est requise.',
      minlength: 'la description doit tre plus que 6 caractres.',
      maxlength: 'la description ne doit pas tre plus que 2000 caractres.'
    },
    universite: {
      required: 'le titre  est requis.'
    },
  };

  compteId?: string;
  msgError?: string;
  universites: Universite[] = [];

  constructor(
    private formBuilder: FormBuilder,
    private router: Router,
    private offreService: OffreService,
    private universiteService: UniversiteService
  ) {
    this.createForm();
  }

  ngOnInit(): void {
    this.universiteService
      .getAllUniversities()
      .subscribe(universites => this.universites = universites)
  }

  createForm() {
    this.offreForm = this.formBuilder.group({
      titre: ['', [Validators.required, Validators.minLength(4), Validators.maxLength(150)]],
      societe: ['', [Validators.required, Validators.minLength(4), Validators.maxLength(150)]],
      lien: ['', [Validators.required, Validators.minLength(4), Validators.maxLength(150)]],
      description: ['', [Validators.required, Validators.minLength(4), Validators.maxLength(1000)]],
      universite: ['', [Validators.required]],
      fichier: ''
    });
    this.offreForm
      .valueChanges
      .subscribe(data => this.onValueChanged(data));
    this.onValueChanged();
  }

  onValueChanged(data?: any) {
    if (!this.offreForm) { return; }
    const form = this.offreForm;
    for (const field in this.formErrors) {
      if (this.formErrors.hasOwnProperty(field)) {
        // clear previous error message (if any)
        this.formErrors[field] = '';
        const control = form.get(field);
        if (control && control.dirty && !control.valid) {
          const messages = this.validationMessages[field];
          for (const key in control.errors) {
            if (control.errors.hasOwnProperty(key)) {
              this.formErrors[field] += messages[key] + ' ';
            }
          }
        }
      }
    }
  }

  ngOnChanges() {
    this.onSubmit();
  }

  closeModal() {
    this.router.navigateByUrl('reset-password');
  }

  onSubmit() {
    this.offre = this.offreForm?.value;
    this.offre.fichier = 'images/offres/' + this.selectedFile.name
    this.offreService
        .telecharger(this.selectedFile)
        .subscribe( file => {
          this.offreService
              .createOffre(this.offre)
              .subscribe(
                offre=> {},
                err => { this.msgError = err.error.message; },
                () => {
                  setTimeout(() => { this.loading = false; }, 2000);
                  location.reload()
                }
              )
        } )
    this.offreFormDirective.resetForm();
  }
  fileChanged(event: any) {
    this.selectedFile = event.target.files[0];
  }
}
