import { Component, OnInit, ViewChild } from '@angular/core';
import { FormGroup, FormArray, FormBuilder } from '@angular/forms';
import { TutoralService } from './../../services/tutoral.service';
import { Router, ActivatedRoute } from '@angular/router';
import { Tutoral } from './../../shared/tutoral';

@Component({
  selector: 'app-update-tutoral',
  templateUrl: './update-tutoral.component.html',
  styleUrls: ['./update-tutoral.component.scss']
})
export class UpdateTutoralComponent implements OnInit {
  @ViewChild('fform', { static: false }) tutoralFormDirective: any;
  tutoralForm: FormGroup;
  tutorals: FormArray;
  msgError: string = '';
  idTutoral?: string;
  tutural?: Tutoral;
  tutoralUpdate: Tutoral;

  constructor(
    private formBuilder: FormBuilder,
    private tutoralService: TutoralService,
    private route: ActivatedRoute,
    private router: Router) { }

  ngOnInit() {
    this.idTutoral = this.route.snapshot.params['id'];
    this.tutoralService
        .getTutoralById( this.idTutoral )
        .subscribe( 
          tutoral => this.tutoralUpdate = tutoral,
          error => this.msgError = error
         )

    this.tutoralForm = this.formBuilder.group({
      titre: '',
      type: '',
      lien: ''
    });
  }
 

  onSubmit() {
      var valeur = this.tutoralForm.value.lien.substr(this.tutoralForm.value.lien.indexOf("=") + 1);
      var autre: string = "";
      if(valeur.indexOf("&")>=0) {
        autre = valeur.substr(0, valeur.indexOf("&"));
        this.tutoralForm.value.lien = autre;
      } else
        this.tutoralForm.value.lien = valeur;
    this.tutoralService
        .updateTutoral(
          this.tutoralUpdate?._id,
          {
            titre: this.tutoralForm.value.titre,
            lien: this.tutoralForm.value.lien,
            type: this.tutoralForm.value.type
          } as Tutoral
        )
        .subscribe(data =>{
          this.router.navigateByUrl('/guide-tutorial');                         
        });
    }
}
