import { Component, Inject, OnInit } from '@angular/core';
import { MatDialog, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { ParcoursService } from './../../services/parcours.service';
import { Parcours } from './../../shared/parcours';
import { Router } from '@angular/router';

@Component({
  selector: 'app-supprimer-filiere',
  templateUrl: './supprimer-filiere.component.html',
  styleUrls: ['./supprimer-filiere.component.scss']
})
export class SupprimerFiliereComponent implements OnInit {
  id: string = '';
  parcours?: Parcours;
  loading: boolean = false;
  constructor(
    private parcoursService: ParcoursService,
    @Inject(MAT_DIALOG_DATA) private data: any,
    private matDialog: MatDialog,
    private router: Router
  ) { }

  ngOnInit(): void {
    this.id = this.data.id;
    this.parcoursService
        .getParcoursById( this.id )
        .subscribe(parcours => this.parcours = parcours );
  }

  supprimerFilier() {
    this.loading = true;
    this.parcoursService
        .deleteParcours( this.parcours?._id )
        .subscribe( 
          parcours => {},
          error => {},
          () => {
            this.closeModal();
            location.reload();
            this.router.navigateByUrl('/departement-detail/' + this.data.idDepartement );
          }
        )
        
  }

  closeModal() {
    this.matDialog.closeAll()
  }

}
