import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { ActualiteService } from '../modules/services/actualite.service';
import { UniversiteService } from '../modules/services/universite.service';
import { Actualite } from '../modules/shared/actualite';
import { baseURL } from '../modules/shared/baseurl';
import { Universite } from '../modules/shared/universite';
import { flyInOut, expand } from 'animations/app.animation';
@Component({
  selector: 'app-detail-actualite',
  templateUrl: './detail-actualite.component.html',
  styleUrls: ['./detail-actualite.component.scss'],
  host: {
    '[@flyInOut]': 'true',
    'style': 'display: block;'
  },
  animations: [
    flyInOut(),
    expand()
  ]
})
export class DetailActualiteComponent implements OnInit {
  idActualite: string;
  idUniversite?: string;
  universite?: Universite;
  actualite: Actualite;
  baseURL: string = baseURL;
  cinqDernieresActualite: Actualite[]=[];
  constructor(
    private route: ActivatedRoute,
    private universiteService: UniversiteService,
    private actualiteService: ActualiteService,
    private router: Router
  ) { }

  ngOnInit(): void {
    this.idActualite = this.route.snapshot.params['id'];
    this.actualiteService
        .getActualiteById( this.idActualite )
        .subscribe( actualite => {
          this.actualite = actualite;
        })
    this.idUniversite = localStorage.getItem('universite')?.toString() as string;
    this.universiteService
    .getUniversiteById( this.idUniversite )
    .subscribe( universite => this.universite = universite );
    this.actualiteService
        .getLastFiveActualite(this.idUniversite)
        .subscribe( actualite => {
          this.cinqDernieresActualite = actualite
        } )
  }

  addItem(newItem: Actualite[]) {
    //this.cinqDernieresActualite = newItem;
    this.idActualite = this.route.snapshot.params['id'];
    this.actualiteService
        .getActualiteById( this.idActualite )
        .subscribe( actualite => {
          this.actualite = actualite;
        })
  }


  gotActualiteDetail(id: string) {
    this.router.navigateByUrl('detail-actualite/' + id);
    this.actualiteService
        .getActualiteById( id )
        .subscribe( actualite => {
          this.actualite = actualite;
        })
  }

}
