import { Component, OnInit, ViewChild } from '@angular/core';
import { FormArray, FormBuilder, FormGroup } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { ChapitreService } from '../../services/chapitre.service';
import { MatiereService } from '../../services/matiere.service';
import { SectionService } from '../../services/section.service';
import { Chapitre } from '../../shared/chapitre';
import { Matiere } from '../../shared/matiere';
import { Section } from '../../shared/section';

@Component({
  selector: 'app-ajouter-chapitre',
  templateUrl: './ajouter-chapitre.component.html',
  styleUrls: ['./ajouter-chapitre.component.scss']
})
export class AjouterChapitreComponent implements OnInit {
  @ViewChild('fform', { static: false }) compteFormDirective: any;
  chapitreForm: FormGroup;
  items: FormArray;
  selectedFile: File[] = [];
  idMatiere: string = "";
  matiere?: Matiere;

  constructor(
    private formBuilder: FormBuilder,
    private chapitreService: ChapitreService,
    private sectionService: SectionService,
    private matiereService: MatiereService,
    private route: ActivatedRoute,
    private router: Router
  ) { }

  ngOnInit() {
    this.idMatiere = this.route.snapshot.params['id'];

    this.matiereService
      .getMatiereById(this.idMatiere)
      .subscribe(matiere => {
        this.matiere = matiere;
      })

    this.chapitreForm = this.formBuilder.group({
      titre: '',
      resume: '',
      items: this.formBuilder.array([this.createItem()])
    });
  }
  get userDetails() {
    return (this.chapitreForm.controls.items as FormGroup).controls;
  }

  get userFormGroups() {
    return this.chapitreForm.get('items') as FormArray
  }

  createItem(): FormGroup {
    return this.formBuilder.group({
      titre: '',
      contenu: '',
      fichier: '',
      video: ''
    });
  }

  addItem(): void {
    this.items = this.chapitreForm.get('items') as FormArray;
    this.items.push(this.createItem());
  }

  onSubmit() {
    for (let i = 0; i < this.chapitreForm.value.items.length; i++) {
      if (this.chapitreForm.value.items[i].video.length > 0) {
        let valeur = this.chapitreForm.value.items[i].video.substr(this.chapitreForm.value.items[i].video.indexOf("=") + 1)
        this.chapitreForm.value.items[i].video = valeur;
      }
    }
    this.chapitreService
      .createChapitre({
        titre: this.chapitreForm.value.titre,
        resume: this.chapitreForm.value.resume,
        matiere: this.matiere
      } as Chapitre)
      .subscribe(chapitre => {
        for (let i = 0; i < this.chapitreForm.value.items.length; i++) {
          this.sectionService
            .createSection(
              {
                titre: this.chapitreForm.value.items[i].titre,
                contenu: this.chapitreForm.value.items[i].contenu,
                fichier: (this.chapitreForm.value.items[i].fichier.length > 0) ? this.chapitreForm.value.items[i].fichier : '',
                video: (this.chapitreForm.value.items[i].video.length > 0) ? this.chapitreForm.value.items[i].video : '',
                chapitre: chapitre
              } as Section,
              this.selectedFile[i]
            )
            .subscribe(data => {});
          this.sectionService
            .telecharger(this.selectedFile[i])
            .subscribe(
              data => {  },
              error => {  },
              () => {
                this.router.navigateByUrl('matiere-detail/' + this.idMatiere)
              }
            )
        }
      })

  }

  fileChanged(event: any) {
    this.selectedFile.push(event.target.files[0]);
  }

}
