import { Component, OnInit, ViewChild } from '@angular/core';
import { FormArray, FormBuilder, FormGroup } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { CadreAdministratifService } from '../../services/cadre-administratif.service';
import { UniversiteService } from '../../services/universite.service';
import { CadreAdministratif } from '../../shared/cadreAdministratif';
import { Universite } from '../../shared/universite';

@Component({
  selector: 'app-ajouter-cadre-administratif',
  templateUrl: './ajouter-cadre-administratif.component.html',
  styleUrls: ['./ajouter-cadre-administratif.component.scss']
})
export class AjouterCadreAdministratifComponent implements OnInit {
  @ViewChild('fform', { static: false }) cadreFormDirective: any;
  cadreForm: FormGroup;
  items: FormArray;
  idUniversite: string = "";
  cadre?: CadreAdministratif;
  universite?: Universite;
  cadres: CadreAdministratif[] = [];

  constructor(
    private formBuilder: FormBuilder,
    private universiteService: UniversiteService,
    private cadreAdministratifService: CadreAdministratifService,
    private route: ActivatedRoute,
    private router: Router
  ) { }

  ngOnInit() {
    this.idUniversite = this.route.snapshot.params['id'];
    this.cadreAdministratifService
        .getCadreAdministratifByUniversiteId( this.idUniversite )
        .subscribe( cadres => this.cadres = cadres )

    this.universiteService
      .getUniversiteById(this.idUniversite)
      .subscribe(universite => {
        this.universite = universite;
      })

    this.cadreForm = this.formBuilder.group({
      items: this.formBuilder.array([this.createItem()])
    });
  }
  get cadreDetails() {
    return (this.cadreForm.controls.items as FormGroup).controls;
  }

  get cadreFormGroups() {
    return this.cadreForm.get('items') as FormArray
  }

  createItem(): FormGroup {
    return this.formBuilder.group({
      titre: '',
      fonction: '',
      prenom: '',
      nom: '',
      universite: ''
    });
  }

  addItem(): void {
    this.items = this.cadreForm.get('items') as FormArray;
    this.items.push(this.createItem());
  }

  onSubmit() {
        for (let i = 0; i < this.cadreForm.value.items.length; i++) {
          this.cadreAdministratifService
            .createCadreAdministratif(
              {
                titre: this.cadreForm.value.items[i].titre,
                fonction: this.cadreForm.value.items[i].fonction,
                nom: this.cadreForm.value.items[i].nom,
                prenom: this.cadreForm.value.items[i].prenom,
                universite: this.universite
              } as CadreAdministratif
            )
            .subscribe(
              presentation => {},
              error => {},
              () => {
                location.reload();
                //this.router.navigateByUrl('/cadre-administratif/' + this.universite?._id);
              }
            );
        }
  }
  supprimerCadre(id: string) {
    this.cadreAdministratifService
        .deleteCadreAdministratif(id)
        .subscribe(
          cadre => {},
          error => {},
          () => {
            location.reload();
          }
        )
  }
}