import { Component, Inject, OnChanges, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { Observable } from 'rxjs';
import { CompteService } from '../../services/compte.service';
import { EtudiantService } from '../../services/etudiant.service';
import { Etudiant } from '../../shared/etudiant';
import { MatDialog, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { EnseignantService } from '../../services/enseignant.service';

@Component({
  selector: 'app-update-avatar',
  templateUrl: './update-avatar.component.html',
  styleUrls: ['./update-avatar.component.scss']
})
export class UpdateAvatarComponent implements OnInit, OnChanges {

  selectedFile: File;
  @ViewChild('fform', { static: false }) compteFormDirective: any;
  compteForm: FormGroup;
  etudiant$?: Observable<Etudiant>;
  loading: boolean = false;


  compte?: any;
  idEtudianti?: string;

  formErrors: any = {
    avatar: ''
  };

  validationMessages: any = {
    avatar: {
      required: 'la confirmation du mot de passe est requis.'
    }
  };

  idEtudiant?: string;
  newEtudiant$?: Observable<Etudiant>;
  compteId?: string;
  constructor(
    private formBuilder: FormBuilder,
    private router: Router,
    private compteService: CompteService,
    private etudiantService: EtudiantService,
    private route: ActivatedRoute,
    private enseignantService: EnseignantService,
    @Inject(MAT_DIALOG_DATA) private data: any,
    public dialog: MatDialog,
  ) {
    this.idEtudiant = this.route.snapshot.params['id'];
    this.createForm();
  }


  ngOnChanges() {

  }

  ngOnInit(): void {
    this.idEtudianti = this.data.id;
    this.newEtudiant$ = this.etudiantService.getEtudiantById(this.idEtudiant)
  }

  createForm() {
    this.compteForm = this.formBuilder.group({
      prenom: ['', [Validators.required]],
    });
    this.compteForm
      .valueChanges
      .subscribe(data => this.onValueChanged(data));
    this.onValueChanged();
  }

  onValueChanged(data?: any) {
    if (!this.compteForm) { return; }
    const form = this.compteForm;
    for (const field in this.formErrors) {
      if (this.formErrors.hasOwnProperty(field)) {
        // clear previous error message (if any)
        this.formErrors[field] = '';
        const control = form.get(field);
        if (control && control.dirty && !control.valid) {
          const messages = this.validationMessages[field];
          for (const key in control.errors) {
            if (control.errors.hasOwnProperty(key)) {
              this.formErrors[field] += messages[key] + ' ';
            }
          }
        }
      }
    }
  }


  onSubmit() {
    this.compte = this.compteForm.value;
    const compteId = localStorage.getItem('_id')?.toString();
    this.loading = true;
    this.etudiantService
      .createfiles(this.selectedFile, compteId, this.data.id)
      .subscribe(et => {
      }, error => { error == null ? this.loading = true : this.loading = false; },
        () => {
          setTimeout(() => {
            this.loading = false;
          }, 1500)
        }
      )
    this.enseignantService
      .createfiles(this.selectedFile, compteId, this.data.id)
      .subscribe(et => {
        this.etudiantService
          .getEtudiantByCompteId(compteId)
          .subscribe(etudiant => { if (etudiant != null) localStorage.setItem('avatar', etudiant.avatar) })
      })
    this.enseignantService
      .getEnseignantByCompteId(compteId)
      .subscribe(enseignant => { if (enseignant != null) localStorage.setItem('avatar', enseignant.avatar) })
    this.compteFormDirective.resetForm();

    setTimeout(() => {
      window.location.reload();
      this.router.navigateByUrl('/etudiant-profile');
      this.dialog.closeAll()
    }, 1000);

  }

  fileChanged(event: any) {
    this.selectedFile = event.target.files[0];
  }
}
