import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ColloqueService } from '../modules/services/colloque.service';
import { UniversiteService } from '../modules/services/universite.service';
import { baseURL } from '../modules/shared/baseurl';
import { Colloque } from '../modules/shared/colloque';
import { Universite } from '../modules/shared/universite';
import { flyInOut, expand } from 'animations/app.animation';

@Component({
  selector: 'app-detail-colloque',
  templateUrl: './detail-colloque.component.html',
  styleUrls: ['./detail-colloque.component.scss'],
  host: {
    '[@flyInOut]': 'true',
    'style': 'display: block;'
  },
  animations: [
    flyInOut(),
    expand()
  ]
})
export class DetailColloqueComponent implements OnInit {
  idUniversite: string = '';
  idColloque: string;
  universite: Universite;
  colloque: Colloque;
  baseURL: string = baseURL;
  constructor(
    private universiteService: UniversiteService,
    private colloqueService: ColloqueService,
    private route: ActivatedRoute
  ) { }

  ngOnInit(): void {
    this.idColloque = this.route.snapshot.params['id'];
    this.idUniversite = localStorage.getItem('universite')?.toString() as string;
    this.universiteService
        .getUniversiteById( this.idUniversite )
        .subscribe( universite => this.universite = universite );

    this.colloqueService
        .getColloqueById( this.idColloque )
        .subscribe( colloque => this.colloque = colloque )
  }
}
