import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { baseURL } from '../shared/baseurl';
import { Offre } from '../shared/offre';

@Injectable({
  providedIn: 'root'
})
export class OffreService {
  
  constructor(private httpClient: HttpClient) { }
  getAllOffres(): Observable<Offre[]> {
    return this.httpClient.get<Offre[]>(baseURL + 'offres');
  }

  getOffreById(id: string): Observable<Offre> {
    return this.httpClient.get<Offre>(baseURL + 'offres' + id)
  }

  createOffre(Offre: Offre): Observable<Offre> {
    return this.httpClient.post<Offre>(baseURL + 'offres', Offre, {});
  }

  getLogoName(file: File): Observable<any> {
    const formData = new FormData();
    formData.append('logo', file)
    console.log("SERVICE", formData);
    return this.httpClient.put<any>(baseURL + 'offres', formData, {});
  }

  getOffreByUniversiteId(universiteId: string): Observable<Offre[]> {
    return this.httpClient.get<Offre[]>(baseURL + 'offres/universites/' + universiteId);
  }

  updateOffre(id: string, Offre: Offre): Observable<Offre> {
    return this.httpClient.put<Offre>(baseURL + 'offres/' + id, Offre);
  }
  //Les methodes de cration ici
telecharger(file: File): Observable<File> {
    let formData = new FormData();
    formData.append('fichier', file);
    console.log(formData);
    return this.httpClient.post<File>(baseURL + "offres/fichiers", formData);
  }
}
