import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ClasseService } from '../modules/services/classe.service';
import { DepartementService } from '../modules/services/departement.service';
import { ParcoursService } from '../modules/services/parcours.service';
import { SujetEtCorrectionService } from '../modules/services/sujet-et-correction.service';
import { UniversiteService } from '../modules/services/universite.service';
import { baseURL } from '../modules/shared/baseurl';
import { Departement } from '../modules/shared/departement';
import { Universite } from '../modules/shared/universite';

@Component({
  selector: 'app-sujet-et-correction',
  templateUrl: './sujet-et-correction.component.html',
  styleUrls: ['./sujet-et-correction.component.scss']
})
export class SujetEtCorrectionComponent implements OnInit {

  idUniversite: string = '';
  universite: Universite;
  departements: Departement[] = [];
  parcours: any = {};
  classes: any = {};
  sujetEtCorrections: any = {};
  baseURL: string = baseURL;
  loading: boolean = true;
  constructor(
    private universiteService: UniversiteService,
    private route: ActivatedRoute,
    private departementService: DepartementService,
    private classeService: ClasseService,
    private parcoursService: ParcoursService,
    private sujetEtCorrectionService: SujetEtCorrectionService
  ) { }

  ngOnInit(): void {
    this.idUniversite = this.route.snapshot.params['id'];
    this.universiteService
        .getUniversiteById( this.idUniversite )
        .subscribe( 
          universite => this.universite = universite,
          error => {},
          () => this.loading = false 
        );

    this.departementService
        .getDepartementByUniversiteById( this.idUniversite )
        .subscribe( departements => {
          this.departements = departements;
          this.departements.forEach( departement => {
            this.parcoursService
                .getParcoursByDepartementId( departement._id )
                .subscribe(parcours => {
                  this.parcours[departement.nom] = parcours;
                  console.log(this.parcours)
                  parcours.forEach( prs => {
                    this.classeService
                      .getClasseByParcoursId( prs._id )
                      .subscribe( classes => {
                        this.classes[prs.nom] = classes;
                        classes.forEach(classe => {
                          this.sujetEtCorrectionService
                              .getSujetEtCorrectionByClasseId( classe._id )
                              .subscribe( sujetEtCorrection => {
                                this.sujetEtCorrections[classe.nom]=sujetEtCorrection
                              } )
                        } )
                      } )
                  } )
                })
          } )
        })
    
  }

}
