import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { baseURL } from '../shared/baseurl';
import { Distinction } from '../shared/distinction';

@Injectable({
  providedIn: 'root'
})
export class DistinctionService {
  constructor(private httpClient: HttpClient) { }
  getAllDistinction(): Observable<Distinction[]> {
    return this.httpClient.get<Distinction[]>(baseURL + 'distinctions');
  }

  getDistinctionById(id: string): Observable<Distinction> {
    return this.httpClient.get<Distinction>(baseURL + 'distinctions/' + id)
  }

  createDistinction(distinction: Distinction): Observable<Distinction> {
    return this.httpClient.post<Distinction>(baseURL + 'distinctions', distinction, {});
  }

  updateDistinction(id: string, distinction: Distinction): Observable<Distinction> {
    return this.httpClient.put<Distinction>(baseURL + 'distinctions/' + id, distinction);
  }
  //Les methodes de cration ici
  telecharger(file: File): Observable<File> {
    let formData = new FormData();
    formData.append('fichier', file);
    console.log(formData);
    return this.httpClient.put<File>(baseURL + "distinctions", formData);
  }
  getDistinctionByUniversiteId( idUniversite: string ): Observable<Distinction[]>{
    return this.httpClient.get<Distinction[]>( baseURL + 'distinctions/universites/' + idUniversite );
  }
}
