import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { baseURL } from '../shared/baseurl';
import { Section } from '../shared/section';

@Injectable({
  providedIn: 'root'
})
export class SectionService {

  constructor(private httpClient: HttpClient) { }

  getAllSections(): Observable<Section[]> {
    return this.httpClient.get<Section[]>(baseURL + 'sections');
  }
  getSectionById(idSection: string): Observable<Section> {
    return this.httpClient.get<Section>(baseURL + 'sections/' + idSection);
  }

  createSection(section: Section, file: File): Observable<Section> {
    if (file != undefined) {
      section.fichier = 'images/ressources/' + file.name;
      let formData = new FormData();
      formData.append('fichier', file);
    }
    return this.httpClient.post<Section>(baseURL + 'sections', section);
  }

  updateSection(idSection: string, section: Section): Observable<Section> {
    return this.httpClient.put<Section>(baseURL + 'sections/' + idSection, section)
  }
  getSectionByChapitreId(idChapitre: string): Observable<Section[]> {
    return this.httpClient.get<Section[]>(baseURL + 'sections/chapitres/' + idChapitre);
  }

  deleteSection(idSection: string): Observable<Section> {
    return this.httpClient.delete<Section>(baseURL + 'sections/' + idSection);
  }

  //Les methodes de cration ici
  public telecharger(file: File): Observable<string> {
    let formData = new FormData();
    formData.append('fichier', file);
    console.log(formData);
    return this.httpClient.put<string>(baseURL + "sections", formData);
  }
}

