import { Component, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { CadreAdministratifService } from '../../services/cadre-administratif.service';
import { UniversiteService } from '../../services/universite.service';
import { CadreAdministratif } from '../../shared/cadreAdministratif';
import { Universite } from '../../shared/universite';

@Component({
  selector: 'app-modifier-cadre-administratif',
  templateUrl: './modifier-cadre-administratif.component.html',
  styleUrls: ['./modifier-cadre-administratif.component.scss']
})
export class ModifierCadreAdministratifComponent implements OnInit {
  @ViewChild('fform', { static: false }) cadreFormDirective: any;
  cadreForm: FormGroup;

  universite: Universite;
  cadre: CadreAdministratif;
  cadreUpdate: CadreAdministratif;
  loading: boolean = false;

  
  
  idCadre: string = '';

  constructor(
    private formBuilder: FormBuilder,
    private router: Router,
    private cadreService: CadreAdministratifService,
    private universiteService: UniversiteService,
    private route: ActivatedRoute
  ) {
    this.createForm();
  }

  ngOnInit(): void {
    this.idCadre = this.route.snapshot.params['id'];

  
    this.cadreService
      .getCadreAdministratifById(this.idCadre)
      .subscribe(cadre => {
        this.cadreUpdate = cadre;
        var u = this.cadreUpdate.universite as Universite;
        this.universiteService
        .getUniversiteById( u._id )
        .subscribe( u => this.universite = u )
      });
  }

  createForm() {
    this.cadreForm = this.formBuilder.group({
      titre: '',
      fonction: '',
      nom: '',
      prenom: ''
    });
  }
  onSubmit() {
    this.cadre = this.cadreForm?.value;
    this.loading = true;
    this.cadreService
            .updateCadreAdministratif(this.idCadre, this.cadre)
            .subscribe(cadre => {},
              err => { },
              () => {
                setTimeout(() => { this.loading = false ;}, 1800);
                this.router.navigateByUrl('ajouter-cadre/' + this.cadreUpdate.universite)
               
              }
            )
    }
}
