import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { UniversiteService } from '../../services/universite.service';
import { Universite } from '../../shared/universite';
import { baseURL } from '../../shared/baseurl';
import { DepartementService } from '../../services/departement.service';
import { Observable } from 'rxjs';
import { Departement } from '../../shared/departement';
import { MatDialog } from '@angular/material/dialog';
import { SupprimerDepartementComponent } from '../supprimer-departement/supprimer-departement.component';

@Component({
  selector: 'app-universite-detail',
  templateUrl: './universite-detail.component.html',
  styleUrls: ['./universite-detail.component.scss']
})
export class UniversiteDetailComponent implements OnInit {

  universite?: Universite;
  loading: boolean = false;
  baseURL = baseURL;
  departements$?: Observable<Departement[]>;
  idUniversite: string = '';
  constructor(
    private universiteService: UniversiteService,
    private activatedRoute: ActivatedRoute,
    private departementService: DepartementService,
    private dialog: MatDialog
  ) { }

  ngOnInit(): void {
    this.idUniversite = this.activatedRoute.snapshot.params['id'];
    this.loading = true;
    this.departements$ = this.departementService.getDepartementByUniversiteById(this.idUniversite);
    this.universiteService
      .getUniversiteById(this.idUniversite)
      .subscribe(universite => { this.loading = false; this.universite = universite })
  }

  supprimerDepartementModal(idDepartement: string) {
    this.dialog.open( SupprimerDepartementComponent, {
      position: {
        top: '8%'
      },
      width: '30%',
      height: '30%',
      data: {
        idDepartement: idDepartement,
        idUniversite: this.idUniversite
      }
    } )
  }

}
