import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { baseURL } from '../shared/baseurl';
import { Compte } from '../shared/compte';
import { catchError, map } from 'rxjs/operators';
@Injectable({
  providedIn: 'root'
})
export class CompteService {

  constructor(private httpClient: HttpClient) { }

  createCompte(compte: Compte): Observable<Compte> {
    return this.httpClient.post<Compte>(baseURL + 'auth/register', compte);
  }
  verifyCompte(compte?: Compte): Observable<any> {
    return this.httpClient.post<any>(baseURL + 'auth/verify-user', compte);
  }
  verifyEmail(email?: string): Observable<any> {
    return this.httpClient.get<any>(baseURL + 'auth/verify-email/' + email);
  }

  resetPassword(email?: string, compte?: Compte): Observable<any> {
    return this.httpClient.put<any>(baseURL + 'auth/reset-password/' + email, compte);
  }

  getCompteById(idCompte?: string): Observable<Compte> {
    return this.httpClient.get<Compte>(baseURL + 'auth/comptes/' + idCompte);
  }
  updateCompte( idCompte: string, compte: Compte ): Observable<Compte> {
    return this.httpClient.put<Compte>( baseURL + 'auth/' + idCompte, compte );
  }
}
