import { Component, OnInit, Inject } from '@angular/core';
import {MatDialog, MAT_DIALOG_DATA} from '@angular/material/dialog';

@Component({
  selector: 'app-verifier-dossier-modal',
  templateUrl: './verifier-dossier-modal.component.html',
  styleUrls: ['./verifier-dossier-modal.component.scss']
})
export class VerifierDossierModalComponent implements OnInit {
  fichier: string ='';
  constructor(
    public dialogRef: MatDialog,
    @Inject(MAT_DIALOG_DATA) public data: any) {}

  ngOnInit(): void {
    this.fichier = this.data.fichier;
  }

  onNoClick(): void {
    this.dialogRef.closeAll();
  }

}
