import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { PresentationService } from '../modules/services/presentation.service';
import { UniversiteService } from '../modules/services/universite.service';
import { Presentation } from '../modules/shared/presentation';
import { Universite } from '../modules/shared/universite';
import { flyInOut, expand } from 'animations/app.animation';

@Component({
  selector: 'app-presentation-universite',
  templateUrl: './presentation-universite.component.html',
  styleUrls: ['./presentation-universite.component.scss'],
  host: {
    '[@flyInOut]': 'true',
    'style': 'display: block;'
  },
  animations: [
    flyInOut(),
    expand()
  ]
})
export class PresentationUniversiteComponent implements OnInit {
  
  universite: Universite;
  idUniversite: string = '';
  presentations?: Presentation[];
  loading: boolean = true;
  constructor(
    private route: ActivatedRoute,
    private universiteService: UniversiteService,
    private presentationService: PresentationService
  ) { }

  ngOnInit(): void {
    this.idUniversite = this.route.snapshot.params['id'];
    this.universiteService
        .getUniversiteById( this.idUniversite )
        .subscribe( 
          universite => this.universite = universite,
          error => {},
          ()=> this.loading = false
        );
    
    this.presentationService
        .getPresentationByUniversiteId( this.idUniversite )
        .subscribe( presentations => this.presentations = presentations );
  }

}
