import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { from, Observable, of } from 'rxjs';
import { baseURL } from '../shared/baseurl';
import { Universite } from '../shared/universite';


@Injectable({
  providedIn: 'root'
})
export class UniversiteService {

  constructor(private httpClient: HttpClient) { }
  getAllUniversities(): Observable<Universite[]> {
    return this.httpClient.get<Universite[]>(baseURL + 'universites');
  }

  getUniversiteById(id: string): Observable<Universite> {
    return this.httpClient.get<Universite>(baseURL + 'universites/' + id)
  }

  createUniversity(universite: Universite): Observable<Universite> {
    return this.httpClient.post<Universite>(baseURL + 'universites', universite, {});
  }

  getLogoName(file: File): Observable<any> {
    const formData = new FormData();
    formData.append('logo', file)
    return this.httpClient.put<any>(baseURL + 'universites', formData, {});
  }

  createImage(file: File): Observable<any> {
    const formData = new FormData();
    formData.append('image', file)
    return this.httpClient.post<any>(baseURL + 'universites/images', formData, {});
  }

  getUniversityByType(type: string): Observable<Universite[]> {
    return this.httpClient.get<Universite[]>(baseURL + 'universites/types/' + type);
  }

  updateUniversite(id: string, universite: Universite): Observable<Universite> {
    return this.httpClient.put<Universite>(baseURL + 'universites/' + id, universite);
  }

}
