import { Component, Inject, OnChanges, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import jwt_decode from 'jwt-decode';
import { ActivatedRoute, Router } from '@angular/router';
import { Observable } from 'rxjs';
import { Section } from '../../shared/section';
import { SectionService } from '../../services/section.service';

@Component({
  selector: 'app-update-section',
  templateUrl: './update-section.component.html',
  styleUrls: ['./update-section.component.scss']
})
export class UpdateSectionComponent implements OnInit {
  @ViewChild('fform', { static: false }) sectionFormDirective: any;
  sectionForm: FormGroup;
  selectedFile: File;

  section: Section;
  sectionUpdate: Section;
  loading: boolean = false;

  formErrors: any = {
    titre: '',
    contenu: '',
    video: '',
  };

  validationMessages: any = {
    titre: {
      required: 'le titre  est requis.',
      minlength: 'le titre tre plus que 4 caractres.',
      maxlength: 'le titre  ne doit pas tre plus que 150 caractres.'
    },
    contenu: {
      required: 'le contenu  est requis.',
      minlength: 'le contenu tre plus que 4 caractres.',
      maxlength: 'le contenu  ne doit pas tre plus que 1000 caractres.'
    },
    video: {
      required: 'la vido est requise.',
      minlength: 'la vido tre plus que 4 caractres.',
      maxlength: 'la vido ne doit pas tre plus que 1000 caractres.'
    },
  };

  idSection: string = '';



  constructor(
    private formBuilder: FormBuilder,
    private router: Router,
    private sectionService: SectionService,
    private route: ActivatedRoute
  ) {
    this.createForm();
  }

  ngOnInit(): void {
    this.idSection = this.route.snapshot.params['id'];
    this.sectionService
      .getSectionById(this.idSection)
      .subscribe(section => this.sectionUpdate = section);
  }

  createForm() {
    this.sectionForm = this.formBuilder.group({
      titre: ['', [Validators.required, Validators.minLength(4), Validators.maxLength(150)]],
      contenu: ['', [Validators.required, Validators.minLength(4), Validators.maxLength(1000)]],
      video: ['', [Validators.required, Validators.minLength(4), Validators.maxLength(150)]],
      fichier: null
    });
    this.sectionForm
      .valueChanges
      .subscribe(data => this.onValueChanged(data));
    this.onValueChanged();
  }

  onValueChanged(data?: any) {
    if (!this.sectionForm) { return; }
    const form = this.sectionForm;
    for (const field in this.formErrors) {
      if (this.formErrors.hasOwnProperty(field)) {
        // clear previous error message (if any)
        this.formErrors[field] = '';
        const control = form.get(field);
        if (control && control.dirty && !control.valid) {
          const messages = this.validationMessages[field];
          for (const key in control.errors) {
            if (control.errors.hasOwnProperty(key)) {
              this.formErrors[field] += messages[key] + ' ';
            }
          }
        }
      }
    }
  }

  ngOnChanges() {
    this.idSection = this.route.snapshot.params['id'];
  }

  onSubmit() {
    this.section = this.sectionForm?.value;
    if (this.selectedFile != null)
      this.section.fichier = "images/ressources/" + this.selectedFile.name;
    var valeur = this.section.video.substr(this.section.video.indexOf("=") + 1)
    var autre: string = "";
    if(valeur.indexOf("&")>=0) {
        autre = valeur.substr(0, valeur.indexOf("&"));
        this.section.video = autre;
      } else
        this.section.video = valeur;
    

    this.loading = true;

    this.sectionService
      .updateSection(this.idSection, this.section)
      .subscribe(data => {});
    this.sectionService
      .telecharger(this.selectedFile)
      .subscribe(
        data => {  },
        error => {  },
        () => {
          this.loading = false;
          this.router.navigateByUrl('/facultes')
        }
      )

  }


  getDecodedAccessToken(token: string): any {
    try {
      return jwt_decode(token);
    }
    catch (Error) {
      return null;
    }
  }

  reloadPage() {
    setTimeout(() => {
      window.location.reload();
    }, 100);
  }

  fileChanged(event: any) {
    this.selectedFile = event.target.files[0];
  }


}

