import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ClasseService } from '../../services/classe.service';
import { FiliereService } from '../../services/filiere.service';
import { Classe } from '../../shared/classe';
import { Filiere } from '../../shared/filiere';

@Component({
  selector: 'app-detail-filiere',
  templateUrl: './detail-filiere.component.html',
  styleUrls: ['./detail-filiere.component.scss']
})
export class DetailFiliereComponent implements OnInit {
  loading: boolean = false;
  filiere: Filiere;
  idFiliere: string = '';
  classes?: Classe[];
  constructor(
    private FiliereService: FiliereService,
    private route: ActivatedRoute,
    private classeService: ClasseService
  ) { }

  ngOnInit(): void {
    this.idFiliere = this.route.snapshot.params['id'];
    this.FiliereService
      .getFiliereById(this.idFiliere)
      .subscribe(filiere => this.filiere = filiere);
    this.loading = true;
    this.getAllClasses();
  }

  getAllClasses() {
    this.loading = true;
    this.classeService
      .getClasseByFiliereId(this.idFiliere)
      .subscribe(
        classes => { this.classes = classes },
        error => { },
        () => { this.loading = false; }
      )
  }

}
