import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { UniversiteService } from '../modules/services/universite.service';
import { VieEstidiantineService } from '../modules/services/vie-estidiantine.service';
import { baseURL } from '../modules/shared/baseurl';
import { Universite } from '../modules/shared/universite';
import { VieEstidiantine } from '../modules/shared/vieEstidiantine';
import { flyInOut, expand } from 'animations/app.animation';

@Component({
  selector: 'app-list-vie-de-campus',
  templateUrl: './list-vie-de-campus.component.html',
  styleUrls: ['./list-vie-de-campus.component.scss'],
  host: {
    '[@flyInOut]': 'true',
    'style': 'display: block;'
  },
  animations: [
    flyInOut(),
    expand()
  ]
})
export class ListVieDeCampusComponent implements OnInit {

  type: string;
  type2: string;
  idUniversite?: string;
  universite?: Universite;
  vieEstidiantines: VieEstidiantine[] = [];
  baseURL: string = baseURL;
  loading: boolean = true;
  constructor(
    private route: ActivatedRoute,
    private universiteService: UniversiteService,
    private vieEstidiantineService: VieEstidiantineService
  ) { }

  ngOnInit(): void {
    this.type = this.route.snapshot.params['type'];
    this.vieEstidiantineService
        .getUniversityByType( this.type )
        .subscribe( vieEstidiantines => {
          this.vieEstidiantines = vieEstidiantines;
        })
    this.idUniversite = localStorage.getItem('universite')?.toString() as string;
    this.universiteService
    .getUniversiteById( this.idUniversite )
    .subscribe( 
      universite => this.universite = universite,
      error => {},
      () => this.loading = false
    );
  }

  addItem(newItem: string) {
    this.type = this.route.snapshot.params['type'];
    this.vieEstidiantineService
        .getUniversityByType( this.type )
        .subscribe( vieEstidiantines => {
          this.vieEstidiantines = vieEstidiantines;
        })
  }

}
