import { Component, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { CalendrierService } from '../../services/calendrier.service';
import { ParcoursService } from '../../services/parcours.service';
import { Calendrier } from '../../shared/calendrier';
import { Parcours } from '../../shared/parcours';

@Component({
  selector: 'app-ajouter-calendrier',
  templateUrl: './ajouter-calendrier.component.html',
  styleUrls: ['./ajouter-calendrier.component.scss']
})
export class AjouterCalendrierComponent implements OnInit {
  @ViewChild('fform', { static: false }) calendrierFormDirective: any;
  calendrierForm: FormGroup;
  idParcours: string = "";
  parcours: Parcours;
  fichierFile: File;
  calendrier: Calendrier;


  constructor(
    private formBuilder: FormBuilder,
    private parcoursService: ParcoursService,
    private route: ActivatedRoute,
    private router: Router,
    private calendrierService: CalendrierService
  ) {}

  ngOnInit() {
    this.idParcours = this.route.snapshot.params['id'];

    this.parcoursService
      .getParcoursById(this.idParcours)
      .subscribe(parcours => {
        this.parcours = parcours;
      })

    this.calendrierForm = this.formBuilder.group({
      titre: '',
      anne:'',
      fichier: '',
      parcours: ''
    });
  }

  onSubmit() {
    this.calendrier = this.calendrierForm.value;
    this.calendrier.parcours = this.parcours;
    this.calendrier.fichier = 'images/calendrier/' +  this.fichierFile.name;

    this.calendrierService
        .createFichier( this.fichierFile )
        .subscribe( fichier => {
          this.calendrierService
              .createCalendrier( this.calendrier )
              .subscribe( calendrier => {
                this.router.navigateByUrl('calendrier-detail/' + calendrier._id)
              }  );
        } )
   
  }

  

  fileChanged(event: any) {
    this.fichierFile = event.target.files[0];
  }
}