import { Component, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { InscriptionEnLigneService } from '../../services/inscription-en-ligne.service';
import { UniversiteService } from '../../services/universite.service';
import { InscriptionEnLigne } from '../../shared/inscriptionEnLigne';
import { Universite } from '../../shared/universite';

@Component({
  selector: 'app-modifier-presentation-en-ligne',
  templateUrl: './modifier-presentation-en-ligne.component.html',
  styleUrls:  ['./modifier-presentation-en-ligne.component.scss']
})
export class ModifierPresentationEnLigneComponent implements OnInit {
  @ViewChild('fform', { static: false }) inscriptionFormDirective: any;
  inscriptionForm: FormGroup;

  universite: Universite;
  inscriptionUpdate: InscriptionEnLigne;
  inscription: InscriptionEnLigne;
  loading: boolean = false;

  formErrors: any = {
    titre: '',
    description: ''
  };

  validationMessages: any = {
    titre: {
      required: 'le titre  est requis.',
      minlength: 'le titre tre plus que 4 caractres.'
    },
    description: {
      required: 'la description est requise.',
      minlength: 'la description tre plus que 6 caractres.'
    }
  };

  idInscription: string = '';

  constructor(
    private formBuilder: FormBuilder,
    private router: Router,
    private inscriptionService: InscriptionEnLigneService,
    private universiteService: UniversiteService,
    private route: ActivatedRoute
  ) {
    this.createForm();
  }

  ngOnInit(): void {
    this.idInscription = this.route.snapshot.params['id'];

    this.inscriptionService
      .getInscriptionEnLigneById(this.idInscription)
      .subscribe(inscription => {
        this.inscriptionUpdate = inscription;
        var u = this.inscriptionUpdate.universite as Universite;
        this.universiteService
        .getUniversiteById( u._id )
        .subscribe( u => this.universite = u )
      });
  }

  createForm() {
    this.inscriptionForm = this.formBuilder.group({
      titre: '',
      description: ''
    });
    this.inscriptionForm
      .valueChanges
      .subscribe(data => this.onValueChanged(data));
    this.onValueChanged();
  }

  onValueChanged(data?: any) {
    if (!this.inscriptionForm) { return; }
    const form = this.inscriptionForm;
    for (const field in this.formErrors) {
      if (this.formErrors.hasOwnProperty(field)) {
        // clear previous error message (if any)
        this.formErrors[field] = '';
        const control = form.get(field);
        if (control && control.dirty && !control.valid) {
          const messages = this.validationMessages[field];
          for (const key in control.errors) {
            if (control.errors.hasOwnProperty(key)) {
              this.formErrors[field] += messages[key] + ' ';
            }
          }
        }
      }
    }
  }


  onSubmit() {
    this.inscription = this.inscriptionForm?.value;
    this.loading = true;
    
    this.inscriptionService
            .updateInscriptionEnLigne(this.idInscription, this.inscription)
            .subscribe(univ => {},
              err => { },
              () => {
                setTimeout(() => { this.loading = false }, 1800);
                this.router.navigateByUrl('ajouter-inscription/' + this.inscriptionUpdate.universite)
              }
            )
    }
}
