import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { CalendrierService } from '../modules/services/calendrier.service';
import { UniversiteService } from '../modules/services/universite.service';
import { baseURL } from '../modules/shared/baseurl';
import { Calendrier } from '../modules/shared/calendrier';
import { Universite } from '../modules/shared/universite';
import { flyInOut, expand } from 'animations/app.animation';
@Component({
  selector: 'app-calendrier-detail',
  templateUrl: './calendrier-detail.component.html',
  styleUrls: ['./calendrier-detail.component.scss'],
  host: {
    '[@flyInOut]': 'true',
    'style': 'display: block;'
  },
  animations: [
    flyInOut(),
    expand()
  ]
})
export class CalendrierDetailComponent implements OnInit {

  calendrier: Calendrier;
  idCalendrier: string = '';
  idUniversite: string = '';
  universite: Universite;
  baseURL: string = baseURL;
  heigth: number = 0;
  width: number = 0;
  constructor(
    private calendrierService: CalendrierService,
    private universiteService: UniversiteService,
    private route: ActivatedRoute
  ) { }

  ngOnInit(): void {
    this.heigth = screen.height -100;
    this.width = screen.width - 100;

    this.idCalendrier = this.route.snapshot.params['id'];

    this.calendrierService
        .getCalendrierById( this.idCalendrier )
        .subscribe( calendrier => this.calendrier = calendrier );

    this.calendrierService
        .getCalendrierByParcoursId(this.idCalendrier)
        .subscribe( calendrier => this.calendrier = calendrier );
    this.idUniversite = localStorage.getItem('universite')?.toString() as string;
    this.universiteService
        .getUniversiteById( this.idUniversite  )
        .subscribe( universite => this.universite = universite )
    
  }

}
