import { Component, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { Departement } from '../../shared/departement';
import { DepartementService } from '../../services/departement.service';
import { Parcours } from '../../shared/parcours';
import { ParcoursService } from '../../services/parcours.service';

@Component({
  selector: 'app-ajouter-parcours',
  templateUrl: './ajouter-parcours.component.html',
  styleUrls: ['./ajouter-parcours.component.scss']
})
export class AjouterParcoursComponent implements OnInit {


  @ViewChild('fform', { static: false }) parcoursFormDirective: any;
  parcoursForm: FormGroup;

  departement: Departement;
  parcours: Parcours;
  loading: boolean = false;
  idDepartement: string = '';

  formErrors: any = {
    nom: '',
    description: ''
  };

  validationMessages: any = {
    nom: {
      required: 'le nom  est requis.',
      minlength: 'le nom tre plus que 2 caractres.',
      maxlength: 'le nom  ne doit pas tre plus que 150 caractres.'
    },
    description: {
      required: 'la description est requise.',
      minlength: 'la description tre plus que 10 caractres.',
      maxlength: 'la description ne doit pas tre plus que 1000 caractres.'
    }
  };


  constructor(
    private formBuilder: FormBuilder,
    private router: Router,
    private route: ActivatedRoute,
    private departementService: DepartementService,
    private parcoursService: ParcoursService
  ) {
    this.createForm();
  }

  ngOnInit(): void {
    this.idDepartement = this.route.snapshot.params['id'];
    this.departementService
      .getDepartementById(this.idDepartement)
      .subscribe(departement => this.departement = departement);
  }

  createForm() {
    this.parcoursForm = this.formBuilder.group({
      nom: ['', [Validators.required, Validators.minLength(2), Validators.maxLength(150)]],
      description: ['', [Validators.required, Validators.minLength(10), Validators.maxLength(1000)]]
    });
    this.parcoursForm
      .valueChanges
      .subscribe(data => this.onValueChanged(data));
    this.onValueChanged();
  }

  onValueChanged(data?: any) {
    if (!this.parcoursForm) { return; }
    const form = this.parcoursForm;
    for (const field in this.formErrors) {
      if (this.formErrors.hasOwnProperty(field)) {
        // clear previous error message (if any)
        this.formErrors[field] = '';
        const control = form.get(field);
        if (control && control.dirty && !control.valid) {
          const messages = this.validationMessages[field];
          for (const key in control.errors) {
            if (control.errors.hasOwnProperty(key)) {
              this.formErrors[field] += messages[key] + ' ';
            }
          }
        }
      }
    }
  }

  ngOnChanges() {
    this.idDepartement = this.route.snapshot.params['id'];
  }

  onSubmit() {
    this.parcours = this.parcoursForm?.value;
    //this.parcours.departement = this.departement;
    this.loading = true;
    this.parcoursService
      .createParcours(this.parcours)
      .subscribe(parcours => {
        this.departement.parcours.push( parcours );
        this.departementService
            .updateDepartement( this.departement._id, this.departement )
            .subscribe( departement => {} );
      },
        err => { },
        () => {
          this.loading = false;
          this.router.navigateByUrl('/departement-detail/' +  this.idDepartement);
        }
      )
  }
  reloadPage() {
    setTimeout(() => {
      window.location.reload();
    }, 100);
  }

}
