import { Component, OnInit } from '@angular/core';
import { UniversiteService } from '../../services/universite.service';
import { Universite } from '../../shared/universite';

@Component({
  selector: 'app-institut',
  templateUrl: './institut.component.html',
  styleUrls: ['./institut.component.scss']
})
export class InstitutComponent implements OnInit {

  instituts?: Universite[];

  constructor(private universiteService: UniversiteService) { }

  ngOnInit(): void {
    this.getFacultes('institut');
  }

  getFacultes(type: string) {
    this.universiteService
      .getUniversityByType(type)
      .subscribe(instituts => { this.instituts = instituts; });
  }


}
