import { Component, OnInit } from '@angular/core';
import { Classe } from './../../shared/classe';
import { ClasseService } from './../../services/classe.service';
import { ActivatedRoute } from '@angular/router';
import { Dossier } from './../../shared/dossier';
import { DossierService } from './../../services/dossier.service';
import { Etudiant } from './../../shared/etudiant';
import { EtudiantService } from './../../services/etudiant.service';

@Component({
  selector: 'app-list-candidat',
  templateUrl: './list-candidat.component.html',
  styleUrls: ['./list-candidat.component.scss']
})
export class ListCandidatComponent implements OnInit {
  loading: boolean = false;
  classe?: Classe;
  idClasse?: string;
  errorClasse?: any;
  dossiers: Dossier[] = [];
  etudiants: Etudiant[] = [];
  constructor(
    private classeService: ClasseService,
    private dossierService: DossierService,
    private etudiantService: EtudiantService,
    private route: ActivatedRoute
  ) { }

  ngOnInit(): void {
    this.loading = true;
    this.idClasse = this.route.snapshot.params['id'];
    this.classeService
        .getClasseById( this.idClasse )
        .subscribe( 
          classe => this.classe = classe,
          error => this.errorClasse = error,
          () => this.loading = false
         );
         
    this.dossierService
        .getDossierByClasseId( this.idClasse )
        .subscribe( 
          dossiers => this.dossiers = dossiers,
          error => this.errorClasse = error,
          () => this.loading = false
         );
    this.etudiantService
        .getAllEtudiant()
        .subscribe(
          etudiants => this.etudiants = etudiants,
          error => this.errorClasse = error,
          () => this.loading = false
        )
  }

  getStudentByCompteId( compteId: string ): Etudiant {
    return this.etudiants.filter( etudiant => etudiant.compte._id === compteId )[0];
  }

}
