import { Component, OnInit } from '@angular/core';
import { Universite } from './../../shared/universite';
import { UniversiteService } from './../../services/universite.service';
import { Departement } from './../../shared/departement';
import { Dossier } from './../../shared/dossier';
import { DossierService } from './../../services/dossier.service';
import { Etudiant } from './../../shared/etudiant';
import { EtudiantService } from './../../services/etudiant.service';

@Component({
  selector: 'app-candidat-accepter',
  templateUrl: './candidat-accepter.component.html',
  styleUrls: ['./candidat-accepter.component.scss']
})
export class CandidatAccepterComponent implements OnInit {
  loading: boolean = false;
  universites: Universite[] = [];
  departement?: Departement;
  univError?: any;
  dossiers: Dossier[] = [];
  etudiants: Etudiant[] = [];

  constructor(
    private universiteService: UniversiteService,
    private dossierService: DossierService,
    private etudiantService: EtudiantService
  ) { }

  ngOnInit(): void {
    this.loading = true;
    this.universiteService
        .getAllUniversities()
        .subscribe( 
          universites => this.universites = universites,
          error => this.univError = error,
          () => { this.loading = false; }
         );

    this.dossierService
        .getAllDossiers()
        .subscribe( 
          dossiers => this.dossiers = dossiers,
          error => this.univError = error,
          () => { this.loading = false; }
         );
  this.etudiantService
         .getAllEtudiant()
         .subscribe( 
           etudiants =>this.etudiants = etudiants,
           error => this.univError = error,
           () => { this.loading = false; }
          );
  }

  selectDepartement( departement: Departement ) {
    this.departement = departement
  }

  getDossierByClasses( idClasse: string ): Etudiant[]{
  var etudiantInscris: Etudiant[] = [];
   this.etudiants.map(etudiant => {
     if( etudiant.classe != null ) {
       if( etudiant.classe._id == idClasse ) {
         etudiantInscris.push(etudiant);
       }
     }
   });
   return etudiantInscris;
  }

}
