import { Component, Inject, OnChanges, OnInit, ViewChild } from '@angular/core';
import { PLANSITE } from '../modules/shared/plansite';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Compte } from '../modules/shared/compte';
import { AuthService } from '../services/auth.service';
import jwt_decode from 'jwt-decode';
import { Router } from '@angular/router';
import { EtudiantService } from '../modules/services/etudiant.service';
import { EnseignantService } from '../modules/services/enseignant.service';
import { CompteService } from '../modules/services/compte.service';
import { MatDialog } from '@angular/material/dialog';
import { flyInOut, expand } from 'animations/app.animation';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss'],
  host: {
    '[@flyInOut]': 'true',
    'style': 'display: block;'
  },
  animations: [
    flyInOut(),
    expand()
  ]
})
export class LoginComponent implements OnInit, OnChanges {
  @ViewChild('fform', { static: false }) compteFormDirective: any;
  compteForm: FormGroup;

  compte?: Compte;
  loading: boolean = false;
  formErrors: any = {
    'username': '',
    'password': ''
  };

  validationMessages: any = {
    'username': {
      'required': 'le username  est requis.',
      'minlength': 'le username tre plus que 4 caractres.',
      'maxlength': 'le nom l\'auteur ne doit pas tre plus que 150 caractres.'
    },
    'password': {
      'required': 'le mot de passe est requis.',
      'minlength': 'le mot de passe tre plus que 6 caractres.',
      'maxlength': 'le mot de passe ne doit pas tre plus que 150 caractres.'
    }
  };

  compteId?: string;
  msgError?: string;

  plansite = PLANSITE;
  constructor(
    private formBuilder: FormBuilder,
    private authService: AuthService,
    private router: Router,
    private etudiantSerice: EtudiantService,
    private enseignantService: EnseignantService,
    private compteService: CompteService,
    private matDiag: MatDialog
  ) {
    this.createForm();
  }

  ngOnInit(): void {
  }

  createForm() {
    this.compteForm = this.formBuilder.group({
      username: ['', [Validators.required, Validators.minLength(4), Validators.maxLength(150)]],
      password: ['', [Validators.required, Validators.minLength(4), Validators.maxLength(150)]]
    });
    this.compteForm
      .valueChanges
      .subscribe(data => this.onValueChanged(data));
    this.onValueChanged();
  }

  onValueChanged(data?: any) {
    if (!this.compteForm) { return; }
    const form = this.compteForm;
    for (const field in this.formErrors) {
      if (this.formErrors.hasOwnProperty(field)) {
        // clear previous error message (if any)
        this.formErrors[field] = '';
        const control = form.get(field);
        if (control && control.dirty && !control.valid) {
          const messages = this.validationMessages[field];
          for (const key in control.errors) {
            if (control.errors.hasOwnProperty(key)) {
              this.formErrors[field] += messages[key] + ' ';
            }
          }
        }
      }
    }
  }

  ngOnChanges() {
    this.onSubmit();
  }

  closeModal() {
    this.router.navigateByUrl('reset-password');
    this.matDiag.closeAll()
  }

  onSubmit() {
    this.compte = this.compteForm?.value;
    const user = this.compte?.username;

    this.compteService
      .verifyCompte(this.compte)
      .subscribe(
        compte => {

          this.authService
            .login(this.compte)
            .subscribe(data => {
              let d = this.getDecodedAccessToken(data.token);
              localStorage.setItem('token', data.token);
              localStorage.setItem('exp', d.exp);
              localStorage.setItem('username', d.username);
              localStorage.setItem('roles', d.roles.join(','))
              localStorage.setItem('_id', d._id)
              localStorage.setItem('admin', d.admin);
              this.loading = true;

              this.compteId = localStorage.getItem('_id')?.toString();
              if (this.compteId != undefined) {
                this.etudiantSerice
                  .getEtudiantByCompteId(this.compteId)
                  .subscribe(data => {
                    if (data != null) {
                      localStorage.setItem('avatar', data.avatar); this.reloadPage();
                    }
                  }, error => { this.msgError = error.message; })

                this.enseignantService
                  .getEnseignantByCompteId(this.compteId)
                  .subscribe(data => {
                    if (data != null) { localStorage.setItem('avatar', data.avatar); this.reloadPage(); }
                  }, error => { this.msgError = error.message; })
              }

              if( d.admin ) {
                this.router.navigateByUrl('/facultes');
              }  else {
                this.router.navigateByUrl('/');
              }
            }, error => {
              if (error != null) {
                this.msgError = "L'utilisateur " + user + " n'existe pas";
                this.loading;
              }
            },
              () => {
                setTimeout(() => { this.loading = false; }, 2000)
              }
            )

        },
        err => { this.msgError = err.error.message; },
        () => {
          setTimeout(() => { this.loading = false; }, 2000)
        }
      )
    this.compteFormDirective.resetForm();


  }


  getDecodedAccessToken(token: string): any {
    try {
      return jwt_decode(token);
    }
    catch (Error) {
      return null;
    }
  }

  reloadPage() {
    setTimeout(() => {
      window.location.reload();
    }, 100);
  }

}
