import { Component, OnInit } from '@angular/core';
import { ClasseService } from './../../services/classe.service';
import { MatiereService } from './../../services/matiere.service';
import { ActivatedRoute, Router } from '@angular/router';
import { SemestreService } from './../../services/semestre.service';

@Component({
  selector: 'app-supprimer-semestre',
  templateUrl: './supprimer-semestre.component.html',
  styleUrls: ['./supprimer-semestre.component.scss']
})
export class SupprimerSemestreComponent implements OnInit {
  idSemestre: string ='';
  constructor(
    private semestreService: SemestreService,
    private matiereService: MatiereService,
    private route: ActivatedRoute,
    private router: Router
  ) { }

  ngOnInit(): void {
    this.idSemestre = this.route.snapshot.params['id'];
    this.semestreService
        .deleteSemestre( this.idSemestre )
        .subscribe( semestre => {
           if( semestre.matieres.length > 0 ) {
             let j: number = 0;
            semestre.matieres.forEach( (matiere, i) => {
              this.matiereService
                  .deleteMatiere( matiere._id )
                  .subscribe( mat => {
                    j += 1;
                  } );
            } );
            if( semestre.matieres.length === j ) {
              this.router.navigate(['/facultes'])
            }   
           } else {
            this.router.navigate(['/facultes'])
           }
        } )
  }

}
