import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { Observable, of } from 'rxjs';
import { VariablesGlobales } from 'src/variablesGlobales';
import { baseURL } from '../modules/shared/baseurl';
import { Compte } from '../modules/shared/compte';

@Injectable()
export class AuthService {
  private loggedIn?: boolean;
  private token?: any;
  constructor(
    private router: Router,
    private httpClient: HttpClient,
    private variable: VariablesGlobales
  ) { }
  setLoggedIn(loggedIn: boolean, token?: any) {
    this.loggedIn = loggedIn;
    this.token = token;
  }

  login(compte?: Compte) {
    return this.httpClient.post<any>(baseURL + 'auth/login', compte);
  }

  logout() {
    localStorage.removeItem('exp');
    localStorage.removeItem('username');
    localStorage.removeItem('roles');
    localStorage.removeItem('_id');
    localStorage.removeItem('token');
    localStorage.removeItem('admin')
    localStorage.removeItem('avatar');
    localStorage.removeItem('universite');
    this.router.navigateByUrl('/');
  }

  isAdmin(): boolean {
    var admin: boolean = false;
    const roles = localStorage.getItem('roles')?.toString().split(',');

    roles?.forEach(r => {
      if (r == 'ADMIN') {
        admin = true;
      }
    });

    return admin;
  }


  goToUniversiteDetail(idUniversite: string): Observable<string> {
    localStorage.setItem('universite', idUniversite);
    this.variable.parametre = localStorage.getItem('universite')?.toString();
    return of(idUniversite);
  }

}