import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { ActivatedRoute, Router } from '@angular/router';
import { Observable } from 'rxjs';
import { LoginComponent } from '../login/login.component';
import { ModalAccessRestreintComponent } from '../modal-access-restreint/modal-access-restreint.component';
import { ClasseService } from '../modules/services/classe.service';
import { CompteService } from '../modules/services/compte.service';
import { DepartementService } from '../modules/services/departement.service';
import { DossierService } from '../modules/services/dossier.service';
import { MatiereService } from '../modules/services/matiere.service';
import { ParcoursService } from '../modules/services/parcours.service';
import { UniversiteService } from '../modules/services/universite.service';
import { Classe } from '../modules/shared/classe';
import { Compte } from '../modules/shared/compte';
import { Departement } from '../modules/shared/departement';
import { Matiere } from '../modules/shared/matiere';
import { Universite } from '../modules/shared/universite';
import { flyInOut, expand } from 'animations/app.animation';
@Component({
  selector: 'app-list-cours',
  templateUrl: './list-cours.component.html',
  styleUrls: ['./list-cours.component.scss'],
  host: {
    '[@flyInOut]': 'true',
    'style': 'display: block;'
  },
  animations: [
    flyInOut(),
    expand()
  ]
})
export class ListCoursComponent implements OnInit {

  niveau: any = {
    licence: [],
    mastere: [],
    doctorat: []
  };
  parcours: any[] = [];

  classes: any[] = [];
  matieres: any = {};
  semestres: any = {};
  classeSelectded?: any;
  semaine?: string;
  departementSeelected?: any;
  matiereSelected?: any;
  isInscris: boolean = false;
  compteId?: string;
  loading: boolean = true;

  panelOpenState = false;
  idUniversite: string = "";
  universite: Universite;
  departements: Departement[] = [];
  compte: Compte;
  constructor(
    private route: ActivatedRoute,
    private universiteService: UniversiteService,
    private departementService: DepartementService,
    private classeService: ClasseService,
    private parcoursService: ParcoursService,
    private matiereService: MatiereService,
    private dossierService: DossierService,
    private compteService: CompteService,
    private dialog: MatDialog,
    private router: Router
  ) {

  }


  ngOnInit(): void {
    this.idUniversite = this.route.snapshot.params['id'];


    this.compteId = localStorage.getItem('_id')?.toString();
    this.compteService
        .getCompteById( this.compteId )
        .subscribe( compte => { 
              this.compte = compte;
            })
    this.universiteService
      .getUniversiteById(this.idUniversite)
      .subscribe(
        universite => {this.universite = universite},
        error => {},
        () => this.loading = false
      )
    localStorage.setItem('universite', this.idUniversite);
    this.universiteService
      .getUniversiteById(this.idUniversite)
      .subscribe(universite => {
        this.universite = universite;
        this.departementService
          .getDepartementByUniversiteById(this.universite._id)
          .subscribe(departements => {
            this.departements = departements;
            this.departements.forEach((d, i) => {
              this.parcoursService
                .getParcoursByDepartementId(d._id)
                .subscribe(parcours => {
                  this.parcours[i] = parcours;
                  for (let j = 0; j < this.parcours[i].length; j++) {
                    this.classeService
                      .getClasseByParcoursId(this.parcours[i][j]._id)
                      .subscribe(prcs => {
                        this.classes[j] = prcs;
                        for (let k = 0; k < this.classes[j].length; k++) {
                          this.matiereService
                            .getMatiereByClasseId(this.classes[j][k]._id)
                            .subscribe(matieres => {
                              this.matieres[this.classes[j][k].nom] = matieres;
                              this.semestres[this.classes[j][k].nom] = new Set();
                              this.matieres[this.classes[j][k].nom].forEach((mm: any) => {
                                this.semestres[this.classes[j][k].nom].add(mm.semestre);
                              });
                            })
                        }
                      })
                  }
                })
            })
          })
      }
      );
  }
  getClasseByFiliereIdAndNiveau(idFiliere: string, niveau: string): Observable<Classe[]> {
    return this.classeService
      .getClasseByFiliereIdAndNiveau(idFiliere, niveau);
  }

  selectionSemaine(departement: any, classe: any, semaine: string, matiere: any) {
    this.departementSeelected = departement;
    this.classeSelectded = classe;
    this.semaine = semaine;
    this.matiereSelected = matiere;
    this.panelOpenState = false;
  }

  selectionParcours(classe: any, semaine: string) {
    //this.parcoursSelected = this.parcours[i];
  }

  openLogin() {
    this.dialog.open( LoginComponent, {
     position: {
       top: '5%'
     }
    } )
  }

  openAccessRestreintModal(classe: Classe, departement: Departement) {
    this.dialog.open( ModalAccessRestreintComponent, {
     position: {
       top: '5%'
     },
     data: {
       classe: classe,
       departement: departement
     }
    } )
  }



  watchCourse(classe: Classe, departement: Departement, matiere: Matiere) {
    if(this.compteId == undefined || this.compteId == null) {
      this.openLogin();
    } else {
      if( classe._id !=  this.compte.classe ) {
        this.openAccessRestreintModal(classe, departement);
        //alert("Le cours est ouvert aux tudiants de la "+ classe.nom + " du dpartement " + departement.nom)
      } else {
        /*this.dossierService
            .getDossierByCompteId( this.compte._id )
            .subscribe( dossier => {
              if(dossier?.valide == true) {
                this.router.navigateByUrl('cours/' + matiere._id)
              } else {
                this.openAccessRestreintModal(classe, departement);
              }
            } )*/
      }
    }
  }
}

