import { Component, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { ColloqueService } from '../../services/colloque.service';
import { UniversiteService } from '../../services/universite.service';
import { Colloque } from '../../shared/colloque';
import { Universite } from '../../shared/universite';

@Component({
  selector: 'app-create-colloque',
  templateUrl: './create-colloque.component.html',
  styleUrls: ['./create-colloque.component.scss']
})
export class CreateColloqueComponent implements OnInit {

  
  @ViewChild('fform', { static: false }) colloqueFormDirective: any;
  colloqueForm: FormGroup;

  selectedFile: File;

  colloque: Colloque;
  loading: boolean = false;
  formErrors: any = {
    titre: '',
    presentation: '',
    description: '',
    programme: '',
    date: '',
    organisateur: '',
    universite: '',
    lieux: ''
  };

  validationMessages: any = {
    titre: {
      required: 'le titre  est requis.',
      minlength: 'le titre tre plus que 4 caractres.'
    },
    description: {
      required: 'la description est requise.',
      minlength: 'la description doit tre plus que 6 caractres.'
    },
    presentation: {
      required: 'la presentation est requise.',
      minlength: 'la presentation doit tre plus que 6 caractres.'
    },
    programme: {
      required: 'le champ programme est requis.',
      minlength: 'le champ programme doit tre plus que 6 caractres.'
    },
    organisateur: {
      required: 'le champ organisateur est requis.',
      minlength: 'le champ organisateur doit tre plus que 6 caractres.'
    },
    date: {
      required: 'le champ date est requis.',
      minlength: 'le champ date doit tre plus que 6 caractres.'
    },
    lieux: {
      required: 'le champ lieux est requis.',
      minlength: 'le champ lieux doit tre plus que 6 caractres.'
    },
    universite: {
      required: 'le titre  est requis.'
    },
  };

  compteId?: string;
  msgError?: string;
  universites: Universite[] = [];

  constructor(
    private formBuilder: FormBuilder,
    private router: Router,
    private colloqueService: ColloqueService,
    private universiteService: UniversiteService,
    private route: ActivatedRoute
  ) {
    this.createForm();
  }

  ngOnInit(): void {
    this.universiteService
      .getAllUniversities()
      .subscribe(universites => this.universites = universites)
  }

  createForm() {
    this.colloqueForm = this.formBuilder.group({
      titre: ['', [Validators.required, Validators.minLength(4)]],
      programme: ['', [Validators.required, Validators.minLength(4)]],
      organisateur: ['', [Validators.required, Validators.minLength(4)]],
      date: ['', [Validators.required, Validators.minLength(4)]],
      lieux: ['', [Validators.required, Validators.minLength(4)]],
      presentation: ['', [Validators.required, Validators.minLength(4)]],
      description: ['', [Validators.required, Validators.minLength(4)]],
      universite: ['', [Validators.required]],
      fichier: ''
    });
    this.colloqueForm
      .valueChanges
      .subscribe(data => this.onValueChanged(data));
    this.onValueChanged();
  }

  onValueChanged(data?: any) {
    if (!this.colloqueForm) { return; }
    const form = this.colloqueForm;
    for (const field in this.formErrors) {
      if (this.formErrors.hasOwnProperty(field)) {
        // clear previous error message (if any)
        this.formErrors[field] = '';
        const control = form.get(field);
        if (control && control.dirty && !control.valid) {
          const messages = this.validationMessages[field];
          for (const key in control.errors) {
            if (control.errors.hasOwnProperty(key)) {
              this.formErrors[field] += messages[key] + ' ';
            }
          }
        }
      }
    }
  }

  ngOnChanges() {
    this.onSubmit();
  }

  closeModal() {
    this.router.navigateByUrl('reset-password');
  }

  onSubmit() {
    this.colloque = this.colloqueForm?.value;
    this.colloque.fichier = 'images/colloques/' + this.selectedFile.name;
    this.colloqueService
        .telecharger(this.selectedFile)
        .subscribe( file => {
          this.colloqueService
              .createColloque(this.colloque)
              .subscribe(
                colloque=> {},
                err => { this.msgError = err.error.message; },
                () => {
                  setTimeout(() => { this.loading = false; }, 2000);
                  location.reload()
                }
              )
        } )
    this.colloqueFormDirective.resetForm();
  }


  fileChanged(event: any) {
    this.selectedFile = event.target.files[0];
  }

}

