import { Component, Inject, OnInit } from '@angular/core';
import { MatDialog, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { InscriptionEnLigneService } from '../../services/inscription-en-ligne.service';
import { InscriptionEnLigne } from '../../shared/inscriptionEnLigne';

@Component({
  selector: 'app-supprimer-inscription-en-ligne',
  templateUrl: './supprimer-inscription-en-ligne.component.html',
  styleUrls: ['./supprimer-inscription-en-ligne.component.scss']
})
export class SupprimerInscriptionEnLigneComponent implements OnInit {
  idInscription: string = '';
  inscription?: InscriptionEnLigne;
  loading: boolean = false;
  constructor(
    private inscriptionService: InscriptionEnLigneService,
    @Inject(MAT_DIALOG_DATA) private data: any,
    private matDialog: MatDialog,
    private router: Router
  ) { }

  ngOnInit(): void {
    this.idInscription = this.data.idInscription;
    this.inscriptionService
      .getInscriptionEnLigneById(this.idInscription)
      .subscribe(inscription => this.inscription = inscription)
  }

  supprimerinscription() {
    this.loading = true;
    this.inscriptionService
      .deleteInscriptionEnLigne(this.idInscription)
      .subscribe(
        filiere => {},
        error => {},
        () => { 
          this.loading = false, 
          this.matDialog.closeAll(),
          location.reload() 
        }
      )
  }

  closeModal() {
    this.matDialog.closeAll()
  }

}
