import { Component, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { DistinctionService } from '../../services/distinction.service';
import { UniversiteService } from '../../services/universite.service';
import { Distinction } from '../../shared/distinction';
import { Universite } from '../../shared/universite';

@Component({
  selector: 'app-create-distinction',
  templateUrl: './create-distinction.component.html',
  styleUrls: ['./create-distinction.component.scss']
})
export class CreateDistinctionComponent implements OnInit {

  
  @ViewChild('fform', { static: false }) distinctionFormDirective: any;
  distinctionForm: FormGroup;

  selectedFile: File;

  distinction: Distinction;
  loading: boolean = false;
  formErrors: any = {
    titre: '',
    presentation: '',
    description: '',
    universite: ''
  };

  validationMessages: any = {
    titre: {
      required: 'le titre  est requis.',
      minlength: 'le titre tre plus que 4 caractres.'
    },
    description: {
      required: 'la description est requise.',
      minlength: 'la description doit tre plus que 6 caractres.'
    },
    presentation: {
      required: 'la presentation est requise.',
      minlength: 'la presentation doit tre plus que 6 caractres.'
    },
    universite: {
      required: 'le titre  est requis.'
    },
  };

  compteId?: string;
  msgError?: string;
  universites: Universite[] = [];

  constructor(
    private formBuilder: FormBuilder,
    private router: Router,
    private distinctionService: DistinctionService,
    private universiteService: UniversiteService,
    private route: ActivatedRoute
  ) {
    this.createForm();
  }

  ngOnInit(): void {
    this.universiteService
      .getAllUniversities()
      .subscribe(universites => this.universites = universites)
  }

  createForm() {
    this.distinctionForm = this.formBuilder.group({
      titre: ['', [Validators.required, Validators.minLength(4)]],
      presentation: ['', [Validators.required, Validators.minLength(4)]],
      description: ['', [Validators.required, Validators.minLength(4)]],
      universite: ['', [Validators.required]],
      fichier: ''
    });
    this.distinctionForm
      .valueChanges
      .subscribe(data => this.onValueChanged(data));
    this.onValueChanged();
  }

  onValueChanged(data?: any) {
    if (!this.distinctionForm) { return; }
    const form = this.distinctionForm;
    for (const field in this.formErrors) {
      if (this.formErrors.hasOwnProperty(field)) {
        // clear previous error message (if any)
        this.formErrors[field] = '';
        const control = form.get(field);
        if (control && control.dirty && !control.valid) {
          const messages = this.validationMessages[field];
          for (const key in control.errors) {
            if (control.errors.hasOwnProperty(key)) {
              this.formErrors[field] += messages[key] + ' ';
            }
          }
        }
      }
    }
  }

  ngOnChanges() {
    this.onSubmit();
  }

  closeModal() {
    this.router.navigateByUrl('reset-password');
  }

  onSubmit() {
    this.distinction = this.distinctionForm?.value;
    this.distinction.fichier = 'images/distinctions/' + this.selectedFile.name;
    this.distinctionService
        .telecharger(this.selectedFile)
        .subscribe( file => {
          this.distinctionService
              .createDistinction(this.distinction)
              .subscribe(
                distinction=> {},
                err => { this.msgError = err.error.message; },
                () => {
                  setTimeout(() => { this.loading = false; }, 2000);
                  location.reload()
                }
              )
        } )
    this.distinctionFormDirective.resetForm();
  }


  fileChanged(event: any) {
    this.selectedFile = event.target.files[0];
  }

}
