import { Component, Inject, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { Observable } from 'rxjs';
import { MAT_DIALOG_DATA, MatDialog } from '@angular/material/dialog';
import { DossierService } from './../modules/services/dossier.service';
import { Dossier } from './../modules/shared/dossier';
@Component({
  selector: 'app-update-releve-modal',
  templateUrl: './update-releve-modal.component.html',
  styleUrls: ['./update-releve-modal.component.scss']
})
export class UpdateReleveModalComponent implements OnInit {

  selectedFile: File;
  @ViewChild('fform', { static: false }) compteFormDirective: any;
  dossierForm: FormGroup;
  dossier$?: Observable<Dossier>;
  loading: boolean = false;


  dossier: Dossier;
  id: string = '';
  newDossier$?: Observable<Dossier>;
  username: string;

  formErrors: any = {
    avatar: ''
  };

  validationMessages: any = {
    releveBAC: {
      required: 'le fichier est requis.'
    }
  };

  idEtudiant?: string;
  newEtudiant$?: Observable<Dossier>;
  compteId?: string;
  constructor(
    private formBuilder: FormBuilder,
    private router: Router,
    private dossierService: DossierService,
    private route: ActivatedRoute,
    @Inject(MAT_DIALOG_DATA) public data: any,
    public dialog: MatDialog,
  ) {
    this.createForm();
    this.dossier = this.data.dossier;
    this.id = this.dossier._id;
    this.username = this.data.username;
    console.log(this.dossier);
  }


  ngOnInit(): void {
    this.dossierService
        .getDossierById(this.id)
        .subscribe( doc => this.dossier = doc )

  }

  createForm() {
    this.dossierForm = this.formBuilder.group({
      releveBAC: ['', [Validators.required]],
    });
    this.dossierForm
      .valueChanges
      .subscribe(data => this.onValueChanged(data));
    this.onValueChanged();
  }

  onValueChanged(data?: any) {
    if (!this.dossierForm) { return; }
    const form = this.dossierForm;
    for (const field in this.formErrors) {
      if (this.formErrors.hasOwnProperty(field)) {
        // clear previous error message (if any)
        this.formErrors[field] = '';
        const control = form.get(field);
        if (control && control.dirty && !control.valid) {
          const messages = this.validationMessages[field];
          for (const key in control.errors) {
            if (control.errors.hasOwnProperty(key)) {
              this.formErrors[field] += messages[key] + ' ';
            }
          }
        }
      }
    }
  }


  onSubmit() {
    this.dossier.releveBAC= 'images/' + this.username + '/dossiers/' + this.selectedFile.name;
    this.loading = true;
    
     
    var obj = {};
    
        obj = {'releveBAC':  this.dossier.releveBAC};
    this.dossierService
      .createReleveBAC(this.selectedFile)
      .subscribe(et => {
        this.dossierService
          .updateDossier( this.id, this.dossier)
          .subscribe( doc => {} );
      }, error => { error == null ? this.loading = true : this.loading = false; },
        () => {
            this.loading = false;
            window.location.reload();
            this.dialog.closeAll()
        }
      );
  }

  fileChanged(event: any) {
    this.selectedFile = event.target.files[0];
    console.log(this.selectedFile);
  }
}

