import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ClasseService } from '../../services/classe.service';
import { EtudiantService } from '../../services/etudiant.service';
import { MatiereService } from '../../services/matiere.service';
import { SemestreService } from '../../services/semestre.service';
import { Classe } from '../../shared/classe';
import { Etudiant } from '../../shared/etudiant';
import { Matiere } from '../../shared/matiere';
import { Semestre } from '../../shared/semestre';
import { Enseignant } from './../../shared/enseignant';
import { EnseignantService } from './../../services/enseignant.service';

@Component({
  selector: 'app-detail-classe',
  templateUrl: './detail-classe.component.html',
  styleUrls: ['./detail-classe.component.scss']
})
export class DetailClasseComponent implements OnInit {

  loading: boolean = false;
  classe: Classe;
  matieres: Matiere[]=[];
  etudiants?: Etudiant[];
  semestre: Semestre;
  idClasse: string = '';
  idCompte?: string;
  enseignant: Enseignant;
  constructor(
    private route: ActivatedRoute,
    private semestreService: SemestreService,
    private etudiantService: EtudiantService,
    private enseignantService: EnseignantService,
    private classeService: ClasseService
  ) { }

  ngOnInit(): void {
    this.idCompte = localStorage.getItem('_id')?.toString();
    this.idClasse = this.route.snapshot.params['id'];
    this.classeService.getClasseById(this.idClasse).subscribe(c => { 
      this.classe = c;
    });
    this.enseignantService
        .getEnseignantByCompteId( this.idCompte )
        .subscribe( enseignant => {
          this.enseignant = enseignant;
        } )
  }

}
