import { Component, Inject, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { ActivatedRoute } from '@angular/router';
import { DepartementService } from '../../services/departement.service';
import { ParcoursService } from '../../services/parcours.service';
import { Departement } from '../../shared/departement';
import { Parcours } from '../../shared/parcours';
import { SupprimerFiliereComponent } from '../supprimer-filiere/supprimer-filiere.component';
import { Compte } from './../../shared/compte';
import { CompteService } from './../../services/compte.service';

@Component({
  selector: 'app-detail-departement',
  templateUrl: './detail-departement.component.html',
  styleUrls: ['./detail-departement.component.scss']
})
export class DetailDepartementComponent implements OnInit {

  idDepartement: string = '';
  departement?: Departement;
  parcourss?: Parcours[];
  compteId?: string;
  compte: Compte;
  constructor(
    private departementService: DepartementService,
    private route: ActivatedRoute,
    private parcoursService: ParcoursService,
    private compteService: CompteService,
    private matDiag: MatDialog
  ) { }

  ngOnInit(): void {
    this.idDepartement = this.route.snapshot.params['id']; 
    this.compteId = localStorage.getItem('_id')?.toString();
    this.departementService
      .getDepartementById(this.idDepartement)
      .subscribe(departement => this.departement = departement)

    this.parcoursService
      .getParcoursByDepartementId(this.idDepartement)
      .subscribe(parcourss => this.parcourss = parcourss);

    this.compteService
        .getCompteById( this.compteId )
        .subscribe( compte => {
          this.compte = compte;
        });
  }

  openDialogSupprimerFiliere(id: string) {
    const dialogRef = this.matDiag.open(SupprimerFiliereComponent, {
      width: '50%',
      autoFocus: true,
      maxHeight: '100vh',
      position: {
        top: '10%',
        left: '35%'
      },
      data: { id: id, idDepartement: this.idDepartement }
    });

    dialogRef.afterClosed().subscribe(result => {});
  }


}
