import { ThisReceiver } from '@angular/compiler';
import { Component, OnInit, ViewChild } from '@angular/core';
import { FormArray, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { UniversiteService } from '../../services/universite.service';
import { VieEstidiantineService } from '../../services/vie-estidiantine.service';
import { Universite } from '../../shared/universite';
import { VieEstidiantine } from '../../shared/vieEstidiantine';

@Component({
  selector: 'app-create-vie-estidiantine',
  templateUrl: './create-vie-estidiantine.component.html',
  styleUrls: ['./create-vie-estidiantine.component.scss']
})
export class CreateVieEstidiantineComponent implements OnInit {


  @ViewChild('fform', { static: false }) vieEstidiantineFormDirective: any;
  vieEstidiantineForm: FormGroup;
  images: FormArray;

  selectedFile: File[] = [];

  vieEstidiantine: VieEstidiantine;
  loading: boolean = false;
  formErrors: any = {
    titre: '',
    description: ''
  };

  validationMessages: any = {
    titre: {
      required: 'le titre  est requis.',
      minlength: 'le titre tre plus que 4 caractres.'
    },
    description: {
      required: 'la description est requise.',
      minlength: 'la description doit tre plus que 6 caractres.'
    }
  };

  compteId?: string;
  msgError?: string;
  universites: Universite[] = [];
  type: string = '';


  constructor(
    private formBuilder: FormBuilder,
    private router: Router,
    private VieEstidiantineService: VieEstidiantineService,
    private universiteService: UniversiteService,
    private route: ActivatedRoute
  ) {
    this.createForm();
  }

  ngOnInit(): void {
    this.type = this.route.snapshot.params['name'];

    this.universiteService
      .getAllUniversities()
      .subscribe(universites => this.universites = universites)
  }

  createForm() {
    this.vieEstidiantineForm = this.formBuilder.group({
      titre: ['', [Validators.required, Validators.minLength(4)]],
      description: ['', [Validators.required, Validators.minLength(4)]],
      universite: 0,
      images: this.formBuilder.array([this.createImage()])
    });
    this.vieEstidiantineForm
      .valueChanges
      .subscribe(data => this.onValueChanged(data));
    this.onValueChanged();
  }

  onValueChanged(data?: any) {
    if (!this.vieEstidiantineForm) { return; }
    const form = this.vieEstidiantineForm;
    for (const field in this.formErrors) {
      if (this.formErrors.hasOwnProperty(field)) {
        // clear previous error message (if any)
        this.formErrors[field] = '';
        const control = form.get(field);
        if (control && control.dirty && !control.valid) {
          const messages = this.validationMessages[field];
          for (const key in control.errors) {
            if (control.errors.hasOwnProperty(key)) {
              this.formErrors[field] += messages[key] + ' ';
            }
          }
        }
      }
    }
  }

  ngOnChanges() {
    this.onSubmit();
  }

  closeModal() {
    this.router.navigateByUrl('reset-password');
  }

  onSubmit() {
    this.vieEstidiantine = this.vieEstidiantineForm?.value;
    this.vieEstidiantine.images = [];
    this.vieEstidiantine.type = this.type;

    if (this.selectedFile.length > 0) {
      this.selectedFile.forEach(f => {
        this.vieEstidiantine?.images.push('images/vies/' + f.name)
      })
    }
    if (this.vieEstidiantine != null && this.selectedFile) {
      this.VieEstidiantineService
        .createVieEstidiantine(this.vieEstidiantine)
        .subscribe(
          vieEstidiantine => {
            this.selectedFile.forEach(fichier => {
              this.VieEstidiantineService
                .telecharger(fichier)
                .subscribe(
                  data => { },
                  error => {  },
                  () => {
                    this.router.navigateByUrl('formations')
                  }
                )
            });
          },
          err => { this.msgError = err.error.message; },
          () => {
            setTimeout(() => { this.loading = false; }, 2000)
          }
        )

    }

    this.vieEstidiantineFormDirective.resetForm();
  }

  createImage(): FormGroup {
    return this.formBuilder.group({
      fichier: '',
    });
  }

  addImage(): void {
    this.images = this.vieEstidiantineForm.get('images') as FormArray;
    this.images.push(this.createImage());
  }
  fileChanged(event: any) {
    this.selectedFile.push(event.target.files[0]);
  }
  get fichierFormGroups() {
    return this.vieEstidiantineForm.get('images') as FormArray
  }
}


