import { Component, Inject, OnChanges, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators, FormArray } from '@angular/forms';
import jwt_decode from 'jwt-decode';
import { ActivatedRoute, Router } from '@angular/router';
import { Universite } from '../../shared/universite';
import { Observable } from 'rxjs';
import { Adresse } from '../../shared/adresse';
import { AdresseService } from '../../services/adresse.service';
import { UniversiteService } from '../../services/universite.service';

@Component({
  selector: 'app-mis-a-jour-universite',
  templateUrl: './mis-a-jour-universite.component.html',
  styleUrls: ['./mis-a-jour-universite.component.scss']
})
export class MisAJourUniversiteComponent implements OnInit {
  @ViewChild('fform', { static: false }) compteFormDirective: any;
  universiteForm: FormGroup;
  selectedFile: File;

  universite: Universite;
  universiteUpdate: Universite;
  loading: boolean = false;

  formErrors: any = {
    nom: '',
    description: '',
    dateCreation: null,
    adresse: null,
  };

  imagesFiles: File[] = [];
  images: FormArray;

  validationMessages: any = {
    nom: {
      required: 'le nom  est requis.',
      minlength: 'le nom tre plus que 4 caractres.',
      maxlength: 'le nom  ne doit pas tre plus que 150 caractres.'
    },
    description: {
      required: 'la description est requise.',
      minlength: 'la description tre plus que 6 caractres.',
      maxlength: 'la description ne doit pas tre plus que 1000 caractres.'
    },
    dateCreation: {
      required: 'la date de cration est requise.',
    },
    adresse: {
      required: 'l\'adresse est requise.',
    }
  };

  id: string = '';

  adresses$?: Observable<Adresse[]>;

  constructor(
    private formBuilder: FormBuilder,
    private router: Router,
    private adresseService: AdresseService,
    private universiteService: UniversiteService,
    private route: ActivatedRoute
  ) {
    this.createForm();
  }

  ngOnInit(): void {
    this.id = this.route.snapshot.params['id'];
    this.universiteService
      .getUniversiteById(this.id)
      .subscribe(universite => this.universiteUpdate = universite);
    this.adresses$ = this.adresseService.getAllAdresses();
  }

  createForm() {
    this.universiteForm = this.formBuilder.group({
      nom: ['', [Validators.required, Validators.minLength(4), Validators.maxLength(150)]],
      description: ['', [Validators.required, Validators.minLength(4), Validators.maxLength(150)]],
      adresse: ['', [Validators.required]],
      images: this.formBuilder.array([this.createImages()]),
      dateCreation: ['', [Validators.required]],
      logo: ''
    });
    this.universiteForm
      .valueChanges
      .subscribe(data => this.onValueChanged(data));
    this.onValueChanged();
  }

  onValueChanged(data?: any) {
    if (!this.universiteForm) { return; }
    const form = this.universiteForm;
    for (const field in this.formErrors) {
      if (this.formErrors.hasOwnProperty(field)) {
        // clear previous error message (if any)
        this.formErrors[field] = '';
        const control = form.get(field);
        if (control && control.dirty && !control.valid) {
          const messages = this.validationMessages[field];
          for (const key in control.errors) {
            if (control.errors.hasOwnProperty(key)) {
              this.formErrors[field] += messages[key] + ' ';
            }
          }
        }
      }
    }
  }

  ngOnChanges() {
    this.id = this.route.snapshot.params['id'];
  }

  onSubmit() {
    this.universite = this.universiteForm?.value;
    this.universite.logo = 'images/universites/' + this.selectedFile.name
    this.universite.images = [];
    for(let i=0; i < this.imagesFiles.length; i++) {
      this.universite.images.push('images/universites/' + this.imagesFiles[i].name );
    }

    for(let i=0; i < this.imagesFiles.length; i++) {
      this.universiteService
          .getLogoName( this.imagesFiles[i] )
          .subscribe( image => {} );
    }

    this.loading = true;
    this.universiteService
      .getLogoName(this.selectedFile)
      .subscribe(universite => {
        this.universiteService
          .updateUniversite(this.id,this.universite)
          .subscribe(
            univ => { this.router.navigateByUrl('universite/' + univ._id); },
            err => { },
            () => () => {this.loading = false;  }
          );
      });
      
  }


  getDecodedAccessToken(token: string): any {
    try {
      return jwt_decode(token);
    }
    catch (Error) {
      return null;
    }
  }

  reloadPage() {
    setTimeout(() => {
      window.location.reload();
    }, 100);
  }

  fileChanged(event: any) {
    this.selectedFile = event.target.files[0];
  }
  fileChangedImages(event: any) {
    this.imagesFiles.push(event.target.files[0]);
  }

  get imageDetails() {
    return (this.universiteForm.controls.items as FormGroup).controls;
  }

  get imageFormGroups() {
    return this.universiteForm.get('images') as FormArray
  }

  addItem(): void {
    this.images = this.universiteForm.get('images') as FormArray;
    this.images.push(this.createImages());
  }

  createImages(): FormGroup {
    return this.formBuilder.group({
      logo: ''
    });
  }



}
