import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { baseURL } from '../shared/baseurl';
import { Formation } from '../shared/formation';

@Injectable({
  providedIn: 'root'
})
export class FormationService {
  constructor(private httpClient: HttpClient) { }

  getAllFormations(): Observable<Formation[]> {
    return this.httpClient.get<Formation[]>(baseURL + 'formations');
  }
  getFormationById(idFormation: string): Observable<Formation> {
    return this.httpClient.get<Formation>(baseURL + 'formations/' + idFormation);
  }

  createFormation(formation: Formation): Observable<Formation> {
    return this.httpClient.post<Formation>(baseURL + 'formations', formation);
  }

  updateFormation(idFormation: string, formation: Formation): Observable<Formation> {
    return this.httpClient.put<Formation>(baseURL + 'formations/' + idFormation, formation)
  }
  getFormationByUniversiteId(idUniversite: string): Observable<Formation[]> {
    return this.httpClient.get<Formation[]>(baseURL + 'formations/universites/' + idUniversite);
  }

  deleteFormation(idFormation: string): Observable<Formation> {
    return this.httpClient.delete<Formation>(baseURL + 'formations/' + idFormation);
  }
  //Les methodes de cration ici
  public telecharger(file: File): Observable<string> {
    let formData = new FormData();
    formData.append('fichier', file);
    console.log(formData);
    return this.httpClient.put<string>(baseURL + "formations", formData);
  }
}
