import { Component, OnInit, ViewChild } from '@angular/core';
import { FormArray, FormBuilder, FormGroup } from '@angular/forms';
import { MatDialog } from '@angular/material/dialog';
import { ActivatedRoute, Router } from '@angular/router';
import { InscriptionEnLigneService } from '../../services/inscription-en-ligne.service';
import { UniversiteService } from '../../services/universite.service';
import { InscriptionEnLigne } from '../../shared/inscriptionEnLigne';
import { Universite } from '../../shared/universite';
import { SupprimerInscriptionEnLigneComponent } from '../supprimer-inscription-en-ligne/supprimer-inscription-en-ligne.component';

@Component({
  selector: 'app-ajouter-inscription-en-ligne',
  templateUrl: './ajouter-inscription-en-ligne.component.html',
  styleUrls: ['./ajouter-inscription-en-ligne.component.scss']
})
export class AjouterInscriptionEnLigneComponent implements OnInit {

  @ViewChild('fform', { static: false }) inscriptionFormDirective: any;
  inscriptionForm: FormGroup;
  items: FormArray;
  idUniversite: string = "";
  inscriptionEnligne?: InscriptionEnLigne;
  universite?: Universite;
  inscriptions: InscriptionEnLigne[] = [];

  constructor(
    private formBuilder: FormBuilder,
    private universiteService: UniversiteService,
    private inscriptionEnLigneService: InscriptionEnLigneService,
    private route: ActivatedRoute,
    private router: Router,
    private dialog: MatDialog
  ) { }

  ngOnInit() {
    this.idUniversite = this.route.snapshot.params['id'];

    this.inscriptionEnLigneService
        .getInscriptionEnLigneByUniversiteId( this.idUniversite )
        .subscribe( inscriptions => this.inscriptions = inscriptions )

    this.universiteService
      .getUniversiteById(this.idUniversite)
      .subscribe(universite => {
        this.universite = universite;
      })

    this.inscriptionForm = this.formBuilder.group({
      items: this.formBuilder.array([this.createItem()])
    });
  }
  get cadreDetails() {
    return (this.inscriptionForm.controls.items as FormGroup).controls;
  }

  get inscriptionFormGroups() {
    return this.inscriptionForm.get('items') as FormArray
  }

  createItem(): FormGroup {
    return this.formBuilder.group({
      titre: '',
      description: '',
      universite: ''
    });
  }

  addItem(): void {
    this.items = this.inscriptionForm.get('items') as FormArray;
    this.items.push(this.createItem());
  }

  onSubmit() {
        for (let i = 0; i < this.inscriptionForm.value.items.length; i++) {
          this.inscriptionEnLigneService
            .createInscriptionEnLigne(
              {
                titre: this.inscriptionForm.value.items[i].titre,
                description: this.inscriptionForm.value.items[i].description,
                universite: this.universite
              } as InscriptionEnLigne
            )
            .subscribe(presentation =>{
               location.reload();
              });
        }

  }

  supprimerInscriptionEnLigneInfosDialog(idInscription: string){
    this.dialog.open( SupprimerInscriptionEnLigneComponent, {
      position: {
        top: '8%'
      },
      width: '35%',
      height: '35%',
      data: {
        idInscription: idInscription,
        idUniversite: this.idUniversite
      }
    } )
  }
}