import { Component, OnInit } from '@angular/core';
import { PLANSITE } from '../modules/shared/plansite';

@Component({
  selector: 'app-footer',
  templateUrl: './footer.component.html',
  styleUrls: ['./footer.component.scss']
})
export class FooterComponent implements OnInit {
  anneeUniversitaire: string = '';
  langue: string = "";
  plansite: any;
  constructor() {
    this.anneeUniversitaire = new Date().getFullYear() + '-' + Number((new Date().getFullYear()) + 1)
  }

  ngOnInit(): void {
    if (localStorage.getItem("langue") == "Franais") {
      this.plansite = PLANSITE.fr;
    } else if (localStorage.getItem("langue") == "Anglais") {
      this.plansite = PLANSITE.en;
    }
  }
}

