import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { baseURL } from '../shared/baseurl';
import { Recherche } from '../shared/recherche';

@Injectable({
  providedIn: 'root'
})
export class RechercheService {

  constructor(private httpClient: HttpClient) { }
  getAllRecherche(): Observable<Recherche[]> {
    return this.httpClient.get<Recherche[]>(baseURL + 'recherches');
  }

  getRechercheById(id: string): Observable<Recherche> {
    return this.httpClient.get<Recherche>(baseURL + 'recherches/' + id)
  }

  createRecherche(Recherche: Recherche): Observable<Recherche> {
    return this.httpClient.post<Recherche>(baseURL + 'recherches', Recherche, {});
  }

  getLogoName(file: File): Observable<any> {
    const formData = new FormData();
    formData.append('logo', file)
    console.log("SERVICE", formData);
    return this.httpClient.put<any>(baseURL + 'recherches', formData, {});
  }

  getRechercheByUniversiteId(idUniversite: string): Observable<Recherche[]> {
    return this.httpClient.get<Recherche[]>(baseURL + 'recherches/universites/' + idUniversite);
  }

  updateRecherche(id: string, Recherche: Recherche): Observable<Recherche> {
    return this.httpClient.put<Recherche>(baseURL + 'recherches/' + id, Recherche);
  }
  //Les methodes de cration ici
  telecharger(file: File): Observable<string> {
    let formData = new FormData();
    formData.append('fichier', file);
    console.log(formData);
    return this.httpClient.put<string>(baseURL + "recherches", formData);
  }
}
