import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { baseURL } from '../shared/baseurl';
import { Presentation } from '../shared/presentation';


@Injectable({
  providedIn: 'root'
})
export class PresentationService {

  constructor(private httpClient: HttpClient) { }

  getAllPresentations(): Observable<Presentation[]> {
    return this.httpClient.get<Presentation[]>(baseURL + 'presentations');
  }
  getPresentationById(idPresentation: string): Observable<Presentation> {
    return this.httpClient.get<Presentation>(baseURL + 'presentations/' + idPresentation);
  }

  createPresentation(presentation: Presentation): Observable<Presentation> {
    return this.httpClient.post<Presentation>(baseURL + 'presentations', presentation);
  }

  updatePresentation(idPresentation: string, Presentation: Presentation): Observable<Presentation> {
    return this.httpClient.put<Presentation>(baseURL + 'presentations/' + idPresentation, Presentation)
  }
  getPresentationByUniversiteId(idUniversite: string): Observable<Presentation[]> {
    return this.httpClient.get<Presentation[]>(baseURL + 'presentations/universites/' + idUniversite);
  }

  deletePresentation(idPresentation: string): Observable<Presentation> {
    return this.httpClient.delete<Presentation>(baseURL + 'Presentations/' + idPresentation);
  }
}

