import { Component, Inject, OnInit } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { MatDialog, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { ActivatedRoute, Router } from '@angular/router';
import { CompteService } from '../modules/services/compte.service';
import { DossierService } from '../modules/services/dossier.service';
import { Compte } from '../modules/shared/compte';
import { Dossier } from '../modules/shared/dossier';
import { Universite } from './../modules/shared/universite';

@Component({
  selector: 'app-validation-dossier-etudiant',
  templateUrl: './validation-dossier-etudiant.component.html',
  styleUrls: ['./validation-dossier-etudiant.component.scss']
})
export class ValidationDossierEtudiantComponent implements OnInit {
  etudiantForm: FormGroup;
  releveFiles: File[];
  releveNotes: File[];
  dossier: Dossier;
  compte: Compte;
  diplomeFile: File;
  releveBACFile: File;
  id: string = '';
  constructor(
    private dialog: MatDialog,
    @Inject(MAT_DIALOG_DATA) public data: any,
    private dossierService: DossierService,
    private route: ActivatedRoute,
    private compteService: CompteService,
    private router: Router,
  ) { }

  ngOnInit(): void {
   console.log(this.data)
  }
  closeModal() {
    this.dialog.closeAll();
  }




  validation() {
    
    this.dossier = this.data.dossier;
    this.etudiantForm = this.data.etudiantForm;
    this.releveBACFile = this.data.releveBACFile;
    this.releveFiles = this.data.releveFiles;
    this.diplomeFile = this.data.diplomeFile;
    this.dossier.universite = this.data.universite;
    this.compte = this.data.compte;

    this.dossier.releveNotes = [];

    console.log( 'COMPTE+++:*** ', this.data.compte )

    for(let i=0; i < this.etudiantForm.value.releves.length; i++) {
      this.dossier.releveNotes.push('images/' + this.compte.username + '/' + this.releveFiles[i].name );
    }
    for (let i = 0; i < this.etudiantForm.value.releves.length; i++) {
          this.dossierService
              .createReleveReleves(this.releveFiles[i])
              .subscribe(releves=>console.log(''))
    }
    this.dossierService
              .createDiplome( this.diplomeFile )
              .subscribe(releves=>console.log(''))
    this.dossierService
              .createReleveBAC(this.releveBACFile)
              .subscribe(releves=>console.log(''));

    this.dossierService
        .createDossier(this.dossier)
        .subscribe( dossier => {
          console.log('DOSSIER AJOUTER AVEC SUCCES: ', this.dossier);
        } );

    this
        .compteService
        .updateCompte( this.compte._id, {classe: this.data.classe} as Compte )
        .subscribe( data => console.log( data )  )

        console.log('DOSSIER AJOUTER AVEC SUCCES: ', this.dossier);
        location.reload();
        this.closeModal();
  }
 
}
