import { Component, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router, RouterEvent } from '@angular/router';
import { Classe } from '../../shared/classe';
import { ClasseService } from '../../services/classe.service';
import { Compte } from '../../shared/compte';
import { Parcours } from '../../shared/parcours';
import { ParcoursService } from '../../services/parcours.service';

@Component({
  selector: 'app-ajouter-classe',
  templateUrl: './ajouter-classe.component.html',
  styleUrls: ['./ajouter-classe.component.scss']
})
export class AjouterClasseComponent implements OnInit {


  @ViewChild('fform', { static: false }) compteFormDirective: any;
  classeForm: FormGroup;

  classe: Classe;
  parcours: Parcours;
  loading: boolean = false;
  idParcours: string = '';
  compte: Compte;

  niveaux: string[] = ['Licence', 'Mastre', 'Doctorat']


  formErrors: any = {
    nom: '',
    description: '',
    annee: ''
  };

  validationMessages: any = {
    nom: {
      required: 'le nom  est requis.',
      minlength: 'le nom tre plus que 4 caractres.',
      maxlength: 'le nom  ne doit pas tre plus que 150 caractres.'
    },
    annee: {
      required: 'l\'anne universitaire est requise.',
      minlength: 'l\'anne universitaire tre plus que 4 caractres.',
      maxlength: 'l\'anne universitaire ne doit pas tre plus que 150 caractres.'
    },
    description: {
      required: 'la description  est requis.',
      minlength: 'la description tre plus que 4 caractres.',
      maxlength: 'la description  ne doit pas tre plus que 1000 caractres.'
    }
  };


  constructor(
    private formBuilder: FormBuilder,
    private router: Router,
    private route: ActivatedRoute,
    private parcoursService: ParcoursService,
    private classeService: ClasseService,
  ) {
    this.createForm();
  }

  ngOnInit(): void {
    this.idParcours = this.route.snapshot.params['id'];
    this.parcoursService
      .getParcoursById(this.idParcours)
      .subscribe(parcours => this.parcours = parcours);
  }

  createForm() {
    this.classeForm = this.formBuilder.group({
      nom: ['', [Validators.required, Validators.minLength(4), Validators.maxLength(150)]],
      annee: ['', [Validators.required, Validators.minLength(4), Validators.maxLength(150)]],
      description: ['', [Validators.required, Validators.minLength(4), Validators.maxLength(1000)]]
    });
    this.classeForm
      .valueChanges
      .subscribe(data => this.onValueChanged(data));
    this.onValueChanged();
  }

  onValueChanged(data?: any) {
    if (!this.classeForm) { return; }
    const form = this.classeForm;
    for (const field in this.formErrors) {
      if (this.formErrors.hasOwnProperty(field)) {
        // clear previous error message (if any)
        this.formErrors[field] = '';
        const control = form.get(field);
        if (control && control.dirty && !control.valid) {
          const messages = this.validationMessages[field];
          for (const key in control.errors) {
            if (control.errors.hasOwnProperty(key)) {
              this.formErrors[field] += messages[key] + ' ';
            }
          }
        }
      }
    }
  }

  onSubmit() {
    this.classe = this.classeForm?.value;
    this.classe.parcours = this.parcours;
    this.loading = true;

    this.classeService
      .createClasse(this.classe)
      .subscribe(classe => {
        this.parcours.classes.push( classe );
        this.parcoursService
            .updateParcours( this.idParcours, this.parcours )
            .subscribe( parcours => this.parcours = parcours )
      },
        err => { },
        () => {
          setTimeout(() => { this.loading = false }, 1800);
          this.router.navigateByUrl('/parcours-detail/' + this.idParcours)
        }
      )
  }


  reloadPage() {
    setTimeout(() => {
      window.location.reload();
    }, 100);
  }

}
