import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { baseURL } from '../shared/baseurl';
import { Calendrier } from '../shared/calendrier';

@Injectable({
  providedIn: 'root'
})
export class CalendrierService {

  constructor(private httpClient: HttpClient) { }

  getAllCalendriers(): Observable<Calendrier[]> {
    return this.httpClient.get<Calendrier[]>(baseURL + 'calendriers');
  }
  getCalendrierById(idCalendrier: string): Observable<Calendrier> {
    return this.httpClient.get<Calendrier>(baseURL + 'calendriers/' + idCalendrier);
  }

  createCalendrier(Calendrier: Calendrier): Observable<Calendrier> {
    return this.httpClient.post<Calendrier>(baseURL + 'calendriers', Calendrier);
  }

  updateCalendrier(idCalendrier: string, Calendrier: Calendrier): Observable<Calendrier> {
    return this.httpClient.put<Calendrier>(baseURL + 'calendriers/' + idCalendrier, Calendrier)
  }
  getCalendrierByParcoursId(idParcours: string): Observable<Calendrier> {
    return this.httpClient.get<Calendrier>(baseURL + 'calendriers/parcours/' + idParcours);
  }

  deleteCalendrier(idCalendrier: string): Observable<Calendrier> {
    return this.httpClient.delete<Calendrier>(baseURL + 'calendriers/' + idCalendrier);
  }

  createFichier(file: File): Observable<File> {
    let formData = new FormData();
    formData.append('fichier', file);
    console.log(formData);
    return this.httpClient.post<File>(baseURL + "calendriers/fichiers", formData);
  }
}
